import { _decorator, Component, Node } from 'cc';
import { Enum } from 'cc';
import { CCString } from 'cc';
import { js } from 'cc';
import { IAPIServices, IAPIServicesAdaptor, IServiceConfig } from './interfaces/IAPIServices';
import { RequestSchema } from '../network/HttpRequestComponent';
import { error } from 'cc';
import { DEV } from 'cc/env';
import { remoteCCClass } from '../../gamify/utils/RemoteScriptImporter';
import { CCObject } from 'cc';
import { EventHandler } from 'cc';
import { resolve } from 'path';
import { warn } from 'cc';
import { log } from 'cc';

// import { excuteAsRemoteClass } from '../../gamify/utils/RemoteScriptImporter';
const { ccclass, property, executeInEditMode } = _decorator;

enum RequestMethod {
    GET,
    POST,
    PUT, 
    OPTIONS,    
    DELETE
}

Enum(RequestMethod);


enum RequestParamsType {
    KEY_VALUE,
    FUNCTIONAL
}




@remoteCCClass('RequestParams')
// @executeInEditMode(true)
class RequestParams extends CCObject {
    @property({
        type:Enum(RequestParamsType)        
    })
    type:RequestParamsType = RequestParamsType.KEY_VALUE;

    @property
    key:string = '';

    @property({
        visible(){
            return this.type == RequestParamsType.KEY_VALUE
        }
    })
    value:string = '';

    @property({
        type:EventHandler,
        visible(){
            return this.type == RequestParamsType.FUNCTIONAL
        },
        displayName:'Value'
    })
    funcionalValue:EventHandler = new EventHandler()
    
}

// js.unregisterClass(RequestParams);

@remoteCCClass('ServiceRequestConfig')
// @executeInEditMode(true)
export class ServiceRequestConfig extends Component implements IServiceConfig {
            
    @property({type:CCString})
    public get serviceName(): string {
        return this._serviceName;
    }
    public set serviceName(value: string) {
        this._serviceName = value;
    }

    @property({serializable:true})
    private _serviceName: string = null;


    @property({
        type:CCString
    })
    public get url(): string|Function {
        return this._url;
    }
    public set url(value: string|Function) {
        this._url = value;
    }
    
    @property({serializable:true, visible:false})
    private _url:string|Function = '/client/lobby-url';

    @property({
        type:RequestMethod,
        displayName:'Method'
    })
    methodId:RequestMethod = RequestMethod.GET;

    @property({
        type:[CCString]
    })
    get headers():string[] { return ["\'Content-type\':\'application\/json\'"]}   // Do not need more other headers.

    @property({
        type:[RequestParams],
        tooltip:'Copy data from the path (or Value Type) inside \'Value\' field to \n fill the params[Key] field which use to request to url.\n Using the {path.to..} block to identify the path.'
    })
    params:RequestParams[] = [];
    
    @property({
        type:[RequestParams],
        visible(){
            return this.methodId == RequestMethod.POST
        },
        tooltip:'Copy data from the path (or Value Type) inside \'Value\' field to \n fill the params[Key] field which use to request to url.\n Using the {path.to..} block to identify the path.'
    })
    payloads:RequestParams[] = [];

    @property({
        type:[RequestParams],
        tooltip:'Copy data from the path (or Value Type, or Function(state)=>value ) \n inside \'Value\' field to fill the path inside \'Key\' field.\n Using the {path.to..} block to identify the path.'
    })
    responseScripts:RequestParams[] = [];

    protected _adaptor:IAPIServices|IAPIServicesAdaptor;
    get adaptor():IAPIServices|IAPIServicesAdaptor{
        return this._adaptor;
    }

    private _waitForReadyCalback:Function[] = [];

    setAdaptor(adaptor:IAPIServices|IAPIServicesAdaptor){
        this._adaptor = adaptor;
        if(this._waitForReadyCalback && this._waitForReadyCalback.length && adaptor.isReady()){            
            while(this._waitForReadyCalback.length){
                const resolveFunc:Function = this._waitForReadyCalback.shift();
                resolveFunc();
            }
            this._waitForReadyCalback = null;
        }
    }

    
    /**
     * 
     */
    onLoad() {
        if(!this._adaptor){
            //  Run as the independance component.
            if(!this.serviceName) this.serviceName = js.getClassName(this);
            const apiServiceClass:any = js.getClassByName('APIServices');
            let networkOrAdaptor:IAPIServices|IAPIServicesAdaptor = this.node?.getComponent(apiServiceClass) as unknown as IAPIServices|IAPIServicesAdaptor
            if(networkOrAdaptor){
                const serviceConfigs:ServiceRequestConfig[] = this.node.getComponents(ServiceRequestConfig)?.filter((serviceConfig:ServiceRequestConfig)=> serviceConfig.serviceName == this.serviceName);
                if(serviceConfigs){
                    const lastIndex:number = serviceConfigs.length - 1;
                    if(serviceConfigs[lastIndex] && serviceConfigs[serviceConfigs.length - 1] == this){                
                        serviceConfigs.forEach((serviceConfig:ServiceRequestConfig)=>this._adaptor.registerServiceRequest(serviceConfig.serviceName, serviceConfig.getRequestConfig()));
                    }
                }
            }else{
                // error('Error when find APIService')
                const APIServiceAdaptor:any = js.getClassByName('APIServiceAdaptor');
                networkOrAdaptor = (this.node.getComponent(APIServiceAdaptor) || this.node.addComponent(APIServiceAdaptor)) as unknown as IAPIServices|IAPIServicesAdaptor;
            }            
            if(!!networkOrAdaptor) {
                this.setAdaptor(networkOrAdaptor);
            }
        }
    }


    // -----------------------------------
    /**
     * Ultil to 
     */
    protected async waitForReady(){
        if(!this._adaptor || (this._adaptor && !this._adaptor.isReady())){
            await new Promise((resolve:Function)=>{
                this._waitForReadyCalback.push(resolve);
            })
        }else{
            warn('SERVICE SETUP DONE !!!!!!!!!!!!!!')
        }
    }

    // -----------------------------------

    /**
     * 
     * @param triggerParams RequestSchema[] | RequestSchema     Tham s sa i iu kin request.
     * - Nu l mng RequestSchema object, th t cc RequestSchema trong mng tng ng vi th t ca tng request trong mt service.
     * - Nu ch l RequestSchema object, RequestSchema object s c ap dng cho ton b request trong service .
     * @returns 
     */
    async request(triggerParams?:any[]):Promise<any>{
        //         
        await this.waitForReady();
        // 
        if(Object.prototype.hasOwnProperty.call(this._adaptor.constructor.prototype,'request')){
            const networkData:any = await (this._adaptor as IAPIServicesAdaptor).request(this.serviceName, triggerParams);
            log('Completed request !!!')
            return networkData;
        }else{
            // Case: the connected adaptor is a network component.
            log('Dispath truc tiep vao networkservice !')
            this._adaptor.dispatch({type:this._adaptor.getRequestAction(this.serviceName), payload:triggerParams});
        }
    }

    /**
     * 
     */
    get ResponseAction():string{
        if(this._adaptor){
            return this._adaptor.getResponseAction(this.serviceName);
        }
        return this.serviceName 
    }
    

    /**
     * 
     * @returns 
     */
    getRequestConfig():RequestSchema{
        const request:RequestSchema = Object.create(null)
        // combinde string params
        const combinedParams:any = js.createMap();
        // combine params
        this.params.forEach((param:RequestParams)=>{      
            if(param.type == RequestParamsType.KEY_VALUE){
                combinedParams[param.key] = param.value;
            }else if(param.type == RequestParamsType.FUNCTIONAL){
                const eventHandler:EventHandler = param.funcionalValue as EventHandler;
                if(eventHandler){
                    const compName:string = eventHandler.component || eventHandler._componentName;
                    const targetCaller:Component = eventHandler.target?.getComponent(compName);
                    if(targetCaller && targetCaller[eventHandler.handler]){                    
                        combinedParams[param.key] = (targetCaller[eventHandler.handler] as Function).bind(targetCaller);
                    }          
                }      
            }
        })
        // combine payload
        const combinedPayloads:any = js.createMap();
        if(this.methodId == RequestMethod.POST){            
            this.payloads.forEach((payload:RequestParams)=>{      
                if(payload.type == RequestParamsType.KEY_VALUE){
                    combinedPayloads[payload.key] = payload.value;
                }else if(payload.type == RequestParamsType.FUNCTIONAL){
                    const eventHandler:EventHandler = payload.funcionalValue as EventHandler;
                    if(eventHandler){
                        const compName:string = eventHandler.component || eventHandler._componentName;
                        const targetCaller:Component = eventHandler.target?.getComponent(compName);
                        if(targetCaller && targetCaller[eventHandler.handler]){                    
                            combinedPayloads[payload.key] = (targetCaller[eventHandler.handler] as Function).bind(targetCaller);
                        }          
                    }      
                }
            })
        }

        // combine response script
        const responseScriptParams:any = js.createMap();
        this.responseScripts.forEach((param:RequestParams)=>{ 
            if(param.type == RequestParamsType.KEY_VALUE){
                responseScriptParams[param.key] = param.value;
            }else if(param.type == RequestParamsType.FUNCTIONAL){
                const eventHandler:EventHandler = param.funcionalValue as EventHandler;
                if(eventHandler){
                    const targetCaller:Component = eventHandler.target?.getComponent(eventHandler.component);
                    if(targetCaller && targetCaller[eventHandler.handler]){                    
                        combinedParams[param.key] = (targetCaller[eventHandler.handler] as Function).bind(targetCaller);
                    }          
                } 
            }
        })
        //         
        request.url = this.url;
        request.method = RequestMethod[this.methodId] as 'GET'|'PUT'| 'OPTIONS' | 'POST' | 'DELETE';
        if(this.params.length) {request.params = combinedParams;}
        if(this.methodId == RequestMethod.POST){ request.payload =  combinedPayloads;}
        request.response_script = responseScriptParams;
        // 
        return request;
    }

    

}


