const DEFAULT_ELEMENT_NAME:string = 'element';
const WordBreakingRegex:RegExp = /([\w ]+)\s([\w ]+)/g;        //          /((\b[^\s]+\b)((?<=\.\w).)?)/g;

// /\([^)]*\)|\[[^\]]*\]/g;
const Separate:RegExp = /\r?\n|\r|\n/g
const FindBracketRegex:RegExp = /\<(.*?)\>/g;       // < > Bracket
const InsideBracketRegex:RegExp = /(?<=\<).+?(?=\>)/g;

const RemoveBracketRegex:RegExp = /[<>]/g;

export const LangEnum:any = GameUtils.getEnumFromArray(SupportedLanguages, 'default')

enum LocalizeType {
    NORMAL_TEXT,
    RICH_TEXT,
    IMAGE_TEXT
}
Enum(LocalizeType)


