
import { _decorator, js, error, log, Component, __private, Constructor } from 'cc';
import { DEBUG } from 'cc/env';
import { IGamifyDispatcher } from '../interfaces/IDispatcher';
import { GAMIFY_LOGS, GAMIFY_WARNNING } from '../utils/Setting';
import { Action } from './Action';
import { Dispatcher } from './Dispatcher';
import { warn } from 'cc';
import { Eventify } from 'cc';

const { ccclass } = _decorator;

/**
 * 
 * 
 * Author: Hallopatidu@gmail.com
 */
@ccclass('Store')
export class Store extends Component{
    protected _instanceIndex:number = 0;
    protected __ccClassName:string = '';
    protected _dispatchToken:string = null;
    protected _dispatcher:Dispatcher = null;
    
    private _changed:boolean = false;
    
    
    constructor() {
        super();
        this.__ccClassName = js.getClassName(this);
        let this_class:any = js.getClassByName(this.__ccClassName);
        this._instanceIndex = this_class.__totalInstances ? this_class.__totalInstances + 1 : ++this._instanceIndex;
        this_class.__totalInstances = this._instanceIndex; 
        // DEBUG && GAMIFY_LOGS && log('>>>>>>>> Class ' + this.__ccClassName + '   to ' + _class.__totalInstances + ' instances') ;
    }

    /**
     * 
     */
    onDestroy(){
        this.removeDispatcher();
        this._dispatchToken = null;
    }


    /**
     * 
     * @param {*} newState 
     */
    onChanged(){
        // override 
        // DEBUG && GAMIFY_LOGS && log('Cp nht store thnh cng :: '+ this.__ccClassName)
    }

    

    //----------------------------
    // Feature Function
    //----------------------------

    /**
     * 
     */
    public getDispatcher():Dispatcher{
        if(!this._dispatcher ){
            this._dispatcher = Dispatcher.instance;
        }
        return this._dispatcher;
    }

    /**
     * 
     * @param dispatcher 
     * @param isForce 
     */
    public setDispatcher(dispatcher:Dispatcher, isForce:boolean = false){
        // 
        if(dispatcher){
            !(dispatcher instanceof Dispatcher) ? DEBUG ? error(this.__ccClassName + ".setDispatcher(dispatcher): Dispatcher truyn vo phi khc v l subclass ca Dispatcher") : error(false) : undefined;
            if(!this._dispatchToken){                
                this._dispatcher = dispatcher;                
                this._dispatchToken = this._dispatcher.register(this.__generateCallBack.call(this));
            }else{
                if(isForce){
                    // G dispatcher c.                    
                    this.removeDispatcher();
                    // Gn li dispatcher
                    this.setDispatcher(dispatcher);
                }
            } 
            // dispatcher.test();                   
        }else{
            (!dispatcher && this._dispatcher) ? (DEBUG && GAMIFY_LOGS) ? log(this.__ccClassName + ".setDispatcher(dispatcher): S dng Dispatcher mc nh.") : error(false) : undefined;
        }        
    }

    /**
     * 
     */
    public removeDispatcher(){
        if(this._dispatcher && this._dispatchToken){
            this._dispatcher.unregister(this._dispatchToken);
        }
        this._dispatchToken = null;
    }


    /**
     * This exposes a unique string to identify each store's registered callback.
     * This is used with the dispatcher's waitFor method to declaratively depend
     * on other stores updating themselves first.
     */
    public getDispatchToken(){
        return this._dispatchToken;
    }


    /**
     * Returns whether the store has changed during the most recent dispatch.
     */
    public hasChanged(){
        !this.getDispatcher().isDispatching() && GAMIFY_WARNNING ? DEBUG ? warn( this.__ccClassName + ".hasChanged(): This function is only called during dispatching.") : error(false) : undefined;
        return this._changed;
    }

    /**
     * 
     * @param token 
     */
    public waitFor(token:string){
        this.getDispatcher().waitFor([token])
    }

    // ------------------------------------------------------------------------
    // Internal Function
    //  _  : Tin t dnh cho cc function gi trc tip trong class.
    //  __ : Tin t dnh cho cc function dng gi bt thng hoc handler.
    // ------------------------------------------------------------------------

    /**
     * 
     * @returns 
     */
    protected __generateCallBack():Function{
        return (action:Action)=>{
            this.__invokeOnDispatch(action);
        }
    }
        
    /**
     * 
     */
    protected __emitChange() {        
        !this.getDispatcher().isDispatching() && GAMIFY_WARNNING ? DEBUG ? warn( this.__ccClassName + ".__emitChange(): This function is only called during dispatching.") : error(false) : undefined;
        this._changed = true;
        this.onChanged();
    }
        
     /**
     * This method encapsulates all logic for invoking __onDispatch. It should
     * be used for things like catching changes and emitting them after the
     * subclass has handled a action.
     */
    protected __invokeOnDispatch(action:Action) {
        this._changed = false;
        this.__onDispatch(action);
    }


    /**
     * The callback that will be registered with the dispatcher during
     * instantiation. Subclasses must override this method. This callback is the
     * only way the store receives new data.
     */
    protected __onDispatch(action:Action) {        
        !false ? DEBUG ? error( this.__ccClassName + ' c v  qun overridden hm __onDispatch(), vic lm ny l bt buc') : error(false) : undefined;
        this.__emitChange();
    }

}

