import { NodeEventType } from 'cc';
import { UIOpacity } from 'cc';
import { tween } from 'cc';
import { UITransform } from 'cc';
import { Vec3 } from 'cc';
import { director } from 'cc';
import { Canvas } from 'cc';
import { EventHandler } from 'cc';
import { misc } from 'cc';
import { clamp } from 'cc';
import { view } from 'cc';
import { Vec2 } from 'cc';
import { EventTouch } from 'cc';
import { sys } from 'cc';
import { Tween } from 'cc';
import { Size } from 'cc';
import { Component } from 'cc';
import { _decorator} from 'cc';
import { log } from 'cc';
import { Input } from 'cc';
import { GameUtils } from '../../utils/GameUtils';

const { ccclass, property, requireComponent, executeInEditMode } = _decorator;

@ccclass('FloatButton')
@executeInEditMode(true)
@requireComponent(UITransform)
@requireComponent(UIOpacity)

/**
 * Learn more from https://discuss.cocos2d-x.org/t/understanding-the-principles-of-the-coordinate-system/54108
 * 
 */
export class FloatButton extends Component {
    
    @property({
        type:[EventHandler]
    })
    clickEvents: EventHandler[] = [];
    
    private saveKey:string;

    private touchOffset: Vec3;
    private minVec:Vec3 = new Vec3();
    private maxVec:Vec3 = new Vec3();

    private uiOpacity:UIOpacity
    private dimOpacity = 90;
    private dimDuration = 3;
    private dragable = true;
     

    onLoad(){
        this.saveKey = 'G'+GameUtils.getHashFromString(this.node.getPathInHierarchy() + '::FloatButton')
        this.uiOpacity = this.node.getComponent(UIOpacity) || this.node.addComponent(UIOpacity);
        
        //
        Input.EventType.TOUCH_START
        this.node.on(NodeEventType.MOUSE_ENTER, this.onMouseEnter, this);
        this.node.on(NodeEventType.MOUSE_LEAVE, this.onMouseLeave, this);
        // 
        this.node.on(NodeEventType.TOUCH_START, this.onTouchStart, this);        
        this.node.on(NodeEventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(NodeEventType.TOUCH_END, this.onTouchEnd, this);
        this.node.on(NodeEventType.TOUCH_CANCEL, this.onTouchEnd, this);
        this.onMouseLeave();        
        // 
        const uiTransform:UITransform = this.node?.getComponent(UITransform);
        const parentPos:Vec3 = this.node?.parent?.getWorldPosition();
        const nodeSize:Size = uiTransform.contentSize;
        const designSize:Size = view.getDesignResolutionSize();
        const minX = -designSize.width / 2 + nodeSize.width / 2;
        const maxX = designSize.width / 2 - nodeSize.width / 2;
        const minY = -designSize.height / 2 + nodeSize.height / 2;
        const maxY = designSize.height / 2 - nodeSize.height / 2;
        //  
        Vec2.set(this.minVec, parentPos.x + minX, parentPos.y + minY);
        Vec2.set(this.maxVec, parentPos.x + maxX, parentPos.y + maxY);
        //         
    }

    start(){
        const savePosStr:string = GameUtils.getLocalStorageItem(this.saveKey);
        if(savePosStr){
            const saveData:{x:number, y:number, z:number} = JSON.parse(savePosStr);
            const savePos:Vec3 = new Vec3(saveData.x, saveData.y, saveData.z);
            this.node.setWorldPosition(savePos);
        }
    }

    onMouseEnter(){        
        if(this.uiOpacity.opacity == this.dimOpacity){
            this.dimIn();
        }else{
            this.unscheduleAllCallbacks();
        }
    }

    onMouseLeave(){
        this.scheduleOnce(this.dimOut, this.dimDuration);
    }

    onTouchStart(event:EventTouch) {                
        if(event.target){
            if(sys.isMobile) this.onMouseEnter();
            const startTouchPoint:Vec2 = event.getUILocation();
            const startTouchWorldPos:Vec3 = new Vec3(startTouchPoint.x, startTouchPoint.y, 0);
            const currentWorldPos:Vec3 = this.node.getWorldPosition();
            this.touchOffset = startTouchWorldPos.subtract(currentWorldPos);
        }
    }

    onTouchMove(event:EventTouch) {
        if(!this.dragable) return;
        if (this.touchOffset) {
            const touchPoint:Vec2 = event.getUILocation();
            const touchWorldPos:Vec3 = new Vec3(touchPoint.x, touchPoint.y, 0);
            const currentWorldPos:Vec3 = touchWorldPos.subtract(this.touchOffset);
            currentWorldPos.clampf(this.minVec, this.maxVec);
            this.node.setWorldPosition(currentWorldPos);
        }
    }

    onTouchEnd(event:EventTouch) {
        if(!this.dragable) return;
        if (sys.platform === sys.Platform.MOBILE_BROWSER) this.onMouseLeave();
        const touchPoint:Vec2 = event.getUILocation();
        const startTouchPoint:Vec2 = event.getUIStartLocation();
        const deltaVec2:Vec2 = touchPoint.subtract(startTouchPoint);
        if(deltaVec2.length() < 5){
            this.onPress(event);
        }else {
            GameUtils.setLocalStorageItem( this.saveKey, JSON.stringify(this.node.getWorldPosition()))
        }
    }

    onPress(event:EventTouch){     
        EventHandler.emitEvents(this.clickEvents);
    }

    // 
    protected dimIn()
    {
        Tween.stopAllByTarget(this);
        tween(this.uiOpacity)
            .to(0.3, { opacity: 255 })
            .start();
    }

    protected dimOut()
    {
        Tween.stopAllByTarget(this);
        tween(this.uiOpacity)
            .to(0.3, { opacity: this.dimOpacity })
            .start();
    }
    // 
}


