
import { Component, error, log, warn, _decorator } from 'cc';
import { DEBUG } from 'cc/env';
import { Action } from './Action';
import { Store } from './Store';

const { ccclass } = _decorator;

/**
 * 
 * 
 * Author: Hallopatidu@gmail.com
 */
@ccclass('Reducer')
export class Reducer extends Store {
    protected _state: any;

    //------------------------------------------
    onLoad(){
        this._state = this.initialState;
    }
    
    /**
     * 
     */
    onDestroy(){
        super.onDestroy();
        this._state = null;
    }
    

    /**
     * 
     */
    onRejected(){
        // override 
        // DEBUG && GAMIFY_LOGS && log('Cp nht store tht bi :: '+ this.__ccClassName)
    }

    //------------------------------------------
    /**
     * Getter that exposes the entire state of this store. If your state is not
     * immutable you should override this and do not expose _state directly.
     */
    public getState():any {
        return Object.assign({}, this._state) ; // Working for a Imutable State
    }

    /**
     * Constructs the initial state for this store. This is called once during
     * construction of the store.
     */
    protected get initialState():any {        
        try{             
            throw new Error( this.__ccClassName + ".initialState :: Cn c th tc khi to State ban u.")
        }catch(err:any){
            error(error);
        }
        return null;
    }


    /**
     * Used to reduce a stream of actions coming from the dispatcher into a
     * single state object.
     */
    public reduce(state:any, action:Action):any {
        // return abstractMethod('ReduceStore', 'reduce');                       
        throw new Error( this.__ccClassName + ".reduce :: Cc subclass k tha Reducer yu cu Override hm ny  c th tc reduce cc trng thi ca Store.");
        return state;
    }


    /**
     * Checks if two versions of state are the same. You do not need to override
     * this if your state is immutable.
     */
    protected areEqual(one:any, two:any) {        
        return one === two; 
    }

    /**
     * 
     * @param {*} action 
     */
    // protected __prepareInvokeOnDispatch(action:Action){
    //     // 
    // }

    /**
     * 
     * @param action 
     */
    // protected __endingInvokeOnDispatch(action:Action){
    //     // 
    // }


    /**
     * This method override super.__invokeOnDispatch and encapsulates all logic for invoking __onDispatch. It should
     * be used for things like catching changes and emitting them after the
     * subclass has handled a action.
     */
    // protected __invokeOnDispatch(action:Action) {
    //     this.__prepareInvokeOnDispatch(action);
    //     super.__invokeOnDispatch(action);
    //     this.__endingInvokeOnDispatch(action);
    // }


    /**
     * The callback that will be registered with the dispatcher during
     * instantiation. Subclasses must override this method. This callback is the
     * only way the store receives new data.
     */
    protected __onDispatch(action:Action) {        
        // Reduce the stream of incoming action to state, update when necessary.
        // let startingState = this._state;
        let endingState = this.reduce(this.getState(), action);
        
        // This means your ending state should never be undefined.            
        // !(endingState !== undefined) ? DEBUG ? error(this.__ccClassName + '.reduce(...) tr v gi tr cha xc nh, d l  qun tr v gi tr state trong trng hp mc nh ? (nu khng thch tr v th nn  l return null)') : error(false) : undefined;
        !(endingState !== undefined) ? DEBUG ? error(this.__ccClassName + '.reduce(...) return undefined value, May be, you have forgotten returning the state attribute ? (could be returning the null value, if you want)') : error(false) : undefined;
        this.setState(endingState);
        // if (!this.areEqual(startingState, endingState)) {
        //     this._state = endingState;

        //     // `__emitChange()` sets `this.__changed` to true and then the actual
        //     // change will be fired from the emitter at the end of the dispatch, this
        //     // is required in order to support methods like `hasChanged()`
        //     this.__emitChange();
        // }else{
        //     this.__rejectChange();
        // }
    }

    /**
     * Check and set new state.
     * @param newState 
     */
    protected setState(newState:any, isForced:boolean = false):boolean{
        let startingState = this._state;
        if ( isForced || !this.areEqual(startingState, newState) ) {
            this._state = newState;
            // `__emitChange()` sets `this.__changed` to true and then the actual
            // change will be fired from the emitter at the end of the dispatch, this
            // is required in order to support methods like `hasChanged()`
            this.__emitChange();
            return true
        }else{
            this.__rejectChange();
            return false
        }
    }

    /**
     * 
     */
    protected __rejectChange(){
        this.onRejected();
    }    

}

