import { _decorator, Component, Node } from 'cc';
import { WebSocketComponent } from '../../../../Scripts/ui/network/WebSocketComponent';
import { Action } from '../../../../Scripts/gamify/core/Action';
import { ServiceRequestConfig } from '../../../../Scripts/ui/api/ServiceRequestConfig';
import { MainGameAction } from '../../../../Scripts/ui/actions/StartActions';
import { error } from 'cc';
import { log } from 'cc';
import { Helper } from '../../../../Scripts/gamify/utils/Helper';
import { js } from 'cc';
import { InboxInfo, PlayerInboxAction } from '../types/PlayerInboxType';
import { EventHandler } from 'cc';
const { ccclass, property } = _decorator;



type PlayerInboxState = {
    status:number,
    data?:InboxInfo[]
    // error?:
}



enum PlayerInboxNetworkState {
    INIT,
    READY
}

@ccclass('PlayerInboxNetwork')
export class PlayerInboxNetwork extends WebSocketComponent {

    private servieNetworkName:string = null;

    /**
     * 
     * @param state 
     * @param action 
     */
    onData(state:any, action:Action){
        const actionType:string = action.type;
        const payload:any = action.payload;
        switch(actionType){
            case MainGameAction.INIT: 
                if(payload && payload.apiService) {
                    this.servieNetworkName = payload.apiService;
                }
                this.initNetworkState(state);                
                break
        }
    }

    /**
     * 
     * @param done 
     * @param capturedState 
     * @param sharedData 
     */
    async onRender(done: Function, capturedState?: any, sharedData?: any): Promise<void> {
        switch(capturedState.status){
            case PlayerInboxNetworkState.INIT:
                await this.connect(this._url);
                break;
        }
        done && done();
    }


    // // ---------------

    /**
     * 
     * @returns 
     */
    private async initNetworkState(state:PlayerInboxState){
        if(!this.servieNetworkName) return;
        const networkState:any = this.getAPIData(this.servieNetworkName);    
        this._url = networkState.config.wssURL;
        state.status = PlayerInboxNetworkState.INIT;
        this.render();
    }

    // ------------- REQUEST PARAMS --------------

    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns the string which passed to {paramKey} of request.
     */
    getStartDateParam(state:any, paramKey:string):string{
        const date:Date = new Date();
        return new Date(date.getFullYear(), date.getMonth(), date.getDate()).toISOString();        
    }
    
    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns the string which passed to {paramKey} of request.
     */
    getEndDateParam(state:any, paramKey:string):string{
        return new Date().toISOString();
    }

    // ---------------

   /**
    * 
    * @param serviceName 
    * @returns 
    */
    protected getAPIData(serviceName:string):any{
        if(serviceName){
            const serviceData:any = this['getOtherState'](serviceName) as any;
            if(!serviceData) {
                error('[Lobby Popup]: Khng c hoc setup nhm service');
                return null;
            }
            return serviceData;
        }else {            
            error('[Lobby Popup] Cha khai bo service name ! C th do StartGameAction.INIT cha c dispatch.')
        }
        return null;
    }
    // ---------------

    /**
     * 
     * @param client 
     * @param event 
     */
    onMessage(client:WebSocket, event:MessageEvent){
        super.onMessage(client, event)
        this.dispatch({type:PlayerInboxAction.UPDATE_INBOX})
        // if(event.data){
            
        // }
    }

    
    /**
     * 
     * @param client 
     * @param event 
     */
    onError(client:WebSocket, event:Event){
        super.onError(client, event)
    }
}


