type Constructor<T> = new (...args: any[]) => T;

export interface IAsyncWaitified {
    waitForReady():Promise<any>
    ready(data?:any):void
}

export function AsyncWaitify<TBase> (base: Constructor<TBase>): Constructor<TBase & IAsyncWaitified> {
    class AsyncWaitified extends (base as unknown as any) implements IAsyncWaitified {
        private _waitingHandler: Function[] = [];
        async waitForReady(){
            if(this._waitingHandler){
                await new Promise((resolve:Function)=>{
                    this._waitingHandler.push(resolve)
                })
            }
        }

        ready(data?:any){
            if(this._waitingHandler && this._waitingHandler.length){
                while(this._waitingHandler.length){
                    const resolveFunc:Function = this._waitingHandler.shift();
                    resolveFunc(data);
                }                
            }
            this._waitingHandler = null
        }
    }
    
    // Mixin with `CallbacksInvokers`'s prototype
    // const callbacksInvokerPrototype = CallbacksInvoker.prototype;
    // const propertyKeys: (string | symbol)[] =        (Object.getOwnPropertyNames(callbacksInvokerPrototype) as (string | symbol)[]).concat(
    //     Object.getOwnPropertySymbols(callbacksInvokerPrototype),
    // );
    // for (let iPropertyKey = 0; iPropertyKey < propertyKeys.length; ++iPropertyKey) {
    //     const propertyKey = propertyKeys[iPropertyKey];
    //     if (!(propertyKey in AsyncWaitified.prototype)) {
    //         const propertyDescriptor = Object.getOwnPropertyDescriptor(callbacksInvokerPrototype, propertyKey);
    //         if (propertyDescriptor) {
    //             Object.defineProperty(AsyncWaitified.prototype, propertyKey, propertyDescriptor);
    //         }
    //     }
    // }

    return AsyncWaitified as unknown as Constructor<TBase & IAsyncWaitified>;
}
