
import { _decorator, js, log, warn } from 'cc';
import { Action } from './core/Action';
import { Dispatcher } from './core/Dispatcher';
import { RenderOrderDispatcher } from './dispatchers/RenderOrderDispatcher';
import { GamifyStore } from './GamifyStore';
import { IGamifyComponent } from './interfaces/IComponent';
import { Helper } from './utils/Helper';
import { GAMIFY_WARNNING } from './utils/Setting';
import { DEBUG, DEV } from 'cc/env';
import { error } from 'cc';
const {property, ccclass} = _decorator;

/**
 * - Tnh nng ng k render vi Captured State - Render vi trng thi a ngay ti v tr gi render();
 * 
 * Author: Hallopatidu@gmail.com
 */ 
@ccclass('GamifyComponent')
export class GamifyComponent extends GamifyStore implements IGamifyComponent {
    
    private _capturedState:any = null;

    protected _capturedAction:Action = null;

    private _registerCapturedState:any = null;

    private _requireRenders:Function[] = [];
    
    protected runInBackground:boolean = false;

    public static isSubClass(className:string):boolean{
        return js.isChildClassOf(js.getClassByName(className), GamifyComponent)
    }
        
    /**
     * 
     */
    onLoad(){    
        if(this.runInBackground || this.enabled ){            
            super.onLoad()
        }
    }
    
    /**
     * 
     * @param done 
     * @param capturedState 
     * @param capturedAction 
     * @param sharedData 
     */
    async onPreRender(done:Function, capturedState:any, sharedData:any, capturedAction:Action){
        this._capturedAction = capturedAction;
        this._capturedState = capturedState;
        // 
        await this.onRender((sharedData?:any)=>{
            done(sharedData);
            this._capturedState = null;
        }, capturedState, sharedData);
        // 
    }
    
    /**
     * 
     * @param done 
     * @param capturedState state data of GamifyComponent at the time of calling this.render(). (state data ca GamifyComponent ti thi im gi this.render(). )
     * @param sharedData    Data is shared between rendering tasks in the same rendering flow. (Data chia se giua cac render trong cng mt lung render.)
     */
    async onRender(done:Function, capturedState?:any, sharedData?:any){
        done();
    }

    /**
     * Hm c h thng gi vo khi kt thc qu trnh render ca Component.
     */
    onCompletedRender(actionType:string){
        
    }

    /**
     * The function is called by the system at the end of the rendering process. (Hm c h thng gi vo khi kt thc qu trnh render.)
     * Note (Lu ):
     * - Only the first Gamify Component rendered in the rendering flow can call this function. (Ch c Gamify Component c hin th u tin trong lung hin th mi c gi hm ny.)
     * @param capturedAction 
     * @param sharedData 
     */
    onCompletedAllRenders(sharedData?:any, capturedAction?:Action){
        
    }
    
    /**
     * Wait for another component to handle rendering first. (i mt component khc x l render trc.)
     * @param stateName 
     * @returns 
     */
    public wait(stateName:string):IGamifyComponent{
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof RenderOrderDispatcher){
            (dispatcher as RenderOrderDispatcher).renderBefore([stateName], this.getStateName());
        }
        return this;
    }

    /**
     * Create a render task. (To mt task render.)
     * Use state data right at the location of calling the render function even before entering the main state. (S dng state data ngay ti v tr gi hm render k c khi cha nhp vo state chnh.)
     * If you want to use the latest state, call this.getState(); (Nu mun s dng state mi nht, gi hm this.getState();)
     * @param useCapturedState If true, then clone state for each render task. (nu l true th clone state cho tng task render.)
     * @returns 
     */
    public render(useCapturedState:boolean = false):IGamifyComponent{
        let dispatcher:Dispatcher = this.getDispatcher();
        if(!dispatcher.isDispatching()) {
            warn('Do not register render outside the dispatching data flow. Warning from ' + this.getStateName())
            return;
        }
        let capturedState:any = useCapturedState ? Helper.clone(this._registerCapturedState) : Object.assign({}, this._registerCapturedState);
        if(!this._requireRenders){
            this._requireRenders = [];
        }
        this._requireRenders.push(()=>{
            // let dispatcher:Dispatcher = this.getDispatcher();
            if(dispatcher && dispatcher instanceof RenderOrderDispatcher){
                (dispatcher as RenderOrderDispatcher).registerRender(  this.getStateName(), 
                                                                capturedState,
                                                                this.onPreRender.bind(this) , 
                                                                this.onCompletedRender.bind(this), 
                                                                this.hasUsingFuntion('onCompletedAllRenders') ? this.onCompletedAllRenders.bind(this) : null );
            }
        })
        return this;
    }


    /**
     * 
     */
    protected registerRender(){
        if(this._requireRenders){
            while(this._requireRenders.length){
                this._requireRenders.shift().call(this);
            }
        }
    }

    protected removeAllRegisterRender(){
        // !this.getDispatcher().isDispatching() && GAMIFY_WARNNING ? DEBUG ? warn( this.__ccClassName + ".removeAllRegisterRender(): This function is only called during dispatching.") : error(false) : undefined;    
        if(this.getDispatcher()?.isDispatching() && this._requireRenders && this._requireRenders.length){ this._requireRenders.length = 0;}
    }


    protected getCaptureState():any{
        return this._capturedState || this.getState();
    }
            
    /**
     * 
     * @param functionName 
     * @returns 
     */
    protected hasUsingFuntion(functionName:string):boolean{
        return Object.prototype.hasOwnProperty.call(this.constructor.prototype, functionName);        
    }

    
    //------------- Override ----------------


    /**
     * 
     * @param state 
     * @param action 
     * @returns 
     */
    public reduce(state:any, action:Action) {
        this._registerCapturedState = state;
        this._registerCapturedState.action = action.type;
        return super.reduce(state, action);
    }

    /**
     * 
     * @param action 
     */
    protected __endingInvokeOnDispatch(action: Action){
        super.__endingInvokeOnDispatch(action);
        this._registerCapturedState = null
    }

    /**
     * B sung: State phi thay i mi cho php render.
     */
    protected __emitChange() {
        this.registerRender();
        super.__emitChange();
    }

    /**
     * 
     */
    protected __rejectChange(){
        GAMIFY_WARNNING && warn('REJECT CHANGE ' + this.__ccClassName);
        this.removeAllRegisterRender();
        super.__rejectChange();
    }

    
    /**
     * B sung: Lun tr v true. V GamifyComponent gi c c GamifyStore ln GamifyComponent khc.
     * @param storeClassName 
     * @returns 
     */
    protected _checkRule(storeClassName:string):boolean{
        return true;
    }

    
    /**
     * B sung: Vi id cng l GamifyComponent th b vic bo li vng lp.
     * @param id 
     */
    protected __detectedUndefinedState(id:string, action:Action){    
        super.__detectedUndefinedState(id, action)    
        // if(DEV){
        //     (this.getStoreComponent(id) instanceof GamifyComponent) ? super.__detectedUndefinedState(id, action) : warn('Found one issue inside data flow');           
        // }
        // !GamifyComponent.isSubClass(id) ? super.__detectedUndefinedState(id, action) : undefined;
    }
 

}

