    /**
    * 
    * @param assetName 
    * @param classType 
    * @returns 
    */
    static getAsset(assetName:string, classType:any):Asset{
       let key:string = GameUtils.cachedAssets.get(assetName);
       if(!key){
           return assetManager.assets.find((asset:Asset, assetKey:string)=>{
               const condition:boolean = (asset.name == assetName) && (asset instanceof classType);
               if(condition){
                   key = assetKey;
                   GameUtils.cachedAssets.set(assetName, assetKey);
               }
               return condition
           })
       }
       return assetManager.assets.get(key)
   }

