    // --------------------------- RegExp -------------------

    /**
     * 
     * @param brackets 
     * @returns RegExp - Default : /(<[^>]+>|[^<>]+)/
     */
    static generateSplitRegex(...brackets:string[]):RegExp{
        let patternRegexStr:string;
        brackets.forEach((bracket:string)=>{
            if(bracket.length == 2){
                patternRegexStr = patternRegexStr ? patternRegexStr +"|" : patternRegexStr;
                patternRegexStr += "(" + bracket[0] +"[^"+ bracket[1] +"]*" + bracket[1] + "|[^" + bracket[0] + bracket[1] + "]+)";                
            }else{
                warn('This bracket ' + bracket + ' should be only 2 characters.')
            }
        })
        return patternRegexStr ? new RegExp(patternRegexStr, "g") : new RegExp(/(<[^>]+>|[^<>]+)/, "g"); 
    }



