    /**
     * 
     * @param startNumber 
     * @param endNumber 
     * @param onUpdate 
     * @param option 
     * {
     *      fixed:number,
     *      delay:number,
     *      duration:number
     * }
     */
    static countingTween(startNumber:number, endNumber:number, onCounting:Function, option?:{fixed?:number, delay?:number, duration?:number}, onDone?:Function):Tween<any>{
        const fixedNum:number = (option && option.fixed) || 0;
        const delayTime:number = (option && option.delay) || 0;
        const duration:number = (option && option.duration) || 0.8;
        const startNum:number = parseFloat(startNumber.toFixed(fixedNum));
        const endNum:number = parseFloat(endNumber.toFixed(fixedNum));
        let objTween = { value: startNum };
        const counter:Tween<any> = tween(objTween)
        .delay(delayTime)
        .to(duration, { value: endNum }, {
            onUpdate(target?, ratio?) {
                const value:number = target['value'];                
                onCounting && onCounting(parseFloat(value.toFixed(fixedNum)))
            },
            onComplete(target) {
                const value:number = target['value'];  
                // onDone && onDone(parseFloat(value.toFixed(fixedNum)))
            },
            // progress (start, end, current, ratio):number {
            //     const countNum:number = parseFloat(current.toFixed(fixedNum))
            //     onCounting && onCounting(countNum)
            //     log('count: ' + countNum)
            //     return start + (end - start) * ratio;
            // }
        })
        .call(()=>{
            counter.stop();
            onDone && onDone(endNum)
        })
        .start();        
        return counter;
    }

    static async delay(target:Node, time:number = 0.5){
        await new Promise((resolve:Function)=>{
            tween(target).delay(time).call(resolve).start();
        })
    }

    /**
     * 
     * @param target 
     * @param options 
     * @param easing 
     */
    static async tweenPromise(target:Node, options?:{
        duration:number
        position?:Vec3,
        rotation?:Vec3  // Euler
        scale?:Vec3
    }, easing?:{easing?:TweenEasing}){
        // 
        return await new Promise((resolve:Function)=>{
            const tweenOptions:any = Object.create(null);
            tweenOptions.duration = options.duration;
            if(options.position) {tweenOptions.position = options.position}
            if(options.scale) {tweenOptions.scale = options.scale}
            if(options.rotation) {
                let  quat : Quat = new Quat();
                Quat.fromEuler(quat, options.rotation.x, options.rotation.y, options.rotation.z);
                tweenOptions.rotation = quat;
            }            
            //
            tween(target)
            .to(options.duration, 
                tweenOptions,
                easing            // Bind rotation
            )                                   
            .call(()=>{
                resolve();
            })
            .start();                                           // Call the start method to enable tween

        })
    }

    // ------------------------- ENUM ------------------------

    /**
     * 
     * @param obj 
     * @param propName 
     * @param enumDef 
     */
    static setEnumAttr (obj, propName, enumDef) {
        CCClass.Attr.setClassAttr(obj, propName, 'type', 'Enum');
        CCClass.Attr.setClassAttr(obj, propName, 'enumList', Enum.getList(enumDef));
    }

    /**
     * 
     * @param targetObj 
     * @param propName 
     * @param newEnum 
     */
    static updateEnum (targetObj, propName , newEnum) {
        let defaultEnum = Object.assign( Enum({}) , newEnum);
        Enum.update(defaultEnum);
        CCClass.Attr.setClassAttr(targetObj, propName, 'type', 'Enum');
        CCClass.Attr.setClassAttr(targetObj, propName, 'enumList', Enum.getList(defaultEnum));
    }

    /**
     * 
     * @param args 
     * @returns 
     */
    static getEnumFromArray(...args:Array<string|string[]>):any{
        const fattenKeys:string[] = Array.from(args).reduce((accumulator:string[], value:string|string[]) => accumulator.concat(value), []) as string[];
        const enumObj: {[key: string]: number} = {};
        let numIndex:number = 0;
        fattenKeys.forEach((key:string, index:number)=>{
            if(js.isString(key)){                
                enumObj[key] = index;  
                numIndex = index;             
            }
        })        
        return Enum(enumObj)
    }

    // ----------------------------------------------------------

    /**
     * Calculate the right alignment size.
     * In this case:
     * You want to put a node inside other node so that the target size fit to height or fit to width with the pattern node.
     * @param elementSize    
     * @param containerSize 
     * @param isCover 
     * @returns 
     */
    static calculateAligning(elementSize:Size, containerSize:Size, isCover:boolean = false):Size{
        const heightRatio:number = elementSize.height/containerSize.height;
        const widthRatio:number = elementSize.width/containerSize.width;
        return (widthRatio > heightRatio)!==isCover ? new Size(containerSize.width, elementSize.height*(1/widthRatio)) : new Size(elementSize.width*(1/heightRatio), containerSize.height);        
    }

