    static getLocalStorageItem(key:string):string{        
        return sys.localStorage.getItem(key)
    }
    
    static removeLocalStorageItem(key:string){
        sys.localStorage.removeItem(key)
    }
    
    static setLocalStorageItem(key:string, value:string){        
        sys.localStorage.setItem(key, value)
    }

    // ---------------------------- Array --------------------------------

    /**
     * nh x (mapping)
     * @param listA 
     * @param listB 
     * @param greater 
     * @param lesser 
     * @param equality 
     */
    static mappingArrays(listA:any[], listB:any[], greater:Function, lesser:Function, equality:Function ){
        let maxLength:number = Math.max(listA.length, listB.length);
        for (let index = 0; index < maxLength; index++) {
            let a:any = listA[index];
            let b:any = listB[index];
            if( !!a > !!b ){
                greater(a,b, index);
            }else if( !!a < !!b ){
                lesser(a,b, index);
            }else {
                equality(a, b, index);
            }
        }
    }

    
  
  

