
import { _decorator, Component, Node, error, warn, log } from 'cc';
import { DEV, EDITOR } from 'cc/env';
import { Action } from '../core/Action';
import { IActionStoreDispatcher } from '../interfaces/IDispatcher';
import { GAMIFY_WARNNING } from '../utils/Setting';
import { ActionQueueDispatcher } from './ActionQueueDispatcher';
const { ccclass } = _decorator;

/**
 * Predefined variables
 * Name = ActionStoreDispatcher
 * DateTime = Wed Mar 09 2022 23:26:55 GMT+0700 (Indochina Time)
 * Author = hallopatidu
 * FileBasename = ActionStoreDispatcher.ts
 * FileBasenameNoExtension = ActionStoreDispatcher
 * URL = db://assets/scripts/gamify/dispatchers/ActionStoreDispatcher.ts
 * ManualUrl = https://docs.cocos.com/creator/3.3/manual/en/
 * 
 * Guide:
 * S dng cho GamifyAction kt ni vi GamifyStore v GamifyComponent khc.
 * GamifyAction kt ni mt nhiu vi GamifyStore v GamifyComponent khc.
 * GamifyAction lun c thc thi trc cc GamifyStore v GamifyComponent mi khi c kt ni.
 * State ca GamifyAction chnh l action ca cc GamifyStore v GamifyComponent khc.
 * Nu GamifyStore hoc GamifyComponent c GamifyAction kt ni cha c ng k dn ti vic kt ni khng thnh cng, th 
 * mi khi GamifyStore hoc GamifyComponent  c ng k th vic kt ni s c thc hin li.
 */
 
@ccclass('ActionStoreDispatcher')
export class ActionStoreDispatcher extends ActionQueueDispatcher implements IActionStoreDispatcher {

    protected _storeMapper:Map<string, string> = new Map(); // Map< GamifyStore|GamifyComponent Token, GamifyAction Token >
    
    protected _actionStores:Map<string, Function> = new Map(); // Map<GamifyAction token, GamifyAction callback> : Lu danh sch cc GamifyAction  c kt ni.

    protected _orderConnectorList:Map<string, Function> = new Map();

    // ---------- OVERIDE ---------
    
    /**
     * B sung:
     * - Khng gi vo callback ca cc GamifyAction.
     * - Trng hp Store c kt ni GamifyAction cha c ng k, Chy ng k kt ni gia GamifyAction vi cc GamifyStore hoc GamifyComponent
     * - Trng hp Store c kt ni GamifyAction, gi callback ca ActionStore trc  iu khin callback ca Store
     * @param id 
     */
    protected _excuteCallback(id:string){        
        if(!this._actionStores?.has(id)){
            // Thc thi hm callback khi khng phi l GamifyAction.
            if(this._storeMapper && this._storeMapper.has(id)){
                let actionStoreToken:string|Function = this._storeMapper.get(id);
                if(actionStoreToken && !(typeof actionStoreToken == 'string')){
                    // Nu actionStoreToken khng phi l dng string m l Function th
                    // c th lc dispatch store connect vi GamifyAction vn cha c ng k.
                    // Chy ng k vi callback function   ly GamifyAction Token sau khi chy Function Task kt ni.
                    actionStoreToken = (actionStoreToken as Function)() as string;
                }

                if(this._actionStores.has(actionStoreToken as string) ){                
                    // Nu actionStoreToken c gi tr v dng string th  l token ca GamifyAction c kt ni vi.
                    // Gi callback ca GamifyAction  iu khin callback ca store.
                    const success:boolean = this._callbacks[actionStoreToken as string](this._pendingAction, this.findMapName(id) , this._callbacks[id]);
                    if(success) {
                        return;
                    }
                }

            }
            // Nu GamifyStore khng c map vi GamifyAction no th chy nh mt GamifyStore thng.
            // this._callbacks[id](this._pendingAction);
            super._excuteCallback(id);
        }else{
            // Khng gi thc thi hm callback ca cc GamifyAction.
        }
    }

    /**
     * 
     * @param stateName 
     * @param dispatchToken 
     * @returns 
     */
    public mapToken(stateName:string, dispatchToken:string):string{
        const fullStateName:string = super.mapToken(stateName, dispatchToken);
        // if(stateName == 'APIServices'){
        //     log('-------')
        // }
        // const setupLaterFunc:Function = this._orderConnectorList.get(stateName);
        // setupLaterFunc && setupLaterFunc()
        this._orderConnectorList.forEach((connectorCallLater:Function, stateName:string)=>{
            if(connectorCallLater && this.isSameName(stateName, fullStateName)){
                connectorCallLater(true);
            }
        });
        return fullStateName;
    }


    /**
     * 
     * @param action 
     */
    public dispatch(action: Action): void {
        const senderId:string = this._validateIds([action.sender])[0];        
        if(senderId){
            if(this._storeMapper.has(senderId)){
                // let actionStoreToken:string|Function = this._storeMapper.get(senderId);
                // if(actionStoreToken && !(typeof actionStoreToken == 'string')){
                //     // Nu actionStoreToken khng phi l dng string m l Function th
                //     // c th lc dispatch store connect vi GamifyAction vn cha c ng k.
                //     // Chy ng k vi callback function   ly GamifyAction Token sau khi chy Function Task kt ni.
                //     actionStoreToken = (actionStoreToken as Function)() as string;
                // }
                let actionStoreToken:string = this._storeMapper.get(senderId) as string;
                if(this._actionStores.has(actionStoreToken as string) ){
                    const modifyActionDispatch:Function = this._actionStores.get(actionStoreToken as string) ;
                    modifyActionDispatch && modifyActionDispatch(action);
                }
            }
        }
        // 
        super.dispatch(action);
    }

    destroy(){
        this._orderConnectorList.clear();
        this._orderConnectorList = null;
        super.destroy()
    }

    // ---------- FEATURE ---------

    /**
     * Gn GamifyAction cho GamifyStore / GamifyComponent.
     * @param actionStateName 
     * @param stateName 
     */
    public connectStore(actionStateName:string, stateName:string, outputDispatch?:Function ){
        // Tim tat ca cac store co lien quan toi stateName. Truong hop, co nhieu store cung class.
        const fullStateNames:string[] = this.searchAllRegisterNames(stateName);
        log('Cac store ket noi voi '  + actionStateName  + ' la : ' + fullStateNames + ' ( ' + stateName+' ) ');
        fullStateNames.forEach((fullStateNames:string)=>{
            const coupleTokens:string[] = this._validateIds([actionStateName, fullStateNames]);
            const actionStoreToken:string = coupleTokens[0];
            if(actionStoreToken){
                // Ch lu mt ln vi mt action store token.
                if(!this._actionStores.has(actionStoreToken)){
                    this._actionStores.set(actionStoreToken, outputDispatch);
                }
                const storeToken:string = coupleTokens[1];
                if(storeToken && !this._storeMapper.has(storeToken)){
                    this._storeMapper.set(storeToken, actionStoreToken);
                }else{
                    (DEV || EDITOR) && GAMIFY_WARNNING && warn('Store ' + stateName + ' cha c ng k trn Dispatcher ' + this.__ccClassName + ". Hm connectStore s c gi li sau khi Store c ng k");
                }
                // 
            }else{
                // Li khng tn ti action store token.
                // (DEV || EDITOR) && GAMIFY_WARNNING && warn('Khng tm c token ca Action Store ' + actionStateName + ' ti ' + this.__ccClassName) ;
            }
            //
        });
        // 
        if(!this._orderConnectorList.has(stateName)){
            this._orderConnectorList.set(stateName, (isReconnected:boolean):string=>{
                if(isReconnected){
                    this.connectStore(actionStateName, stateName, outputDispatch);
                }
                return actionStateName;
            });
        }
        // 
    }



    /**
     * Xa ton b phn t ca _storeMapper lin quan ti stateName.
     * @param stateName 
     */
    public disconnectStore(stateName:string){
        const storeToken:string = this._validateIds([stateName])[0] //|| stateName;        
        const interator:IterableIterator<[string, string|Function]> = this._storeMapper.entries();
        let coupleTokens:[string, string|Function] = interator.next().value;
        while(coupleTokens && coupleTokens.length){
            if(coupleTokens[0] == storeToken){
                // Nu stateName ang kho st l GamifyStore hoc GamifyComponent c kt ni 
                // th xa phn t tng ng trong this._storeMapper
                this._storeMapper.delete(coupleTokens[0]);
                this._actionStores.has(storeToken) && this._actionStores.delete(storeToken);
            }else{
                // Nu stateName ang kho st l GamifyAction th so snh vi coupleTokens[1]
                let actionStoreToken:string = coupleTokens[1] as string;
                // So snh actionStoreToken vi storeToken l token ca GamifyAction ang kho st.
                if((typeof actionStoreToken == 'string') && actionStoreToken == storeToken){
                    // Xa ton b gi tr trong this._storeMapper v this._actionStores c lin quan.
                    // Ti v tr 0 l coupleTokens[0]
                    this._storeMapper.delete(coupleTokens[0]);
                    // Xoa cac hamm cai dat tham chieu toi store.
                    this._orderConnectorList.forEach((getActionStoreName:Function, key:string)=>{
                        const actionStoreName:string = getActionStoreName();
                        if(actionStoreName && this.findMapName(actionStoreName) == actionStoreToken){
                            this._orderConnectorList.delete(key);
                        }
                    })
                    // 
                    this._actionStores.has(storeToken) && this._actionStores.delete(storeToken);
                }
                
            }
            // 
            coupleTokens = interator.next().value;
            // 
        }
        // 
    }

    
    // ----------- PRIVATE -----------

    /**
     * 
     * @param stateName 
     * @returns 
     */
    // public searchAllRegisterNames(stateName:string):string[]{
    //     return this.searchAllRegisterNames(stateName);
    // }

    /**
     * 
     * @param stateName 
     * @param fullStateName 
     * @returns 
     */
    private isSameName(stateName:string, fullStateName:string):boolean{
        const splittingKeys:string[] = fullStateName.split(/\<|\>/);
        return (splittingKeys.indexOf(stateName) != -1);
    }

}


