
import { error, js, log, warn, _decorator } from 'cc';
import { DEBUG, DEV } from 'cc/env';
import { Action, ActionObject } from '../core/Action';
import { IRenderOrderDispatcher } from '../interfaces/IDispatcher';
import { FunctionTask, OnRender, PromiseTask, RelativeMap, RenderTask } from '../types/GamifyType';
import { GAMIFY_DATA_LOGS, GAMIFY_LOGS, GAMIFY_RENDER_LOGS, GAMIFY_WARNNING, MAX_TIME_RENDER, REJECT_WHEN_TIMEOUT } from '../utils/Setting';
import { ActionStoreDispatcher } from './ActionStoreDispatcher';
import { Helper } from '../utils/Helper';
const { ccclass } = _decorator;


/**
 * iu khin th t Render ca View.
 * 1. To Promise cho cc render task ca cc GamifyComponent
 * 2. Khi kt thc qu trnh cp nht data, chy cng lc cc Promise task.
 * 3. Trong qu trnh reduce. nh du cc render task ph thuc.
 * 
 */
@ccclass('RenderOrderDispatcher')
export class RenderOrderDispatcher extends ActionStoreDispatcher implements IRenderOrderDispatcher {
    // 
    protected _nullObject:object = Object.create(null);
    //
    private _orderTasks: string[] = null;       // Th t cc store v component trong lung hin th.
    
    // private _renderTasks: FunctionTask = null;  // Cha cc request render.

    // private _completedRenderTasks: Function[] = null;  // Cha cc completed render callback .

    // protected _pendingRenderIds: string[] = null;   // Cc task cn li cha render. Khi mng ny rng s bn ra s kin kt thc render. Ch c gi tr chnh thc khi bt u rendering.

    protected _isRendering:boolean = false;

    // protected _selectedId:string = null;

    /**
     * _relativeMap l data key vi value l string[], mi key l token id ca mt task render, v value l chui cc id tip theo c render.
     */
    // protected _relativeMap: RelativeMap = null;   // Bn  quan trng trong vic sp xp th t render.

    protected _renderMap:Map<string, RenderTask> = null;

    /**
     *
     */
    constructor(id:string) {
        super(id);
        // this._pendingRenderIds = [];
        // this._renderTasks = Object.create(null);
        // 
        this._renderMap = new Map();
        // this._completedRenderTasks = [];
    }

    //----------------- Feature -----------------

    /**
     * Hm update relativeMap, a token id ca phn t cui trong tham s ids
     * vo mng ca cc ids khc.
     * 
     * @param ids 
     * @param relativeMap 
     */
    protected _applyRenderBefore(ids:string[], relativeMap:RelativeMap){
        let validateIds:string[] = this._validateIds(ids);
        let selectedId:string = validateIds.pop();
        validateIds.forEach((tokenId:string)=>{            
            let _waitIds:string[] = relativeMap[tokenId] || [];
            _waitIds = _waitIds.concat([selectedId]);
            relativeMap[tokenId] = _waitIds;
        })
        relativeMap[selectedId] = relativeMap[selectedId] || [];
    }

    
    /**
     * ng k cc onRender function ca GamifyComponent vo b qun l render theo th t.
     * @param id            token id hoc state name c ng k vi h thng.
     * @param onStartRender      Hm onRender ca cc GamifyComponent
     * @param capturedState     State Data ca action ti thi im gi ng k render. 
     */
    public registerRender(id:string, capturedState:any = null, onStartRender?:OnRender, onCompletedRender?:Function, onCompletedAllRenders?:Function){
        if(!this._pendingAction){
            DEV && error('You have just register a render when run on data flow. (Hm registerRender ch c gi khi ang chy lung d liu !)')
        }
        const capturedAction:Action = this._pendingAction as Action;            
        const actionType:string = capturedAction.type;
        const renderTask:RenderTask = this.getRenderTask(actionType);
        const functionTask:FunctionTask = renderTask.tasks;    
        if( functionTask && this.isDispatching() ) {
            const tokenId:string = this._validateIds([id])[0];
            DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('   REGISTER RENDER: ' + id + ' -tokenId: ' + tokenId +'   - action: ' + (this._pendingAction as Action).type);
            //             
            const relativeMap:RelativeMap = renderTask.map;
            const allCompletedMethods:Function[] = renderTask.callbacks;
            const pendingRenderTasks:Set<string> = renderTask.pending;
            // ng k cho php render cho component id
            // this._relativeMap[tokenId] = this._relativeMap[tokenId] || [];
            // this._renderTasks[tokenId] = this._renderTasks[tokenId] || [];
            // 
            relativeMap[tokenId] = relativeMap[tokenId] || [];
            functionTask[tokenId] = functionTask[tokenId] || [];
            // pendingRenderTasks.add(tokenId);
            // 
            // ng k hm OnRender cho id tng ng.
            const registerRenderTask:PromiseTask = (sharedData:any)=>{
                //
                // onCompletedAllRenders && this._completedRenderTasks.push(onCompletedAllRenders);
                onCompletedAllRenders && allCompletedMethods.push(onCompletedAllRenders);
                pendingRenderTasks.add(tokenId);
                // 
                return new Promise<any>((resolve:Function, reject:Function)=>{
                    // DEBUG && GAMIFY_LOGS && log('>>>> EXCUTE RENDER TASK: ' + this.findMapName(tokenId) + ' - MAP: ' + this._pendingRenderIds);
                    const storeName:string = this.findMapName(tokenId)
                    //    pendingRenderTasks.delete(tokenId);
                    // const removeIndex:number = this._pendingRenderIds.indexOf(tokenId)
                    // if(removeIndex !== -1) { this._pendingRenderIds.splice(removeIndex, 1) }
                    // 
                    let timeOutId:any;
                    if((DEBUG || DEV) && !GAMIFY_RENDER_LOGS){
                        timeOutId = setTimeout(()=>{                            
                            DEBUG && warn('[WARNING] TIMEOUT RENDER TASK: ' + tokenId);                            
                            DEBUG && warn('|________ Maybe, You could forget calling done function for the render process from \"' + storeName + '\" component with action: ' + actionType + ' on time. The MAX_TIME_RENDER is ' + MAX_TIME_RENDER + 'second. This MAX_TIME_RENDER constance is configured in gamify/ui/Setting.ts  ');
                            DEBUG && warn('          (C th  qun done render cho component ' + storeName + ' trong lung action ' + actionType + '. iu chnh thi gian ti a cho vic bo li ny trong \"gamify/ui/Setting.ts\" hng s MAX_TIME_RENDER )');
                            clearTimeout(timeOutId);
                            REJECT_WHEN_TIMEOUT && reject && reject();
                        }, MAX_TIME_RENDER*1000) ;
                    }
                    //
                    DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('>>>> START RENDER COMPONENT: ' + storeName + ' -tokenId: ' + tokenId + ' -action: ' + actionType);
                    onStartRender && onStartRender((continueSharedData:any)=>{
                        if(DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS)) {
                            log('>>>>>>>> COMPLETED RENDER COMPONENT: ' + storeName + ' -tokenId: ' + tokenId + ' -action: ' + actionType);                            
                        }
                        onCompletedRender && onCompletedRender(actionType);
                        //
                        (DEBUG || DEV) && clearTimeout(timeOutId);
                        // 
                        if(pendingRenderTasks.has(tokenId)) {pendingRenderTasks.delete(tokenId);}
                        //                    
                        continueSharedData = sharedData ? Object.assign(sharedData, continueSharedData) : continueSharedData;
                        resolve && resolve(continueSharedData);
                        if(pendingRenderTasks.size == 0){
                            this._stopRendering(continueSharedData, capturedAction);
                        }
                    }, capturedState, sharedData, capturedAction);
                    //
                })
                //
            };
            // 
            // this._renderTasks[tokenId].push(registerRenderTask);
            functionTask[tokenId].push(registerRenderTask);
            // 
        }
        // 
    }
    
    
    /**
     * Xa mt onRender  ng k.
     * @param id    token id hoc state name c ng k vi h thng.
     */
     public removeRender(id:string){
        // const selectedActionType:string = actionType || this._pendingAction ? (this._pendingAction as Action).type : null;
        const tokenId:string = this._validateIds([id])[0];
        this._renderMap.forEach((renderTask:RenderTask)=>{
            // Xo funtionTask
            const functionTask:FunctionTask = renderTask.tasks
            delete functionTask[tokenId];
            // Xo map ca tokenId trong relativeMap
            const relativeMap:RelativeMap = renderTask.map;
            delete relativeMap[tokenId];
            // Xo ton b tokenId lin quan trong relativeMap.
            const keys:string[] = Object.keys(relativeMap)
            keys.forEach((key:string)=>{                
                const relativeKeys:string[] = relativeMap[key]
                const index:number = relativeKeys.indexOf(key)
                if(index != -1){
                    relativeKeys.splice(index , 1);
                    relativeMap[key] = relativeKeys;
                }
            })
        })
        // 
    }


    /**
     * ng k cc component chy trc component hin ti.
     * @param beforeIds     Cc token id hoc state name chy render trc component hin ti
     * @param currentId     token id hoc state name ca component hin ti, ci m c ng k vi h thng.
     */
    public renderBefore(beforeIds:string[], currentId:string){
        // const selectedActionType:string = actionType || this._pendingAction ? (this._pendingAction as Action).type : null;
        const selectedActionType:string = this._pendingAction ? (this._pendingAction as Action).type : null;
        if(!selectedActionType){
            DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && error('Khng ng k render th t c -action: ' + selectedActionType + ' - first id: ' + beforeIds + ' - currentId: ' + currentId);
        }
        const renderTask:RenderTask = this.getRenderTask(selectedActionType);
        this._applyRenderBefore(beforeIds.concat(currentId), renderTask.map);
    }

    
    /**
     * Whether the component is rendering or not. (Component c ang chy render hay khng.)
     * @returns 
     */
    public isRendering():boolean{
        return this._isRendering;
    }

    /**
     * 
     * @param actionType 
     */
    protected getRenderTask(actionType:string):RenderTask {
        let renderTask:RenderTask = this._renderMap.get(actionType);
        if(!renderTask){
            renderTask = {
                pending:new Set(),
                map: Object.create(null),
                tasks: Object.create(null),
                callbacks: []
            }
            this._renderMap.set(actionType, renderTask);
        }
        return renderTask;
    }


    /**
     * 
     * @returns 
     */
    protected _startRendering(relativeMap:RelativeMap, funtionRenderTasks:FunctionTask, capturedAction?:Action, pendingTasks?:Set<string>){
        // log('Start Rendering !!!')
        if(funtionRenderTasks && !js.isEmptyObject(funtionRenderTasks) && !this.isRendering()){
            // DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('\n > EXCUTE RENDER for Action: "' + capturedAction.type + '" --- Map:: ' + JSON.stringify(relativeMap));
            // Sort tasks to show correct order. (Sp xp cc task  render ng th t.)
            let orderRenderTasks:string[] = Object.keys(funtionRenderTasks);
            if(orderRenderTasks.length){
                // Arrange render order according to relativeMap. (Sp xp th t render theo relativeMap.)               
                orderRenderTasks = this.sortRelativeMap(orderRenderTasks, relativeMap);
                // 
                // this._pendingRenderIds = orderRenderTasks.concat(this._pendingRenderIds);
                //  
                // this._isRendering = this._pendingRenderIds.length ? true : false;
                this._isRendering = pendingTasks.size != 0 ? true : false;
                //  
                // Use to view rendering progress details. (S dng  xem chi tit tin trnh render.)  
                let relativeMapToString:string;
                if(DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS)){
                    relativeMapToString = JSON.stringify(relativeMap);                    
                    log('\n>>> RENDER INFO :: ACTION: ' + capturedAction.type)                    
                    
                    log('|__ Render Tasks : ' + Object.keys(funtionRenderTasks));
                    log('|__ Relative Map : ' + relativeMapToString);
                    // log('>>>> Pending Render Tasks  : ' + this._pendingRenderIds);                    
                    if(orderRenderTasks && orderRenderTasks.length) {
                        log('|__ Sort Render Tasks : ' + orderRenderTasks);
                        const lastOrderRenderTaskId:string = orderRenderTasks[orderRenderTasks.length-1];
                        log('|__Last Render State: -tokenId: '+ lastOrderRenderTaskId + ' -state name: ' + this.findMapName(lastOrderRenderTaskId));
                    }
                    log('----------------- END INFO \n \n')
                }
                //
                const tasks:Promise<any>[] = [];
                const logTasks:string[] = [];
                while(orderRenderTasks.length) {
                    const orderRenderId:string = orderRenderTasks.shift();                    
                    if(Object.prototype.hasOwnProperty.call(relativeMap, orderRenderId)){
                        if(GAMIFY_RENDER_LOGS){
                            logTasks.push("\n");
                        }
                        // Gom cc progress c lin kt thnh mt promise qua __excuteTask method.
                        // !GAMIFY_RENDER_LOGS ? 
                        tasks.push(this.__excuteTask(orderRenderId, relativeMap, funtionRenderTasks, Promise.resolve(), logTasks)) 
                        // tasks.push( this.__excuteTask(orderRenderId, relativeMap, funtionRenderTasks, Promise.resolve(), logTasks).then(()=>{                         
                        //     if(DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && logTasks && logTasks.length ){
                        //         log('\n ------------ COMPLETED TASK: ' + logTasks + ' -action: ' + capturedAction.type + '  \n');
                        //     }
                        //     return Promise.resolve();
                        // }) );
                    }                    
                }
                // Remaining tasks run on the parallel progress.
                Promise.all(tasks).then((sharedData:any)=>{
                    DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('COMPLETE ALL RENDER TASKS FOR ACTION: ' + capturedAction.type);
                    DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('           |___ relativeMap: ' + relativeMapToString);
                    DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('           |___ logTasks: ' + logTasks);
                    // DEBUG && log('COMPLETE ALL RENDER !!!' + (capturedAction as Action).type);
                    if(orderRenderTasks.length) warn('?????????????????? Van con task dang chay !')
                    if(Array.isArray(sharedData) && sharedData.length == 1){                        
                        sharedData = sharedData[0];
                    }
                    // this._stopRendering(sharedData, capturedAction);
                })
            }
            //            
        }
        //
    }

    
    /**
     * 
     * @param id 
     * @param relativeMap 
     * @param renderTasks 
     * @param lastPromise 
     */
    private __excuteTask(id:string, relativeMap:RelativeMap, renderTasks:FunctionTask, lastPromise:Promise<any>, logTasks:string[] = []):Promise<any>{
        if(relativeMap && renderTasks){ 
            let ids:string[] = relativeMap[id];
            if(ids){
                DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && logTasks && logTasks.push(this.findMapName(id));
                // Delete to avoid re-calling again. (Xa  trnh gi li ln na.)
                delete relativeMap[id];           
                // Where the render is registered multiple times. Append the corresponding renderTasks in order. (Trng hp render c ng k nhiu ln. Ni cc renderTask tng ng chy theo th t.)
                const renderPromiseTasks: PromiseTask[] = renderTasks[id];
                if(renderPromiseTasks && renderPromiseTasks.length){     
                    while(renderPromiseTasks.length){
                        const renderTask:PromiseTask = renderPromiseTasks.shift();
                        if(renderTask){
                            lastPromise = lastPromise ? lastPromise.then(renderTask) : renderTask();
                        }
                    }                    
                }else{
                    DEV && GAMIFY_WARNNING && warn('No task in renderPromiseTasks !!!!')
                }
                //
                while(ids.length){                    
                    this.__excuteTask(ids.shift(), relativeMap, renderTasks, lastPromise, logTasks);                    
                }
                // 
            }            
        }
        return lastPromise //|| Promise.resolve();
    }

    //------------ SORTING --------------

    /**
     * 
     * @param relativeMap 
     * @param pendingIds 
     * @param sorttings 
     * @returns 
     */
    protected sortRelativeMap(orderRenderTasks:string[], relativeMap:object, pendingIds?:object, sorttings?:string[]){
        const pendingObj = pendingIds || Object.create(null);
        let sorts:string[] = sorttings || [];
        for (let id in relativeMap) {
            if (pendingObj[id]) {
                continue;
            }
            this.invokeSortRenderTasks(id, relativeMap, pendingObj, sorts);
        }
        let reverseList:string[] = sorts.reverse();
        reverseList = reverseList.filter((taskId:string)=>{
            return (orderRenderTasks.indexOf(taskId) != -1);
        })
        return reverseList;
    }

    /**
     * waitRenderTask kt hp vi invokeSortRenderTasks tm ra th t relativeMap c sp xp cover ton b cc case render song song ln ni tip.
     * @param ids 
     * @param relativeMap 
     * @param pendingIds 
     * @param sorttings 
     */
    protected waitRenderTask(ids:string[], relativeMap:object, pendingIds?:object, sorttings?:string[]){
        for (let ii:number = 0; ii < ids.length; ii++) {
            let id = ids[ii];
            if(pendingIds && pendingIds[id]){
                continue;
            }
            this.invokeSortRenderTasks(id, relativeMap, pendingIds, sorttings)
        }
    }

    /**
     * invokeSortRenderTasks kt hp vi waitRenderTask tm ra th t relativeMap c sp xp cover ton b cc case render song song ln ni tip.
     * @param id 
     * @param relativeMap 
     * @param pendingIds 
     * @param sorttings 
     */
    protected invokeSortRenderTasks(id:string, relativeMap:object, pendingIds?:object, sorttings?:string[]){
        pendingIds[id] = true;
        const rightIds:string[] = relativeMap[id];        
        if(rightIds && rightIds.length){
            this.waitRenderTask(rightIds, relativeMap, pendingIds, sorttings);
        }
        sorttings.push(id);
    }

    // ---------------
    /**
     * 
     */
    protected _stopRendering(sharedData?:any, capturedAction?:Action){
        // DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && 
        DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('> STOP ALL RENDER ------------------ ' + capturedAction.type);
        // Chi co duy nhat completedAllRender cua GamifyComponent render dau tien duoc goi.
        const actionType:string = capturedAction.type;
        const renderTask:RenderTask = this.getRenderTask(actionType);
        const compltedHandlers:Function[] = renderTask.callbacks;
        // const completedFunc:Function = compltedHandlers.shift();
        // if(completedFunc) {completedFunc(sharedData, capturedAction);}
        while(compltedHandlers.length){
            const completedFunc:Function = compltedHandlers.shift();
            if(completedFunc) {completedFunc(sharedData, capturedAction);}
        }
        // this._renderMap.delete(actionType)
        // 
        // while(this._completedRenderTasks.length){
        //     let completedFunc:Function = this._completedRenderTasks.shift();
        //     if(completedFunc) {completedFunc();}
        // }
        this._isRendering = false;
    }

   
    //----------------- Override ----------------

        
    /**
     * 
     * @param action 
     */
    protected _startDispatching(action:Action) {        
        // this._relativeMap = Object.create(null);
        this._orderTasks = new Array<string>();
        // this._renderTasks = Object.create(null);
        super._startDispatching(action);
    }


    /**
     * Added: allow calling callbacks when loop errors are encountered.
     * (B sung: cho php gi li cc callback khi gp li vng lp.)
     * @internal
     */
    protected _invokeCallback(id:string) {        
        // this._selectedId = id;                   
        super._invokeCallback(id);        
        DEBUG && (GAMIFY_DATA_LOGS || GAMIFY_LOGS) && log('>>>>> REDUCE STORE: Invoke callback from ' + this.findMapName(id) + '---------');
        if(this._isPending[id]) {
            this._orderTasks.push(id);
        }
    }

    
    /**
     * Hm t ng c gi khi lung d liu kt thc.
     */
    protected _stopDispatching() {
        const capturedAction:Action = this._pendingAction as Action;
        const actionType:string = capturedAction.type;
        const renderTask:RenderTask = this.getRenderTask(actionType);        
        super._stopDispatching();
        this._startRendering(renderTask.map, renderTask.tasks, capturedAction as Action, renderTask.pending);
        // this._renderTasks = null;
        // this._relativeMap = null;
        this._orderTasks = null;
    }

    
    /**
     * Thay i thng bo t error sang warnning khi x l li khi gp vng lp d liu.
     * @param id 
     */
    protected __detectCircleLoop(id:string){
        if(!this._isHandled[id]){
            this._isPending[id] = false;
            if(DEBUG && GAMIFY_WARNNING) { 
                let selectedName:string = this.findMapName(this._penddingId);
                let secondName:string = this.findMapName(id);
                warn( this.__ccClassName + '.waitFor(...): Detect circular loop error (A waits for B, B waits for A) when ' + selectedName + ' calls ' + secondName );
                warn( '(' + this.__ccClassName + '.waitFor(...): Pht hin li lp vng trn (A i B, B i A) khi ' + selectedName + ' gi vo ' + secondName + ')');
                warn( selectedName + ' used old state of ' + secondName);                
                warn( '('+selectedName + '  s dng state c ca ' + secondName + ')');
            }
        }
    }


    /**
     * 
     */
    destroy(){
        // this._completedRenderTasks = null;
        this._renderMap = null;
        // this._renderTasks = null;
        this._orderTasks = null;       
        // this._relativeMap = null;
        super.destroy();
    }

}

