import { _decorator, Component, Node } from 'cc';
import { importRemoteCCClass, remoteCCClass } from 'db://assets/Scripts/gamify/utils/RemoteScriptImporter';
import { IGamifyComponent } from '../../../Scripts/gamify/interfaces/IComponent';
import { Action } from '../../../Scripts/gamify/core/Action';
import { SpriteFrame } from 'cc';
import { Color } from 'cc';
import { log } from 'cc';
import { sys } from 'cc';
import { API_ACTION, MainGameAction } from '../../../Scripts/ui/actions/StartActions';
import { error } from 'cc';
import { DEV } from 'cc/env';
import { tween } from 'cc';
import { Tween } from 'cc';
import { UIOpacity } from 'cc';
import { ServiceRequestConfig } from '../../../Scripts/ui/api/ServiceRequestConfig';
import { APIServiceAdaptor } from '../../../Scripts/ui/api/APIServiceAdaptor';
import { SpriteAtlas } from 'cc';
const { ccclass, property } = _decorator;

const CSSButtonStyle = `.lobbyBtn{width:60px;height:60px;position:absolute;right:0;top:0;margin-top:20px;margin-right:20px;border-radius:50%;background-color:#763a00;padding:0;border:0;}.lobbyBtn:hover{background-color:#fca14c;}.lobbyBtn:active{background-color:#4b4b4b;}`;

const GamifyComponent:any = importRemoteCCClass('GamifyComponent')

enum LobbyPopupStatus {
    INIT,
    SETUP_COMPLETED
}

type LobbyPopupSate = {
    status:LobbyPopupStatus,
    enableLobby:boolean,
    lobbyURL:string
}



@remoteCCClass('LobbyPopup')
export class LobbyPopup extends GamifyComponent implements IGamifyComponent {
    
    @property({type:Color})
    buttonColor:Color = new Color();

    // @property({type:SpriteAtlas})
    // atlas:SpriteAtlas

    @property({ type: SpriteFrame })  
    homeIcon:SpriteFrame = null;  
    // public get homeIcon(): SpriteFrame {
    //     if(this.atlas && this._homeIcon){
    //         const sp:SpriteFrame = this.atlas.getSpriteFrame(this._homeIcon.name);
    //         return sp
    //     }
    //     return this._homeIcon;
    // }

    // public set homeIcon(value: SpriteFrame) {
    //     if(this.atlas){
    //         this._homeIcon = this.atlas.getSpriteFrame(value.name);
    //         this._homeIcon
    //     }else{ 
    //         this._homeIcon = value;
    //     }
    // }

    @property({serializable:true})
    private _homeIcon: SpriteFrame = null;

    @property({type:ServiceRequestConfig})
    serviceRequest:ServiceRequestConfig = null;

    // @property({type:APIServiceAdaptor})
    // newtworkAdaptor:APIServiceAdaptor = null;

    servieNetworkName:string = null;
    
    private fillImgWrapper:HTMLButtonElement = null;

    private lobbyURL:string;

    // onStoreRegisted(state: any, action: Action): void {
    //     if(action.sender == this.getStateName()){
    //         console.log('-----------')            
                         
    //     }
    // }

    // onInit(readyStates?: string[]): void {
       
    // }

    /**
     * 
     * @param state 
     * @param action 
     */
    onData(state:LobbyPopupSate, action:Action){
        const actionType:string = action.type;
        const payload:any = action.payload;
        let networkState:any
        switch(actionType){
            case API_ACTION.AUTH_RESULT:
                log('auth result');
            case MainGameAction.INIT:                
                if(payload && payload.apiService) {
                    this.servieNetworkName = payload.apiService;
                }
                networkState = this.getAPIData(this.servieNetworkName);
                state.enableLobby = (!sys.isMobile && networkState && networkState?.config && (networkState?.config?.homeButton === 'true' || networkState?.config?.homeButton === true));
                // 
                if(state.enableLobby){
                    const urlSearchParams = new URLSearchParams(window.location.search);
                    state.lobbyURL = urlSearchParams.get('redirectUrl');
                    if (state.lobbyURL && this.isURLValid(state.lobbyURL)) {
                        state.lobbyURL = decodeURIComponent(state.lobbyURL);
                        this.lobbyURL = state.lobbyURL;
                        state.status = LobbyPopupStatus.SETUP_COMPLETED;
                    } else {                   
                        state.status = LobbyPopupStatus.INIT;
                    }

                    this.render();
                }
                
            case this.serviceRequest.ResponseAction:  
                networkState = this.getAPIData(this.servieNetworkName);
                state.lobbyURL = networkState?.config?.lobbyURL;
                this.lobbyURL = state.lobbyURL;
                state.status = LobbyPopupStatus.SETUP_COMPLETED;
                this.render();
                break
        }
    }

    /**
     * 
     * @param done 
     * @param capturedState 
     * @param sharedData 
     */
    async onRender(done:Function, capturedState?:LobbyPopupSate, sharedData?:any){
        switch(capturedState.status){
            case LobbyPopupStatus.INIT:                
                this.serviceRequest.request();
                break;

            case LobbyPopupStatus.SETUP_COMPLETED:
                this.hidePopup();
                this.displayHomeButton();
                this.setButtonColor(this.buttonColor.toString())  
                break;
        }
        done();
    }

    //-----------------

    /**
     * 
     * @returns 
     */
    getAPIData(serviceName:string):any{
        if(serviceName){
            const serviceData:any = this['getOtherState'](serviceName) as any;
            if(!serviceData) {
                error('[Lobby Popup]: Khng c hoc setup nhm service');
                return null;
            }
            return serviceData;
        }else {            
            error('[Lobby Popup] Cha khai bo service name ! C th do StartGameAction.INIT cha c dispatch.')
        }
        return null;
    }

    //-----------------

    private isURLValid(urlStr: string): boolean {
        return (urlStr != undefined && urlStr != null && urlStr != '#' && urlStr.length > 0);
    }

    /**
     * 
     * @param useEffect 
     */
    private async showPopup(useEffect:boolean = false){
        const uiOpacity:UIOpacity = this.node.getComponent(UIOpacity) || this.node.addComponent(UIOpacity);
        this.node.children.forEach((childNode:Node)=>{            
            childNode.active = true;
        })
        if(useEffect){
            Tween.stopAllByTarget(this.node);
            await new Promise((resolve: Function) => {                
                tween(uiOpacity).set({
                    opacity: 0
                })
                .to(0.4, { opacity: 255 })
                .call(resolve)
                .start();
            });
        }else{
            uiOpacity.opacity = 255;
        }
    }

    /**
     * 
     * @param useEffect 
     */
    private async hidePopup(useEffect:boolean = false){
        if(useEffect){
            Tween.stopAllByTarget(this.node);
            await new Promise((resolve: Function) => {
                const uiOpacity:UIOpacity = this.node.getComponent(UIOpacity) || this.node.addComponent(UIOpacity);
                tween(uiOpacity).set({
                    opacity: 255
                })
                .to(0.4, { opacity: 0 })
                .call(resolve)
                .start();
            });
        }
        // 
        this.node.children.forEach((childNode:Node)=>{                
            childNode.active = false;
        })        
    }
    /**
     * 
     */
    protected displayHomeButton(){
        if(this.homeIcon){
            const styleSheet = document.createElement("style");
            styleSheet.innerText = CSSButtonStyle;
            document.head.appendChild(styleSheet)

            // Create a wrapper div for fillImg
            this.fillImgWrapper = document.createElement('button');
            this.fillImgWrapper.className = 'lobbyBtn';
            let fillImg = document.createElement('img');
            fillImg.className = 'lobbyBtnImg';
            fillImg.src = this.homeIcon.texture.toString()
            fillImg.addEventListener('click', this.onHomeButtonPressed.bind(this));

            this.fillImgWrapper.appendChild(fillImg); // Append fillImg to the wrapper div
            document.body.appendChild(this.fillImgWrapper); // Append the wrapper div to the body


        }else{

        }
    }

    protected setButtonColor(colorStr: string) {
        if (this.isLobbyEnabled && this.fillImgWrapper) {
            var regex = /[0-9A-Fa-f]{6}/g;
            if (colorStr.match(regex)) {
                let styleSheet = document.createElement("style");
                styleSheet.innerText = `.lobbyBtn_1{background-color:${colorStr}}`;
                document.head.appendChild(styleSheet);
                this.fillImgWrapper.classList.add('lobbyBtn_1');
            }
            else {
                log('Color string is invalid - ', colorStr);
            }
        }
    }

    // ---------

    async onHomeButtonPressed() {
        await this.showPopup(true);
    }
    
    async onCancelButtonPressed(){
        await this.hidePopup(true);
    }

    async onOKButtonPressed(){
        this.hidePopup(true);
        if (this.lobbyURL && this.isURLValid(this.lobbyURL)) {
            //redirect
            window.location.replace(this.lobbyURL);
        } else {
            error('Invalid redirect URL ' + this.lobbyURL);
        }
    }
}


