import { SpriteAtlas } from 'cc';
import { Sprite } from 'cc';
import { Font } from 'cc';
import { CCBoolean } from 'cc';
import { CCString } from 'cc';
import { SpriteFrame } from 'cc';
import { Label } from 'cc';
import { warn } from 'cc';
import { resources } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { GamifyComponent } from '../../gamify/GamifyComponent';
import { Helper } from '../../gamify/utils/Helper';
import { JsonAsset } from 'cc';
import { Action } from '../../gamify/core/Action';
import { STORE_ACTION } from '../../gamify/GamifyStore';
// import { MultilingualLabel } from './MultilingualLabel';
import { DEV, EDITOR } from 'cc/env';
import { log } from 'cc';
import { IGamifyComponent } from '../../gamify/interfaces/IComponent';
import { AssetManager } from 'cc';
const { ccclass, property } = _decorator;

export const SupportedLanguages = ["en", "cs", "da", "de", "el", "es", "es_la", "fi", "fr", "hu", "it", "nl", "no", "pl", "pt", "pt_br", "ro", "sk", "sv", "tr", "uk_ua", "bg", "id", "ja", "ko", "ru", "th", "tzh", "vi", "zh"];

const DEFAULT_ATLAS:string = "text_atlas";
const DEFAULT_COMMON_TEXT:string = "common";
const DEFAULT_CUSTOM_FONT:string = "font";
const DEFAULT_GAME_TEXT:string = "text";

@ccclass('LocalizationController')
export class LocalizationController extends GamifyComponent implements IGamifyComponent {
    static ACTION:{LANGUAGE_PACKAGE_LOADED:string} = { LANGUAGE_PACKAGE_LOADED:'language_package_loaded' };
    //raw text, json format
    static textInfo: {[n:string]:string} = null;
    static jsonPackages:Set<string> = new Set();
    //
    private static thaiFont: Font = null;
    private static _language: string = 'en';
    private static _isReady: boolean = false;
    static get language():string{
        return this._language
    }

    static setLanguage(lang:string){
        this._language = lang;
    }

    //image texts
    private static textAtlas: SpriteAtlas = null;


    static setText(sprite: Sprite, name: string) {
        sprite.spriteFrame = null;
    }

    static getRawText(id: string): string {
        if(DEV && !EDITOR){
            !LocalizationController.textInfo && warn('All text datas are not exist !')
            LocalizationController.textInfo && !LocalizationController.textInfo[id] && warn('The text data map with id "' + id + '" is not exist ! (This warnning message is only available in DEV mode)');
        }
        return LocalizationController.textInfo && LocalizationController.textInfo[id] ? LocalizationController.textInfo[id] as string : id;
    }
    
    static getSpriteFrame(id: string): SpriteFrame {        
        return LocalizationController.textAtlas ? LocalizationController.textAtlas.getSpriteFrame(id) : null;
    }

    // static addTextInfo(obj: {key: string, text: string}, isOverwrite: boolean = true) {
    static addTextInfo(obj: {[n:string]:string}, isOverwrite: boolean = true) {
        if (isOverwrite) {
            LocalizationController.textInfo = {...LocalizationController.textInfo, ...obj};
        } else {
            LocalizationController.textInfo = {...obj, ...LocalizationController.textInfo};
        }
    }
    
    static addJSONPackage(packageName:string, obj: {[n:string]:string}, isOverwrite: boolean = true) {
        if(!LocalizationController.jsonPackages.has(packageName)){
            if (isOverwrite) {
                LocalizationController.textInfo = {...LocalizationController.textInfo, ...obj};
            } else {
                LocalizationController.textInfo = {...obj, ...LocalizationController.textInfo};
            }
            LocalizationController.jsonPackages.add(packageName);
        }
    }

    static get isReady():boolean {
        return this._isReady;
    }

    // @property({
    //     type:[Sprite],
    //     group: 'Setup'
    // })
    // textSpriteList: Sprite[] = [];

    // @property({
    //     type:[Label],
    //     group: 'Setup'
    // })
    // rawTextList: Label[] = [];

    @property({
        group: 'Setup',
        displayName: 'Auto Replace Mode'
    })
    isAutoReplaceMode: boolean = false;

    @property({ 
        type: [CCString],
        group: 'Setup', 
        visible: function (this: LocalizationController) { return this.isAutoReplaceMode; } 
    })
    namePatternList: string[] = [];

    @property
    localizedPath:string = 'localizes';
    
    get currentLanguage():string{
        return LocalizationController.language
    }

    get initialState():any{
        return {
            loaded:false
        }
    }

    
    onInit(): void {
        // if (LocalizationController.textInfo) {
        //     //resources are loaded
        //     if (this.isAutoReplaceMode) {
        //         this.autoReplaceRawText();
        //     }
        //     this.onReady();            
        // }else{
        //     const urlVars:object = Helper.getUrlVars();
        //     const selectedLang:string = this.verifyLanguage(urlVars['language'] || LocalizationController.language);  
        //     LocalizationController.setLanguage(selectedLang);
        //     this.loadTextResources(selectedLang)
        // }
        const urlVars:object = Helper.getUrlVars();
        const selectedLang:string = this.verifyLanguage(urlVars['language'] || LocalizationController.language);  
        LocalizationController.setLanguage(selectedLang);
        this.loadTextResources(selectedLang).then(()=>{
            if (this.isAutoReplaceMode) {
                this.autoReplaceRawText();
            }
        })
    }

    /**
     * 
     * @param state 
     * @param newStateName 
     * @param newOtherState 
     */
    onStoreRegisted(state: any, action:Action): void {
        if (LocalizationController.textInfo){  
            // Catching these stores register after this store.               
            this.dispatch({
                type: LocalizationController.ACTION.LANGUAGE_PACKAGE_LOADED
            }, this.getStateName(), action.sender)
        }
    }

    /**
     * 
     * @param state 
     * @param action 
     */
    // onData(state:any, action:Action):any|void{
    //     const actionType:string = action.type;        
    //     switch(actionType){   
    //         case STORE_ACTION.NEW_STATE_REGISTED:  
    //             if (LocalizationController.textInfo){  
    //                 // Catching these stores register after this store.
    //                 const newStateName:string = action.sender;                
    //                 this.dispatch({
    //                     type: LocalizationController.ACTION.LANGUAGE_PACKAGE_LOADED
    //                 }, this.getStateName(), newStateName)
    //             }
    //             break;
            
    //     }
    // }

    /**
     * 
     * @param lang 
     */
    async loadTextResources(lang: string = null){
        if (!LocalizationController.textInfo) {
            await Promise.all([this.imageLoading(lang), this.allTextLoading(lang)]);
            // LocalizationController.setLanguage(lang);
        }
        await this.onReady();
        // this.dispatch({type:LocalizationController.ACTION.LANGUAGE_PACKAGE_LOADED});
    }

    /**
     * 
     * @param lang 
     */
    async allTextLoading(lang: string = null){
        await Promise.all([this.commonTextLoading(lang), this.gameTextLoading(lang), this.customTextLoading(lang)]);
        await this.onTextReady(lang);
    }

    /**
     * 
     */
    // replaceText() {
    //     // this.replaceTextImage();
    //     this.onTextReady();
    // }

    // -----------------------------------------------
    // 
    // -----------------------------------------------

    /**
     * 
     * @param lang 
     * @param defaultPath 
     * @returns 
     */
    protected getLanguageURL(lang:string, defaultPath:string):string{
        return this.localizedPath + "/" + lang + "/" + defaultPath;
    }   

    /**
     * 
     */
    protected get languageBundle():AssetManager.Bundle {
        return resources
    }

    /**
     * 
     * @param lang 
     * @returns 
     */
    protected async imageLoading(lang:string){
        await new Promise((resolve: Function) => {
            if(EDITOR) {
                resolve();
                return;
            }
            // const atlasAssetPath:string = this.localizedPath + "/" + lang + "/" + DEFAULT_ATLAS;
            const atlasAssetPath:string = this.getLanguageURL(lang, DEFAULT_ATLAS);
            if(atlasAssetPath){
                this.languageBundle.load(atlasAssetPath, SpriteAtlas,(err, atlas) => {
                    if (err) {
                        (EDITOR || DEV) && warn("Error loading text atlas for language " + lang + " (This message only show on EDITOR or DEV mode)");
                        resolve();
                        return;
                    }
                    if (lang != SupportedLanguages[0]) {
                        this.languageBundle.release(SupportedLanguages[0] + "/" + DEFAULT_ATLAS);
                    }
                    LocalizationController.textAtlas = atlas as SpriteAtlas;                    
                    resolve();
                });
            }
        });     
        await this.onImageTextReady(lang);
    }

    /**
     * 
     * @param lang 
     * @returns 
     */
    protected async commonTextLoading(lang:string){
        return await new Promise((resolve: Function) => {
            if(EDITOR) {
                resolve();
                return;
            }
            // const textAssetPath:string = this.localizedPath + "/" + lang + '/' + DEFAULT_COMMON_TEXT;
            const textAssetPath:string = this.getLanguageURL(lang, DEFAULT_COMMON_TEXT);
            if(textAssetPath){
                this.languageBundle.load(textAssetPath, (err, assets) => {                
                    if (err) {
                        (EDITOR || DEV) && warn('Error loading common text for language ' + lang + " (This message only show on EDITOR or DEV mode)");
                        resolve();
                        return;
                    }
                    if (assets && assets instanceof JsonAsset) {
                        LocalizationController.addTextInfo(assets.json as {[n:string]:string}, false);
                    }else{
                        LocalizationController.addTextInfo(JSON.parse(assets.toString()), false);
                    }
                    resolve();
                });
            }
        });
    }

    /**
     * 
     * @param lang 
     * @returns 
     */
    protected async gameTextLoading(lang:string){
        return await new Promise((resolve: Function) => {
            if(EDITOR) {
                resolve();
                return;
            }
            // const textAssetPath:string = this.localizedPath + "/" + lang + '/' + DEFAULT_GAME_TEXT;
            const textAssetPath:string = this.getLanguageURL(lang, DEFAULT_GAME_TEXT);
            if(textAssetPath){
                this.languageBundle.load(textAssetPath, (err, assets) => {
                    if (err) {
                        (EDITOR || DEV) && warn('Error loading game text for language ' + lang + " (This message only show on EDITOR or DEV mode)");
                        resolve();
                        return;
                    }
                    if (lang != SupportedLanguages[0]) {
                        this.languageBundle.release(SupportedLanguages[0] + '/' + DEFAULT_GAME_TEXT);
                    }
                    if (assets && assets instanceof JsonAsset) {
                        LocalizationController.addTextInfo(assets.json as {[n:string]:string}, false);
                    }else{
                        LocalizationController.addTextInfo(JSON.parse(assets.toString()));
                    }
                    resolve();
                });
            }
        });
    }

    /**
     * 
     * @param lang 
     * @returns 
     */
    protected async customTextLoading(lang:string){
        return await new Promise((resolve: Function) => {
            if(EDITOR) {
                resolve();
                return;
            }
            if (lang == "th") {
                // const fontAssetPath:string = this.localizedPath + "/" + lang + '/' + DEFAULT_CUSTOM_FONT;
                const fontAssetPath:string = this.getLanguageURL(lang, DEFAULT_CUSTOM_FONT)
                if(fontAssetPath){
                    this.languageBundle.load(fontAssetPath, (err, fontAssets) => {
                        if (err) {
                            (EDITOR || DEV)  && warn("Error loading font for language " + lang + " (This message only show on EDITOR or DEV mode)");
                            resolve();
                            return;
                        }
                        LocalizationController.thaiFont = fontAssets as Font;
                        resolve();
                    });
                }
            } else {
                resolve();
            }
        })
    }

    // ------------------------------------------------
    // 
    // ------------------------------------------------

    /**
     * 
     * @param languageName 
     * @returns 
     */
    protected verifyLanguage(languageName:string):string{        
        return (SupportedLanguages.indexOf(languageName) != -1) ? languageName : LocalizationController.language;
    }

    /**
     * 
     */
    async onImageTextReady(lang:string) {}

    /**
     * 
     */
    async onTextReady(lang:string) {}

    /**
     * 
     */
    async onReady(){
        LocalizationController._isReady = true;
        this.dispatch({type:LocalizationController.ACTION.LANGUAGE_PACKAGE_LOADED});
    }

    /**
     * 
     * @returns 
     */
    protected autoReplaceRawText() {
        if (this.namePatternList.length <= 0) {
            return;
        }

        //iterate through all child nodes
        this.node.walk(undefined, (child) => {
            const is_matched: boolean = this.namePatternList.some((pattern:string) => { return child.name.includes(pattern); });
            if (is_matched) {
                const label:Label = child.getComponent(Label);
                if (label) {
                    //Thai texts does not work well with Arial font
                    if (LocalizationController.language == "th") {
                        label.font = LocalizationController.thaiFont;
                        label.cacheMode = Label.CacheMode.NONE;
                    }
                    // 
                    const content_str = LocalizationController.textInfo[label.string] as string;
                    if (content_str) {
                        label.string = content_str;
                    }
                }
            }
        });
    }

}


