
import { _decorator, Node, error, warn, log } from 'cc';
import { DEBUG } from 'cc/env';
import { Action } from './core/Action';
import { Dispatcher } from './core/Dispatcher';
import { MechanicDispatcher } from './dispatchers/MechanicDispatcher';
import { GamifyComponent } from './GamifyComponent';
// import { GamifyStore } from './GamifyStore';
import { IGamifyScenario } from './interfaces/IStore';
import { GAMIFY_WARNNING } from './utils/Setting';
const {ccclass, disallowMultiple} = _decorator;


@ccclass('GamifyScenario')
@disallowMultiple(true)
export class GamifyScenario extends GamifyComponent implements IGamifyScenario {
    
    // protected _parent:GamifyComponent = null;
    private _selectedAction:Action;

    
    //---------------- Life-Cicle -----------------

    // onParentData(state:any, action:Action):any|void{
    //     return state;
    // }

    // onParentChanged(){
    //     // log('--------------onParentChanged---------------')
    // }

    // onParentRejected(){
    //     // log('--------------onParentRejected---------------')
    // }

    

    /**
     * Kim tra xem ang c lung hin th chy khng ?
     * @returns 
     */
    protected isRendering():boolean {
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof MechanicDispatcher){
            return dispatcher.isRendering()
        }
        return false
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    protected searchAllStateNames(stateName:string):string[]{
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof MechanicDispatcher){
            return dispatcher.searchAllRegisterNames(stateName);
        }
        return [];
    }

    // Write Rendering Scenario

    /**
     * Chn action cn vit kch bn.
     * 
     * v d:  yu cu chuyn ng cc Comp1,Comp2,Comp3.
     * 
     * action('PLAY').sequence('Comp1','Comp2','Comp3'); // chy tun t 
     * action('PLAY').parallel('Comp1','Comp2','Comp3'); // chy song song
     * 
     * Hoc gi thm cc config mc nh thng qua action. 
     * V d: Tt c cc action cng type s c thm nonStop = true vo action.payload
     * action({ type: 'PLAY', payload: { nonStop: true } }).sequence('Comp1','Comp2','Comp3');
     * 
     * @param type 
     * @returns 
     */
    protected action(action:string | Action, ...recordReceivers:string[]):GamifyScenario {
        let configAction:Action;
        let receivers:string[] = recordReceivers;
        if('string' == typeof action){
            configAction = {
                type: action
            }
        }else{
            configAction = action;
            receivers = receivers || action.receiver
        }        
        this._selectedAction = configAction;
        return this;
    }

    /**
     * Xa cc kch bn mc nh - cc kch bn c to ra trc khi lung d liu xy ra.
     * @returns 
     */
    protected clear():GamifyScenario{
        if(this._selectedAction){
            let dispatcher:Dispatcher = this.getDispatcher();
            if(dispatcher && dispatcher instanceof MechanicDispatcher){
                if(dispatcher.isDispatching()){
                    DEBUG && GAMIFY_WARNNING && warn(this.__ccClassName + '.clear(): Hm ch tc dng xa cc kch bn mc nh c ng k trc khi dispatch.');
                }
                (dispatcher as MechanicDispatcher).removeDefaultActionScenerio(this._selectedAction);
            }
        }
        return this;
    }


    /**
     * To kch bn chy tun t.
     * @returns 
     */
    public sequence(...ids:string[]):boolean {
        let targetAction:Action = this._selectedAction || this._currentAction
        if(targetAction){
            let dispatcher:Dispatcher = this.getDispatcher();
            if(dispatcher && dispatcher instanceof MechanicDispatcher){                
                (dispatcher as MechanicDispatcher).registerActionScenario(targetAction, ids);
            }
            this._selectedAction = null;
            return true;
        }
        return false;
    }

    /**
     * To kch bn chy c lp.
     * @returns 
     */
    public parallel(...ids:string[]):boolean {
        let targetAction:Action = this._selectedAction || this._currentAction
        if(targetAction){
            let dispatcher:Dispatcher = this.getDispatcher();
            if(dispatcher && dispatcher instanceof MechanicDispatcher){
                ids.forEach((id:string)=>{
                    (dispatcher as MechanicDispatcher).registerActionScenario(targetAction, [id]);
                })
                this._selectedAction = null;
                return true;
            }
        }       
        return false;
    }

    /**
     * Lu cnig action 
     * @returns 
     */
    public modify(...ids:string[]):boolean {
        let targetAction:Action = this._selectedAction || this._currentAction
        if(targetAction){
            let dispatcher:Dispatcher = this.getDispatcher();
            if(dispatcher && dispatcher instanceof MechanicDispatcher){
                (dispatcher as MechanicDispatcher).registerConfigAction(targetAction, ids);
                this._selectedAction = null;
                return true;
            }
        }       
        return false;
    }
    

}


