
import { log, warn, _decorator } from 'cc';
import { DEBUG } from 'cc/env';
import { Action } from '../core/Action';
import { IActionQueueDispatcher } from '../interfaces/IDispatcher';
import { Helper } from '../utils/Helper';
import { GAMIFY_LOGS } from '../utils/Setting';
import { BasicDispatcher } from './BasicDispatcher';
const { ccclass } = _decorator;

 
@ccclass('ActionQueueDispatcher')
export class ActionQueueDispatcher extends BasicDispatcher implements IActionQueueDispatcher {
    private __actionQueue: Array<Action> = [];
    
    // private __unregisterStateActionQueue: object = Object.create(null);
    
    //--------------- Override --------------

    /**
     * B sung: 
     * - Queue cc action c dispatch trong qu trnh thc hin flow trc .
     * - Queue cc action dnh ring cho mt state name cha c ng k.
     * - Kim tra v merge cc action ging nhau bn lin tip.
     * @param action 
     */
    public dispatch(action:Action){       
        // if( this.isDispatching() || (this.__actionQueue && this.__actionQueue.length) ){
        if( this.isDispatching() && this.__actionQueue  ){
            if(this.__actionQueue.length){
                let lastAction:Action = this.__actionQueue[this.__actionQueue.length-1];
                if( Helper.areEqual(lastAction, action) ) {                    
                    DEBUG && GAMIFY_LOGS && log('> MERGE ACTION: <' + action.type + '> sender: ' + action.sender + ' - receivers: ' + (action.receiver || 'All'));
                    return;
                }            
            }
            // 
            DEBUG && GAMIFY_LOGS && log('> QUEUED ACTION: <' + action.type + '> sender: ' + action.sender + ' - receivers: ' + action.receiver);
            this.__actionQueue.push(action);
        }else{            
            DEBUG && GAMIFY_LOGS && log('> DISPATCH ACTION: <' + action.type + '> sender: ' + action.sender + ' - receivers: ' + action.receiver);
            super.dispatch(action);
            // this._dispatchQueueActions();
        }
    }

    /**
     * B sung: Chy dispatch queue cc action c cached li trong qu trnh dispatch trc .
     */
    protected _stopDispatching(){
        super._stopDispatching();
        this._dispatchQueueActions();
    }

    /**
     * - Dispatch cc action queue
     * - u tin dispatch cc action queue dnh cho cc state cha kp ng k, dispatch trc cc action queue khc.
     */
    protected _dispatchQueueActions(){ 
        if(this.__actionQueue && this.__actionQueue.length){
            let action:Action = this.__actionQueue.shift();
            DEBUG && GAMIFY_LOGS && log('> GET QUEUED ACTION: ' + action.type + ' ca ' + action.sender + '---------');
            this.dispatch(action);
        }
        // 
    }

    destroy(){
        super.destroy();
        this.__actionQueue = null;
    }

}


