    static isNull(obj: any): boolean {
        return (obj == null || typeof (obj) == 'undefined');
    }
    
    /**
     * The function that compares whether two objects are equal or not is the fastest currently. Faster than lodash.isEqual
     * (Hm so snh hai object co bang nhau hay khong, ham nhanh nht hin ti. Nhanh hn lodash.isEqual )
     * 
     * @param a 
     * @param b 
     * @returns 
     */
    static areEqual(a:any , b:any):boolean{
        // So snh a, b khi  dng gi tr.
        if (a === b) return true;

        // So snh khi a, b  dng object
        if (a && b && typeof a == 'object' && typeof b == 'object') {
            // 
            if (Array.isArray(a) && Array.isArray(b)) {
                let length:number = a.length;
                if (length != b.length) return false;
                for (let i = length; i-- !== 0;){
                    if (!Helper.areEqual(a[i], b[i])) {
                        return false
                    };
                }
                return true;
            }
            // 
            if ((a instanceof Map) && (b instanceof Map)) {
                if (a.size !== b.size) return false;
                for (let i of a.entries()){
                    if (!b.has(i[0])) {
                        return false
                    };
                }
                for (let i of a.entries()){
                    if (!Helper.areEqual(i[1], b.get(i[0]))) return false;
                }
                return true;
            }
            //
            if ((a instanceof Set) && (b instanceof Set)) {
                if (a.size !== b.size){
                     return false;
                }
                for (let i of a.entries()){
                    if (!b.has(i[0])) {
                        return false;
                    }
                }
                return true;
            }
            //
            if (ArrayBuffer.isView(a) && ArrayBuffer.isView(b)) {
                let length = a.byteLength;
                if (length != b.byteLength) return false;
                for (let i = length; i-- !== 0;)
                  if (a[i] !== b[i]) return false;
                return true;
            }
            //
            if (a.valueOf && a.valueOf !== Object.prototype.valueOf && b.valueOf && b.valueOf !== Object.prototype.valueOf ) {return a.valueOf() === b.valueOf();}
            if (a.toString && a.toString !== Object.prototype.toString && b.toString && b.toString !== Object.prototype.toString) {return a.toString() === b.toString();}
            let keys:string[] = Object.keys(a);
            let length:number = keys.length;
            // S key ca b phi bng a.
            if (length !== Object.keys(b).length) return false;
            // Bt k key no ca a m b khng c th return false
            for (let i:number = length; i-- !== 0;) {
                if (!Object.prototype.hasOwnProperty.call(b, keys[i])) {
                    return false
                }
            };
            // Tip tc so snh  cc level tip theo.
            for (let i:number = length; i-- !== 0;) {
                let key = keys[i];
                if (!Helper.areEqual(a[key], b[key])) {
                    return false;
                }
            }
            return true;
        }
        // true if both NaN, false otherwise
        return a!==a && b!==b;
    }

    /**
     * Copy data with exclusion. (Sao chp data c loi tr.)
     * Note: Object references cannot be copied, object references will be converted into data with basic types: Array, Map, Set ValueType, number...
     * (Lu : Khng sao chp c vi cc tham chiu object m cc tham chiu object s b convert thnh data vi cc type c bn: Array, Map, Set ValueType, number ...)
     * @param obj // i tng sao chp
     * @param excepteds // clone nhng loi tr cc thuc tnh trong tham s ny.
     * @returns 
     */
    static clone(obj:any, excepteds?:string[]):any{        
        if(obj && typeof obj == 'object'){
            let returnObj:any;
            if (Array.isArray(obj)){
                returnObj = [];
                obj.forEach((value:any,index:number)=>{
                    returnObj[index] = Helper.clone(value);
                })
            }else if (obj instanceof Map){
                returnObj = new Map();
                obj.forEach((value:any, key:any)=>{
                    returnObj.set(key,Helper.clone(value));
                })
            }else if (obj instanceof Set){
                returnObj = new Set();
                obj.forEach((value:any)=>{
                    returnObj.set(Helper.clone(value));
                })
            }else if(obj instanceof ValueType){
                returnObj = obj.clone();
            }else{
                returnObj = {};
                const keys:string[] = Object.keys(obj);
                keys.forEach((key:string)=>{
                    if(excepteds && excepteds.indexOf(key) != -1){
                        undefined;
                    }else{
                        returnObj[key] = Helper.clone(obj[key])
                    }
                })
            }
            return returnObj;
        }else{
            return obj
        }        
    }

    /**
     * 
     * @param obj 
     * @returns 
     */
    static typeOf(obj:any):Constructor{
        if (Array.isArray(obj)) return Array;
        else if(obj instanceof Map) return Map;
        else if(obj instanceof Set) return Set;
        else if(obj instanceof ValueType) return ValueType;
        else if(CCClass._isCCClass(obj)) return js.getClassByName(js.getClassName(obj))
        else return Object;        
    }

    /**
     * Simple object merging.
     * @param toObj 
     * @param fromObj 
     * @param excepteds 
     */
    static merge(toObj:any, fromObj:any, excepteds?:string[]){
        if(fromObj && typeof fromObj == 'object'){
            const keys:string[] = Object.keys(fromObj);
            keys.forEach((key:string)=>{
                if(excepteds && excepteds.indexOf(key) != -1){
                    undefined;
                }else if(toObj[key]){
                    Helper.merge(toObj[key], fromObj[key]);
                }else{
                    toObj[key] = fromObj[key];
                }
            })
        }
    }

