    /**
     * 
     * @param value 
     * @param pattern 
     * @returns 
     */
    static getCommand(value:string, pattern:RegExp = /\{state\.(.*)}/):string{
        const searchValue:string = value.replace(/\s+/g, '').trim();
        const matchKeys:string[] = searchValue.match(pattern);
        if(matchKeys && matchKeys[1]){            
            return matchKeys[1]
        }else{
            const secondPattern:RegExp = /\{(.*)}/;
            return (pattern.source != secondPattern.source) ? this.getCommand(searchValue, secondPattern) : null
        }
        // return this.getCommand
    }

    /**
     * Phn tch mt object thnh cp gi tr (path, value). Ex: ('root.data.field', 10)
     * Sau  so snh  vi chui searchPath  tm ra gi tr cn tm.
     * @param obj 
     * @param searchPath 
     * @param currentPath // khng yu cuu dng thuc tnh ny.
     * @returns 
     */
    static searchValue(obj:any, searchPath:string, currentPath?:null|string):any{        
        if(currentPath && currentPath.indexOf(searchPath) !== -1){
            return obj;
        }else if(obj && typeof obj == 'object'){
            let searchValue:any;
            const keys:string[] = Object.keys(obj);
            // 
            let searchPathResult:string;
            keys.forEach((key:string)=>{
                const sPath:string = currentPath ? currentPath + '.' + key : key;
                const sValue:any = Helper.searchValue(obj[key], searchPath, sPath);
                // Neu chua tim duoc searchValue nao thi luu sValue luon.
                // Neu truoc do da co mot ket qua thi luu ket qua ngan hon.
                if((sValue || js.isNumber(sValue)) && ( (!searchValue) || (searchValue && searchPathResult && (sPath.length < searchPathResult.length)) ) ){
                    searchValue = sValue;                    
                    searchPathResult = sPath;
                }
            })
            //
            return searchValue;
        }        
        return null
    }

