    /**
     * 
     * @param obj 
     * @param fromValue string: path, any: value 
     * @param toPath 
     */
    static copyInside(obj:any, fromValue:any, toPath:string, modifyValueFunc?:Function){
        const value:any = (typeof fromValue == 'string') ? Helper.searchValue(obj, fromValue) : fromValue;
        const paths:string[] = toPath.split('.');
        let pathObj:any = obj;
        while((paths.length - 1) > 0){
            const key:string = paths.shift();            
            pathObj[key] = pathObj[key] || Object.create(null)
            pathObj = pathObj[key];
        }
        const key:string = paths.shift();
        pathObj[key] = modifyValueFunc ? modifyValueFunc(key, value, toPath) : value;
    }


    /**
     * 
     * @param obj 
     * @param searchPath 
     * @param value 
     */
    static updateValue(obj:any, searchPath:string, value:any){
        const refObj:ReferenceObject = this.searchReferenceObject(searchPath, obj);
        if(refObj) {
            refObj[refObj.key] = value;
        }else{
            error('Search Path cha ng => ' + searchPath);
        }
    }

    /**
     * 
     * @param searchPath 
     * @param obj  object
     * @param currentPath string
     * @returns ReferenceObject
     */
    static searchReferenceObject(searchPath:string, obj:any, currentPath?:string):ReferenceObject{
        if(currentPath && currentPath.indexOf(searchPath) != -1){
            return obj;
        }else if(obj && typeof obj == 'object'){
            let searchValue:ReferenceObject;
            const keys:string[] = Object.keys(obj);
            keys.findIndex((key:string)=>{
                const sPath:string = currentPath ? currentPath + '.' + key : key;
                const sValue:any = Helper.searchReferenceObject(searchPath, obj[key], sPath);
                if(!searchValue && sValue){
                    searchValue = Object.create(null);
                    searchValue.parent = obj;
                    searchValue.key = key;
                    searchValue.value = obj[key];
                    return true
                }
                return false
            })
            return searchValue;
        }
        return null
    }

    
    static convertToEnum(objOrArray:any):any{
        const enumDef: {[key: string]: number} = {};
        const names:string[] = Array.isArray(objOrArray) ? objOrArray : Object.keys(objOrArray);
        names.forEach((bundle:string, index:number)=>enumDef[bundle] = index)
        return Enum(enumDef)
    }

    static enumKey(EnumType:any, value:any):string{
        for (var enumMember in EnumType) {
            if(EnumType[enumMember] === value) {
                return enumMember;
            }
        }
        return '';        
    }

    static detechUnitVector(vector:Vec3):Vec3{
        const uX:number = Math.abs(vector.x) || 1;//(vector.x != 0) ? Math.abs(vector.x):1
        const uY:number = Math.abs(vector.y) || 1;//(vector.y != 0) ? Math.abs(vector.y):1
        const uZ:number = Math.abs(vector.z) || 1;//(vector.y != 0) ? Math.abs(vector.y):1
        return vector.clone().divide3f(uX, uY, uZ);
    }

    static positiveVector(vector:Vec2|Vec3):Vec2|Vec3{
        if(vector instanceof Vec2){
            return new Vec2(Math.abs(vector.x), Math.abs(vector.y));
        }else if(vector instanceof Vec3){
            return new Vec3(Math.abs(vector.x), Math.abs(vector.y), Math.abs(vector.z || 0));
        }
        return vector;
    }

