    static queryParamsToString(params:any):string{
        return params ? '?' + Object.keys(params).map( (key:string)=>( encodeURIComponent(key) + '=' + params[key] ) ).join('&') : '';
    }

    static isHttpURL(url:string):boolean{
        const pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
          '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
          '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
          '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
          '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
          '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
        return !!pattern.test(url);
    }

    static parseHttpURL(url:string):any{

    }

    /**
     * 
     * @param url 
     * @returns 
     */
    static urlToArray(url):{[n:string]:string} {
        var request = {};
        var pairs = url.substring(url.indexOf('?') + 1).split('&');
        for (var i = 0; i < pairs.length; i++) {
            if(!pairs[i])
                continue;
            var pair = pairs[i].split('=');
            request[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1]);
         }
         return request;
    }


    static arrayToURL(array:string[]):string {
        var pairs = [];
        for (var key in array)
          if (array.hasOwnProperty(key))
      
            pairs.push(encodeURIComponent(key) + '=' + encodeURIComponent(array[key]));
        return pairs.join('&');
      }

