    static getStringInsideSignal(fullString:string, signal:string = "<>"){
        if(fullString && fullString.length){
            const signalArr:string[] = signal.split('');
            const left:string = signalArr[0];
            const right:string = signalArr[1];
            const InsideSignalRegex:string = "/(?"+left+"=\\"+left+").+?(?=\\"+right+")/g;"
            const insideSignalStr:string[] = fullString.match(new RegExp(InsideSignalRegex))[0].split(/\s/g);
            // const insideTags:string[] = fullString.match(InsideTagRegex)[0].split(/\s/g);
            if(insideSignalStr && insideSignalStr.length) return insideSignalStr[0];
        }
    }
    

    static upperCaseFirstCharacter(value:string):string{
        return value.replace(/\b\w/g, c => c.toUpperCase()).replace(/(?=[A-Z])/g,' ');
    }

    /**
     * 
     * @param value 
     * @param searchHubs 
     * @returns 
     */
    static search(value:string, searchHubs:string[]| SearchingSet):string[]{
        let searchingSet:SearchingSet
        if(searchHubs instanceof SearchingSet){
            searchingSet = searchHubs;
        }else{
            searchingSet = new SearchingSet();
            searchHubs.forEach((str:string)=>{
                searchingSet.add(str);
            })
        }
        if(!searchingSet.isEmpty()){
            return searchingSet.get(value).reduce((results:string[], info:any[] )=>{
                results.push(info[1] as string);
                return results;
            }, [])
        }
        return null;
    }

    static updateEnum (targetObj:any, propName:string , newEnum:unknown):any {
        let defaultEnum = Object.assign( Enum({}) , newEnum);
        Enum['update'](defaultEnum);
        CCClass["Attr"].setClassAttr(targetObj, propName, 'type', 'Enum');
        CCClass["Attr"].setClassAttr(targetObj, propName, 'enumList', Enum["getList"](defaultEnum));
        return defaultEnum
    }

    static getClassNameFromPrefab(prefab:Prefab):string{
        const popupComp:any = prefab.data.getComponent(Component);        
        return popupComp ? js.getClassName(popupComp) : null
    }

