import { _decorator, Component, Node } from 'cc';
import { FloatButton } from '../../../../Scripts/ui/button/FloatButton';
import { SpineClip } from '../../../../Scripts/ui/anims/SpineClip';
import { EventTouch } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PlayerInboxButton')
export class PlayerInboxButton extends FloatButton {
        
    @property({ type: SpineClip })
    public get animation(): SpineClip {
        return this._animation;
    }
    public set animation(value: SpineClip) {
        this._animation = value;
    }

    // @property({serializable:true})
    private _animation: SpineClip;

    onLoad(): void {
        super.onLoad()
        if(!this.animation){
            this.animation = this.getComponent(SpineClip);
        }
    }

    async onPress(event:EventTouch){        
        await this.animation.play(1);
        // this.animation.play(0);
        super.onPress(event);        
    }

    protected onEnable(): void {
        this.animation.play(0);
    }

}


