import { _decorator, Component, Node } from 'cc';
import { GamifyScenario } from '../../gamify/GamifyScenario';
import { DEV } from 'cc/env';
import { warn } from 'cc';
import { log } from 'cc';
import { resolve } from 'path';
import { GameUtils } from '../../utils/GameUtils';
const { ccclass, property } = _decorator;



@ccclass('WebSocketComponent')
export class WebSocketComponent extends GamifyScenario {

    @property
    private isAutoReconnect:boolean = true;

    private _client:WebSocket;

    protected _url:string;

    private _forceToClose:boolean = false;

    // onInit(readyStates?: string[]): void {
    //     if(!this.runOnLoad){
    //         this.connect(this.defaultURL)
    //     }
    // }

    /**
     * 
     * @param url 
     */
    async connect(url?:string):Promise<WebSocket>{
        if(this._client && this._client.readyState == WebSocket.OPEN){            
            DEV && warn('Just support only one connection in the one Application !')
            this._client.close();
        }
        // 
        await new Promise((resolve:Function)=>{  
            // this.isAutoReconnect = true;   
            this._url = url ? url : this._url;
            this._client = new WebSocket(this._url);
            this._client.onopen = (event) => {
                log("Opened WebSocket !!!" + url);
                resolve();
            };
            this._client.onmessage = this.onMessage.bind(this, this._client);
            this._client.onerror = this.onError.bind(this, this._client)
            this._client.onclose = (event) => {
                log("WebSocket closed !!!");
                // this.retryConnection();
                if(this.isAutoReconnect && !this._forceToClose){
                    this.connect(url);
                }
                this._forceToClose = false;
            };
        })
        return this._client;
    }

    async close():Promise<WebSocket>{
        this._forceToClose = true;
        this._client.close();
        return this._client
    }

    onMessage(client:WebSocket, event:MessageEvent){        
        GameUtils.setLocalStorageItem('socket_save', JSON.stringify(event))
    }

    onError(client:WebSocket, event:Event){
        log("WebSocket error !!!");
        
    }

    /**
     * 
     * @param data 
     */
    send(data: string | ArrayBufferLike | Blob | ArrayBufferView){
        if(this._client){
            this._client.send(data)
        }
    }
}


