import { _decorator,director, UITransform, Canvas } from 'cc';
import { GamifyComponent } from '../../gamify/GamifyComponent';

const { ccclass, property } = _decorator;

export const GAME_LAYER = {
    MAIN: 25,
    PANEL: 19,
    IN_GAME_POPUP: 18,
    TOOLTIP_POPUP: 17,
    NOTICE_POPUP: 16,
    ERROR_POPUP: 15,
}

@ccclass('GamePopup')
export class GamePopup extends GamifyComponent {

    @property({
        tooltip:'Cover the entire screen.'
    })
    canCoverTheScreen:boolean = true
    
    onLoad(){
        this.runInBackground = true;
        super.onLoad()
    }
    
    onInit(readyStates?:string[]){
        // if(this.node) this.node.active = false;
        super.onInit(readyStates)
        this.resizeTransform();
    }

    
    /**
     * 
     */
    protected resizeTransform(){
        if(this.canCoverTheScreen){
            const uiTransform:UITransform = this.node.getComponent(UITransform) || this.node.addComponent(UITransform);
            const canvas:Canvas = director.getScene().getComponentInChildren(Canvas);
            if(canvas && canvas.node){
                const canvasTransform:UITransform = canvas.node.getComponent(UITransform);
                uiTransform.setContentSize(canvasTransform.contentSize);
            }
        }
    }

    
    
    

}

