
import { _decorator, Component, Node, log } from 'cc';
import { DEBUG } from 'cc/env';
import { Action } from '../core/Action';
import { IRenderQueueDispatcher } from '../interfaces/IDispatcher';
import { FunctionTask, RelativeMap, RenderTask } from '../types/GamifyType';
import { Helper } from '../utils/Helper';
import { GAMIFY_LOGS, GAMIFY_RENDER_LOGS } from '../utils/Setting';
import { RenderOrderDispatcher } from './RenderOrderDispatcher';
const { ccclass } = _decorator;

/**
 * 
 */
@ccclass('RenderQueueDispatcher')
export class RenderQueueDispatcher extends RenderOrderDispatcher implements IRenderQueueDispatcher {

    protected _nullObject:object = Object.create(null);

    private _renderQueueTasks:Function[] = [];

    protected _pauseRendering:boolean = false;

    /**
    *
    */
    constructor(id:string) {
        super(id);       
    }
    
    /**
     * 
     * @param relativeMap 
     * @param renderTasks 
     */
    protected _startRendering(relativeMap:RelativeMap, renderTasks:FunctionTask, capturedAction?:Action, pendingTasks?:Set<string>){
        
        if(renderTasks && relativeMap 
            && !Helper.areEqual(renderTasks,this._nullObject)
            && !Helper.areEqual(relativeMap,this._nullObject)){
            // 
            if(this.isRendering() || this._pauseRendering){
                // 
                if(!this._renderQueueTasks){
                    this._renderQueueTasks = [];
                }
                // 
                this._renderQueueTasks.push(super._startRendering.bind(this, relativeMap, renderTasks, capturedAction, pendingTasks));
                DEBUG && (GAMIFY_RENDER_LOGS || GAMIFY_LOGS) && log('> QUEUED RENDER :: ' + capturedAction.type);
                // 
            }else{                
                DEBUG && (GAMIFY_RENDER_LOGS || GAMIFY_LOGS) && log('> RENDERING :: ' + capturedAction.type);
                super._startRendering(relativeMap, renderTasks, capturedAction, pendingTasks)
            }
        }else{
            // this.renderQueuTasks();
            // log('There is no render for action \"' + capturedAction?.type + '\" sender: ' + capturedAction.sender + ' reciever: ' + capturedAction.receiver)
        }
    }

    /**
     * 
     * @param sharedData 
     * @param capturedAction 
     */
    protected _stopRendering(sharedData?:any, capturedAction?:Action){
        super._stopRendering(sharedData, capturedAction);
        this.renderQueuTasks();
    }

    // protected _dispatchQueueActions(){
    //     if(!this._isRendering){
    //         super._dispatchQueueActions();
    //     }
    // }

    /**
     * Tr hon vic rendering. mi task c a vo queue.
     * @param status 
     */
    public pauseRendering(status:boolean = true){
        this._pauseRendering = status;
    }

    /**
     * 
     */
    public renderQueuTasks(){
        if(this._renderQueueTasks && this._renderQueueTasks.length){
            DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('>> GET QUEUED RENDER ');
            // let task:Function = this._renderQueueTasks.shift();
            let task:Function = this._renderQueueTasks.pop();
            task();
        }else{
            // Render xong tiep tuc dispatch cac action khac.
            // this._dispatchQueueActions();
            DEBUG && (GAMIFY_LOGS || GAMIFY_RENDER_LOGS) && log('Khng c render queue.');
            // this._renderMap.forEach((renderTask:RenderTask, actionType:string, map:Map<string,RenderTask>)=>{
            //     if(renderTask.pending.size == 0){
            //         map.delete(actionType);
            //     }
            // })
        }
    }

    // public addQueueTasks(relativeMap:RenderMap, renderTasks:FunctionTask){
    //     if(!this._renderQueueTasks){
    //         this._renderQueueTasks = [];
    //     }
        
    //     this._renderQueueTasks.push(()=>{
    //         let savedRelativeMap:RenderMap = relativeMap;
    //         let savedRenderTasks:FunctionTask = renderTasks;
    //         super._startRendering(savedRelativeMap, savedRenderTasks);
    //     });
    //     DEBUG && GAMIFY_LOGS && log('> QUEUED RENDER');
    // }

}

