import { _decorator, Component, Node } from 'cc';
import { SkeletonClip } from '../../parasites/spine/SkeletonClip';
import { audio, AudioReference } from '../audio/AudioReference';
import { AudioSource } from 'cc';
import { AUDIO_LIST } from '../audio/AudioConfig';
import { Skeleton } from 'cc';
const { ccclass, property, executeInEditMode, requireComponent } = _decorator;

// Enum.isEnum()

@ccclass('SpineClip')
// @requireComponent(Skeleton)
@executeInEditMode(true)
export class SpineClip extends SkeletonClip {
    @audio(AUDIO_LIST)
    audioOfClip:AudioSource;

    protected _playlistDuration:number = 0;    

    /**
     * 
     * @param playList 
     */
    protected setPlaylist(playList:string[], isOnlyThis:boolean = false){
        super.setPlaylist(playList, isOnlyThis) 
        if(this.controller){
            if(!this._playList || (this._playList && !this._playList.length)){
                (this.controller as SpineClip).setPlaylistDuration(0);
            }else{
                (this.controller as SpineClip).setPlaylistDuration((this.controller as SpineClip).playlistDuration + this.playlistDuration);
            }
        }
        // log('----------playlist duration :: ' + this.playlistDuration)
    }

    get playlistDuration():number{
        if(!this._playlistDuration && !!this.audioOfClip && this.audioOfClip?.duration){
            this._playlistDuration = this.audioOfClip?.duration;         
        }
        return this._playlistDuration;
    }
    
    setPlaylistDuration(time:number){
        this._playlistDuration = time;
    }

    /**
     * 
     * @param isLoop 
     */
    async excute(isLoop:boolean = this.isLoop){ 
        this.audioOfClip.play();
        await super.excute(isLoop);
        this.audioOfClip.stop();
    }
}


