import { __private, js  } from "cc";
import { CCClass } from "cc";
import { warn } from "cc";

import { DEV } from "cc/env";

const CACHE_KEY = '__ccclassCache__';

// export type CCConstructor = __private._types_globals__Constructor<unknown>;

function makeSmartClassDecorator<TArg> (
    decorate: <TFunction extends Function>(constructor: TFunction, arg?: TArg) => ReturnType<ClassDecorator>,
): ClassDecorator & ((arg?: TArg) => ClassDecorator) {
    return proxyFn;
    function proxyFn(...args: Parameters<ClassDecorator>): ReturnType<ClassDecorator>;
    function proxyFn(arg?: TArg): ClassDecorator;
    function proxyFn (target?: Parameters<ClassDecorator>[0] | TArg): ReturnType<ClassDecorator> {
        try{
            if (typeof target === 'function') {
                // If no parameter specified
                return decorate(target);
            } else {
                return function <TFunction extends Function> (constructor: TFunction) {
                    return decorate(constructor, target);
                };
            }
        }catch(err){
            DEV && warn('[Error In DEV mode]'+ err);
        }
    }
}

export function importRemoteCCClass(className:string):__private._types_globals__Constructor<unknown>{
    return js.getClassByName(className)
}


/**
 * Do not declare this class to cc when a similar class with the same name already exists in the system.
 * (Khng khai bo class ny cho cc khi  tn ti class tng t v trng tn trong h thng.)
 * Priority is given to using classes in scripts of each game if they have the same name. This will not affect other games that share the same remote class
 * (u tin s dng class trong scripts ca tng game nu trng tn. Nh vy s khng nh hng ti cc game khc dng chung remote class.)
 */
export const remoteCCClass: ((name:string) => ClassDecorator) & ClassDecorator = makeSmartClassDecorator<string>((constructor, name) => {            
    if(!js.getClassByName(name)){
        let base = js.getSuper(constructor);
        if (base === Object) {
            base = null;
        }

        const proto = {
            name,
            extends: base,
            ctor: constructor,
        };
        const cache = constructor[CACHE_KEY];
        if (cache) {
            const decoratedProto = cache.proto;
            if (decoratedProto) {
                js.mixin(proto, decoratedProto);
            }
            constructor[CACHE_KEY] = undefined;
        }

        const res = CCClass(proto);
        return res
    }
});

