import { _decorator, Component, js, log, Node } from 'cc';
import ParasiteComponent from '../../gamify/master/ParasiteComponent';

const { ccclass, property } = _decorator;

@ccclass('ChainClip')
export class ChainClip<SuperComponent=Component> extends ParasiteComponent<SuperComponent> {
    
    @property({
        displayName: 'Name'
    })
    public clipName:string = ''

    protected _$sub:ChainClip<SuperComponent> = null;
    public get sub():ChainClip<SuperComponent>{
        return this._$sub;
    }

    protected _$controllerClip:ChainClip<SuperComponent> = null;
    protected get controller():ChainClip<SuperComponent>{        
        if(!this._$controllerClip){
            this._$controllerClip = this.findControllerClip();
        }
        return this._$controllerClip;
    }

    protected _clipNames:string[] = [];

    /**
     * 
     */
    protected onLoad(): void {
        super.onLoad && super.onLoad();
        if(this.super){
            // this.super.loop = false;       
            this._clipNames = [this.clipName];
            if(Object.prototype.hasOwnProperty.call(this._$super, '_$sub')){
                this.super['_$sub'] = this;
                this.registerClip(this)
            }
        }
    }

    // protected start(): void {
    //     // log('----------- ' + this._clipNames)
    // }
    // ---------------

    /**
     * 
     * @param clip 
     */
    registerClip(clip:ChainClip<SuperComponent>){        
        if(this._clipNames && this._clipNames.indexOf(clip.clipName) == -1){
            this._clipNames.push(clip.clipName)
        }
        if(this._$super && this._$super['registerClip']){            
            this.super['registerClip'](clip);
        }
    }

    /**
     * 
     */
    async excute(){
        
    }

    /**
     * 
     */
    async progress(clip:ChainClip<SuperComponent> = this.controller){
        if(!!clip){            
            if(clip.isExecutable()){
                await clip.excute();
            }
            await clip.progress(clip.nextClip());
            await clip.eachCompleted();
        }
    }

    /**
     * 
     */
    protected async eachCompleted(){
        if(this.controller == this){
            // end of progress            
            // this.player.isRunning = false;
            await this.completed();
        }
    }

    async completed(){
        
    }
    
    /**
     * 
     * @returns 
     */
    protected nextClip():ChainClip<SuperComponent>{
        return this.sub;
    }

    /**
     * 
     * @returns 
     */
    protected isExecutable():boolean{        
        return true;
    }
    
    
    // ---------------------------------------------------------------------

    /**
     * 
     * @param clip 
     * @returns 
     */
    private findControllerClip(clip:ChainClip<SuperComponent> = this):ChainClip<SuperComponent>{
        if(!clip) {
            throw new Error("Can't find player !");
        }
        const prevClip:Component|ChainClip = clip['_$super'] as Component|ChainClip
        if(!prevClip || !js.isChildClassOf(prevClip.constructor, ChainClip)){            
            return clip as ChainClip<SuperComponent>;
        }
        return this.findControllerClip(prevClip as ChainClip<SuperComponent>);
    }

    // -------------------

    /**
     * 
     */
    protected onDestroy(): void {
        this._$sub = null;
        this._$controllerClip = null;
        this._clipNames = null;        
    }

}


