import { __private } from 'cc';
import { Enum } from 'cc';
import { warn } from 'cc';
import { AudioClip } from 'cc';
import { log } from 'cc';
import { AudioSource } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { GameUtils } from '../../utils/GameUtils';
import { js } from 'cc';
import { error } from 'cc';
import { DEV, EDITOR } from 'cc/env';
import { math } from 'cc';
import { Helper } from '../../gamify/utils/Helper';

const { ccclass, property, executeInEditMode } = _decorator;

const CACHE_KEY = '__ccclassCache__';
const DEFAULT_STORE = 'Global';
const ENUM_PROPERTY_PREFIX:string = '__$enum_';
const STRING_PROPERTY_PREFIX:string = '__$string_';
const INDEX_PROPERTY_PREFIX:string = '__$number_';
const UNKNOW_AUDIO:string = 'UNKNOW_AUDIO';
const AUDIO_MAP:any = Symbol();
const AUDIO_TAG:any = Symbol();
const AUIDO_ENUM:any = Symbol();
const DEFAULT_ENUM:any = Enum({    
    [UNKNOW_AUDIO]:0
})
// const GetParasiteSuperMethod:any = Symbol();
const isAudioEnumerated:any = Symbol();
const getAudioSourceMethod:any = Symbol();

/**
 * 
 * @param constructor 
 * @param methodName 
 * @returns 
 */
function isModified(constructor:any, methodName:any):boolean{    
    const cSuper:any = js.getSuper(constructor);
    return constructor[methodName] ? true : cSuper !== Component ? isModified(cSuper, methodName) : false;
}

type Initializer = () => unknown;
type AudioSourceType = {[n:string|number]:number|string}
type IPropertyOptions = __private._cocos_core_data_decorators_property__IPropertyOptions;
type PropertyType = __private._cocos_core_data_decorators_property__PropertyType;
type LegacyPropertyDecorator = __private._cocos_core_data_decorators_utils__LegacyPropertyDecorator;
type BabelPropertyDecoratorDescriptor = PropertyDescriptor & { initializer?: Initializer };

// 

/**
 * '@audio'             Developer can choose a Sound in all of AudioReference exist in Scene at that time. Using default AudioReference.Enum 
 * '@audio(String)'     The developer defines a key of a specific AudioReference. Sound could not be played if this AudioReference is not exist.
 * '@audio(Enum)'       Developer can choose a Sound in Enum list.
 * @param options 
 */
export function audio(options?: IPropertyOptions|string|unknown): LegacyPropertyDecorator;
export function audio(type?: PropertyType): LegacyPropertyDecorator;
export function audio(...args: Parameters<LegacyPropertyDecorator>): void;
export function audio(
    target?: Parameters<LegacyPropertyDecorator>[0]| PropertyType, 
    propertyKey?: Parameters<LegacyPropertyDecorator>[1], 
    descriptorOrInitializer?: BabelPropertyDecoratorDescriptor
){
    let options: string|IPropertyOptions | PropertyType | null | unknown = null;    
    function normalized (target: Parameters<LegacyPropertyDecorator>[0],
        propertyKey: Parameters<LegacyPropertyDecorator>[1],
        descriptorOrInitializer:  BabelPropertyDecoratorDescriptor)
    {
        // ----------------
        const constructor:any = target.constructor;  
        const propertyName:string = propertyKey.toString();
        const enumPropertyName:any = ENUM_PROPERTY_PREFIX + propertyName;
        const indexEnumPropertyName:any = INDEX_PROPERTY_PREFIX + propertyName;
        const stringPropertyName:any = STRING_PROPERTY_PREFIX + propertyName;
        //        
        const classStash:unknown = constructor[CACHE_KEY] || ((constructor[CACHE_KEY]) = {});
        const ccclassProto:unknown = classStash['proto'] || ((classStash['proto'])={});
        const properties:unknown = ccclassProto['properties'] || ((ccclassProto['properties'])={});
        const enumPropertyStash:unknown = properties[enumPropertyName] ??= {};        
        const indexEnumPropertyStash:unknown = properties[indexEnumPropertyName] ??= {};        
        const stringPropertyStash:unknown = properties[stringPropertyName] ??= {};
        let AudioEnum:unknown = AudioReference.ENUM;
        // 
        const indexEnumRecordOptions:any = {
            serializable:true,       
            visible:false
        }
        const stringEnumRecordOptions:any = {
            serializable:true,
            visible:false
        }

        const enumPropertyRecordOptions:any = {
            type:AudioEnum,
            group:'AUDIO',
            // serializable:true,       
            displayName: ''+ propertyName.replace(/\b\w/g, c => c.toUpperCase()).replace(/(?=[A-Z])/g,' ')+'',  // upper first character
            // visible:true
        }
        
        const enumPropertyRecord:any = js.mixin(enumPropertyStash, enumPropertyRecordOptions);
        const indexEnumPropertyRecord:any = js.mixin(indexEnumPropertyStash, indexEnumRecordOptions);
        const stringPropertyRecord:any = js.mixin(stringPropertyStash, stringEnumRecordOptions);
        // 
        if(options){
            if(js.isString(options)){
                stringPropertyRecord.default = options;
                stringPropertyRecord.displayName = ''+ Helper.upperCaseFirstCharacter(propertyName) +'';  // upper first character
                stringPropertyRecord.visible = true;
                stringPropertyRecord.readonly = true;
                stringPropertyRecord.group = 'AUDIO';
                stringPropertyRecord.tooltip = '"' + options.toString() + '" key is fixed in your code';
                // Display string property
                Object.defineProperty(target, stringPropertyName, {
                    get:function():string{
                        return options as string;
                    },
                    set:function(val:string){           
                        this[indexEnumPropertyName] = -1;
                    }
                })
                // 
            }else if(Enum.isEnum(options)){
                AudioEnum = Enum(options)
                enumPropertyRecord.visible = true;
                enumPropertyRecord.type = AudioEnum;                
                // Display enum property
                Object.defineProperty(target, enumPropertyName, {
                    get:function():number{                
                        return !this[indexEnumPropertyName] || this[indexEnumPropertyName] == -1 ? AudioEnum[this[stringPropertyName]] :this[indexEnumPropertyName];
                    },
                    set:function(val:number){           
                        this[indexEnumPropertyName] = val;     
                        if(val !== AudioEnum[UNKNOW_AUDIO]){                   
                            this[stringPropertyName] = AudioEnum[val] as string;
                        }else{
                            GameUtils.updateEnum(this, enumPropertyName,  AudioEnum);
                        }
                    }
                });
            }
        }else{
            enumPropertyRecord.visible = true
            // enumPropertyRecord.displayName = ''+ propertyName.replace(/\b\w/g, c => c.toUpperCase()).replace(/(?=[A-Z])/g,' ')+'';  // upper first character
            enumPropertyRecord.displayName = '' + Helper.upperCaseFirstCharacter(propertyName) + '';  // upper first character
            // Display enum property
            Object.defineProperty(target, enumPropertyName, {
                get:function():number{                
                    return !this[indexEnumPropertyName] || this[indexEnumPropertyName] == -1 ? AudioReference.ENUM[this[stringPropertyName]] :this[indexEnumPropertyName];
                },
                set:function(val:number){           
                    this[indexEnumPropertyName] = val;     
                    if(val !== AudioReference.ENUM[UNKNOW_AUDIO]){                   
                        this[stringPropertyName] = AudioReference.ENUM[val] as string;
                    }else{
                        GameUtils.updateEnum(this, enumPropertyName,  AudioReference.ENUM);
                    }
                }
            });
            // 
        }
        // 
        const audioPropertyTags:string[] = constructor[AUDIO_TAG] || ((constructor[AUDIO_TAG]) = []);
        (audioPropertyTags.indexOf(propertyName) == -1) && audioPropertyTags.push(propertyName); 
        // 
        constructor['_executeInEditMode'] = true;
        //         
        // if(!target[isAudioEnumerated]){
        if(!isModified(constructor, isAudioEnumerated)){
            constructor[isAudioEnumerated] = true;
            // If you overrided onLoad function here, I couldn't modify onLoad function for converting cccomponent to gamify component which created by @gamify decorator. 
            // Because, We are working with initialized instance. It's not a instance which game would be used.
            // So, we will override __preload function to prepare for overriding onLoad. You can get effect that you wanna.
            // Because, when the __preload function is called, the onLoad function was initianized completely.
            // Summary, we are working on constructing phase. Do not runtime phase.
            // ----------------------------------------------------------------            
            target['__preload'] = ((lastPreLoad:Function)=>{
                // 
                // this.constructor[isAudioEnumerated] = true;
                return function(...args:any){
                    // 
                    this[getAudioSourceMethod] = function(stringProperty:string):AudioSource{
                        // const classOption:any = AudioSource;
                        const defaultAudioName:string = this[stringProperty];
                        let audioSource:AudioReference = AudioReference.getAudio(defaultAudioName);
                        if(audioSource && !audioSource.disallowMultiple){
                            const comps:AudioSource[] = this.node?.getComponents(AudioSource);
                            let foundId:number = comps.length;
                            let secondAudioSource:AudioSource = comps.find((searchAudioSource: AudioSource, index:number)=>{
                                if (searchAudioSource.name == defaultAudioName){
                                    foundId = index;
                                    return true;
                                }else{
                                    return false
                                }                            
                            });
                            // if(this.node && audioSource && audioSource.playing && !audioSource.loop){
                            // if(audioSource && this.node && !audioSource.loop){
                            if(!secondAudioSource) {
                                secondAudioSource = this.node.addComponent(AudioSource);
                                secondAudioSource.name = defaultAudioName;
                                secondAudioSource.loop = audioSource.loop;
                                secondAudioSource.volume = audioSource.volume;
                                secondAudioSource.playOnAwake = false;
                                secondAudioSource.clip = audioSource.clip;
                                secondAudioSource.onDestroy = (function(onDestroy:Function){                                    
                                    return function(){                                        
                                        audioSource.delRef(this);
                                        onDestroy && onDestroy.call(secondAudioSource);
                                    }
                                })(secondAudioSource.onDestroy)
                                audioSource.addRef(secondAudioSource);
                            }
                            
                            // }
                            return  secondAudioSource;
                        }else{
                            return audioSource
                        }
                    }
                    // 
                    this.onLoad = ((prevOnLoad:Function)=>{
                        return function(){                            
                            // Change or not the enum of Audio list.
                            AudioEnum = !!options && Enum.isEnum(options) ? AudioEnum : AudioReference.ENUM ;
                            const audioPropertyModifyTags:string[] = this.constructor[AUDIO_TAG];
                            audioPropertyModifyTags.forEach((propertyTagName:string)=>{                                
                                const enumPropertyName:string = ENUM_PROPERTY_PREFIX + propertyTagName;
                                const stringPropertyName:any = STRING_PROPERTY_PREFIX + propertyTagName;
                                const indexEnumPropertyName:any = INDEX_PROPERTY_PREFIX + propertyTagName;
                                // 
                                this[enumPropertyName] = this[indexEnumPropertyName];
                                if(EDITOR){
                                    // Making enum options for Audio.
                                    GameUtils.updateEnum(this, enumPropertyName,  AudioEnum);
                                }
                                // Detech unexist audio and change value of enumPropertyName
                                if(this[stringPropertyName] && (this[stringPropertyName] as string).length){
                                    this[enumPropertyName] = !AudioReference.hasAudio(this[stringPropertyName]) ? AudioEnum[UNKNOW_AUDIO] : AudioEnum[this[stringPropertyName]];
                                }
                                // 
                                if( !this[propertyTagName] || !(this[propertyTagName] instanceof Proxy)){ 
                                    const defaultFunction:Function = function():null{
                                                                        EDITOR && warn('There is no audio for the \"' + js.getClassName(constructor) + '.' + propertyTagName + '\" property ! ' + target[stringPropertyName]); 
                                                                        return null
                                                                    };
                                    const methodProxy:ProxyConstructor = new Proxy(this, {                                        
                                        get:function(target:any, prop:string){
                                            const audioSource:AudioSource = target[getAudioSourceMethod](stringPropertyName);
                                            const audioDesc:PropertyDescriptor = js.getPropertyDescriptor(audioSource, prop);
                                            if(!audioSource || !audioDesc) return defaultFunction;
                                            if(!audioDesc.get && typeof audioDesc.value == 'function' ){
                                                return audioDesc.value.bind(audioSource);
                                            }else{
                                                return audioSource[prop];
                                            }
                                        },
                                        set: (target:any, prop:string, value:any) =>{                                            
                                            const audioSource:AudioSource = target[getAudioSourceMethod](stringPropertyName);
                                            const audioDesc:PropertyDescriptor = js.getPropertyDescriptor(audioSource, prop)
                                            if(audioSource && !!audioDesc && !!audioDesc.set){
                                                audioDesc.set.call(audioSource, value);
                                                return true
                                            }
                                            return false
                                        }
                                    })                    
                                    !EDITOR && Object.defineProperty(this, propertyTagName, {
                                        get:()=>methodProxy                                            
                                    })                  
                                }
                            });
                            prevOnLoad && prevOnLoad.call(this, ...args );                    
                        }
                    })(this.onLoad);
                    
                    // 
                    this.onDestroy = ((prevOnDestroy:Function)=>{
                        return function(){           
                            prevOnDestroy && prevOnDestroy.call(this);                            
                            const audioPropertyModifyTags:string[] = this.constructor[AUDIO_TAG];
                            audioPropertyModifyTags.forEach((propertyTagName:string)=>{                            
                                if(!EDITOR){
                                    delete this[propertyTagName];
                                }                                 
                            })
                        }
                    })(this.onDestroy)
                    // 
                    lastPreLoad && lastPreLoad.call(this, ...args );
                }            
            })(target['__preload'])
        }
        // ----------------------------------------------------------------
    }

    if (target === undefined) {
        // @audio() => LegacyPropertyDecorator
        return audio({
            type: AudioSource,
        });
    } else if (typeof propertyKey === 'undefined') {
        options = target;
        return normalized;
    } else {
        // @audio
        normalized(target as Parameters<LegacyPropertyDecorator>[0], propertyKey, descriptorOrInitializer);
        return undefined;
    }
}




@ccclass('AudioReference')
@executeInEditMode(true)
export class AudioReference extends AudioSource {
                
    static get ENUM():AudioSourceType {
        return this[AUIDO_ENUM] || DEFAULT_ENUM;
    };

    /**
     * 
     * @param audio 
     */
    public static registerAudio(audio:AudioReference){
        if(audio && audio.node){
            const audioMap:Map<string,AudioReference> = (this[AUDIO_MAP]) ??= new Map<string, AudioReference>();
            // const audioNodePath:string = audio.node.getPathInHierarchy();
            const clip:AudioClip = audio.clip;
            if(!clip) {
                DEV && warn('[AudioReference.registerAudio] Khng tm thy AudioClip trong AudioReference gn cho node \"' + audio.node.getPathInHierarchy() + '\"')
                return;
            }
            const clipName:string = audio.key || clip.name;
            if(audioMap.has(clipName)) {
                DEV && warn('[AudioReference.registerAudio] C file trng tn  ng k trc  ' + clip.name);
            } else {
                audioMap.set(clipName, audio);
                // 
                // let index:number = 0
                // const enumAudio: {[key: string]: number} = {};
                // audioMap.forEach((value:AudioReference, key:string)=>{
                //     enumAudio[key] = index++
                // })
                // enumAudio[UNKNOW_AUDIO] = index;
                this[AUIDO_ENUM] = this.reduceEnums(audioMap);
            }
        }else{
            error('[AudioReference.registerAudio] ng k ' + js.getClassName(this) + ' li !. Li do ng k mt Audio cha c gn vo node.');
        }
    }

    

    /**
     * 
     * @param audio 
     */
    public static removeAudio(key:string):boolean{
        const audioMap:Map<string,AudioReference> =(this[AUDIO_MAP]) ??= new Map<string, AudioReference>();
        if(audioMap && audioMap.size){
            const clipName:string = key;
            if(clipName && !audioMap.has(clipName)) {
                // EDITOR && warn('[delRef] Khng tm thy Audio ' + clipName + 'trong danh sch ');
                return false;
            } else if(clipName) {
                audioMap.delete(clipName);
                this[AUIDO_ENUM] = this.reduceEnums(audioMap);
                return true
            }

        }else{
            // EDITOR && warn('[delRef] G ng k ' + js.getClassName(this) + ' li !');             
        }
        return false
    }

    /**
     * Edit here
     * @param key 
     * @returns 
     */
    public static getAudio(key:string):AudioReference{
        const audioMap:Map<string, AudioReference> = this[AUDIO_MAP];
        if(audioMap && audioMap.size){
            return audioMap.get(key)
        }
        return null;
    }
    
    /**
     * 
     * @param key 
     * @returns 
     */
    public static hasAudio(key:string):boolean{
        const audioMap:Map<string, AudioReference> = this[AUDIO_MAP];
        if(audioMap && audioMap.size){
            return audioMap.has(key)
        }
        return false;
    }

    private static reduceEnums(audioMap:Map<string,AudioReference>):any{
        let index:number = 0
        const enumAudio: {[key: string]: number} = {};
        audioMap.forEach((value:AudioReference, key:string)=>{
            enumAudio[key] = index++
        })
        enumAudio[UNKNOW_AUDIO] = index;
        return Enum(enumAudio); 
    }

    @property({displayOrder:0})
    get key():string{
        if(!this._key && this.clip){
            this.setKey(this.clip.name)
        }
        return this._key
    }


    /**
     * 
     * @param key 
     */
    setKey(key:string){
        const newKey:string = key.replace(/_[0-9][0-9]/g,'');
        this._key && AudioReference.removeAudio(this._key);
        this._key = newKey;
        AudioReference.registerAudio(this);
    }
    // @property({override:true, visible:false})
    // set loop(val: boolean) {
    //     super.loop = val;
    // }
    // get loop(): boolean {
    //     return super.loop
    // }

    // @property({override:true, visible:false})
    // set playOnAwake(val: boolean) {
    //     super.playOnAwake = val
    // }
    // get playOnAwake(): boolean {
    //     return super.playOnAwake
    // }

    @property({
        override:true, 
        slide:true,
        step:0.01
        // visible:false
    })
    set volume(val: number) {        
        // 
        if (Number.isNaN(val)) { console.warn('illegal audio volume!'); return; }
        val = math.clamp(val, 0, 1);
        if (this._player) {
            this._player.volume = val;
            this._volume = this._player.volume;
        } else {
            this._volume = val;
        }

        if(EDITOR) {
            this._savedVolume = this._volume;
        };
        
        // super['_volume'] = val;
        this._refMap && this._refMap.forEach((audio:AudioSource, key:string)=>{
            if(audio !== this) { audio.volume = val; }
        })
    }
    get volume(): number {
        return this._volume || 1
    }

    @property({
        override:true,
        serializable:true
    })
    get clip(): AudioClip {        
        return this._clip
    }

    set clip(audioClip: AudioClip) {
        if (audioClip === this._clip) {
            return;
        }
        super.clip = audioClip;
        if(audioClip){
            this.setKey(audioClip.name)
        }
        // 
    }

    // @property({
    //     serializable:false,
    //     visible:false
    // })    

    @property({ serializable: true, visible: false })    
    protected get savedVolume(): number {
        return this._savedVolume;
    }

    @property({ serializable: true, visible: false })
    private _key:string = null;

    private _store:string = DEFAULT_STORE;

    private _savedVolume: number = 1;

    private _refMap:Map<string, AudioSource>;

    @property({ 
        serializable: true,
        displayName:'Disallow Multiple Channels',
        tooltip:'If this field is true, there is only one AudioSource instance of component in the system. The background music is used this field.'
    })    
    disallowMultiple:boolean = false;
    // isPersitent
    
    onLoad(): void {
        this._refMap = new Map();
        this.playOnAwake = false;
        super.onLoad();
        this.clip && this.setKey(this.clip.name);
    }

    // onEnable(): void {
    //     // this.play();
    // }

    // onDisable(): void {
    //     this.pause();
    // }

    onDestroy(): void {        
        this.clip && AudioReference.removeAudio(this.key);
    }

    public resetDefaultVolume(){
        this.volume = this.savedVolume;
    }

    public addRef(audioSource:AudioSource){
        // log('add ref :: ' + audioSource.name + ' uuid:: ' + audioSource.uuid)
        this._refMap.set(audioSource.uuid, audioSource);
    }
    
    public delRef(audioSource:AudioSource){
        // log('del ref :: ' + audioSource.name)
        this._refMap.delete(audioSource.uuid);
    }

    // play(): void {
        
    // }

}


