
import { _decorator, warn, error, log } from 'cc';
import { DEBUG, DEV } from 'cc/env';
import { Action } from '../core/Action';
import { Dispatcher } from '../core/Dispatcher';
import { IBaseDispatcher } from '../interfaces/IDispatcher';
import { GAMIFY_LOGS, GAMIFY_WARNNING } from '../utils/Setting';
const { ccclass } = _decorator;

@ccclass('BasicDispatcher')
export class BasicDispatcher extends Dispatcher implements IBaseDispatcher {
    // 
    protected _tokenMap: Map<string, string> = null;
    protected _stateNameMapKeys: string[] = null;
    // protected _stateNameSearching: SearchingSet;
    protected _penddingId:string = null;
    protected _callBackMap:Map<string, Function[]> = null;
    /**
     *
     */
    constructor(id:string) {
        super(id);
        this._tokenMap = new Map();
        this._callBackMap = new Map();
        this._stateNameMapKeys = [];
        // this._stateNameMapKeys = new SearchingSet();
    }


    //------------------- Feature ------------------

    /**
     * Force stop data flow. (Cng p dng lung d liu.)
     * The mechanism is to ignore callbacks called after this function calls. (C ch l b qua cc callback c gi sau khi hm ny gi.)
     * Note: The function only works when running a data stream or isDispatching == true. (Lu : Hm ch c tc dng khi ang chy lung d liu hay isDispatching == true.)
     */
    public skipInvoking(){
        if(this.isDispatching()){
            let tokenIds:string[] = Object.keys(this._isPending);
            tokenIds.forEach((tokenId:string)=>{
                this._isPending[tokenId] = true;
                this._isHandled[tokenId] = true;
            })
        }
    }
    
    /**
     * 
     * @param stateName 
     * @param dispatchToken 
     */
    public mapToken(stateName:string, dispatchToken:string):string{
        // 
        let validateStateName:string = this.__getUniqueStateName(stateName);
        this._tokenMap.set(validateStateName, dispatchToken);
        this._tokenMap.set(dispatchToken, validateStateName);
        this._stateNameMapKeys.push(validateStateName);
        // this._stateNameSearching = null;
        
        // this._stateNameMapKeys.add(validateStateName);
        DEBUG && GAMIFY_LOGS && log('> MAPPING NAME: ' + validateStateName + ' - token: ' + dispatchToken);
        return validateStateName;
    }

    /**
     * 
     * @param stateName 
     * @param dispatchToken 
     */
    public unmapToken(stateName:string, dispatchToken:string){
        let validateStateName:string = this.searchRegisterName(stateName)
        if(this._tokenMap.get(validateStateName) == dispatchToken){
            this._tokenMap.delete(validateStateName);
            this._tokenMap.delete(dispatchToken);
            // 
            // this._stateNameMapKeys.delete(validateStateName);
            this._stateNameMapKeys = this._stateNameMapKeys.filter((value:string)=>{
                return !(value == validateStateName);
            })
            // this._stateNameSearching = null;
        }else{
            if(DEBUG) { 
                error(this.__ccClassName + '.unmapToken('+ validateStateName +', '+ dispatchToken +'): State Name v Dispatch Token khng phi l mt cp.')
            }
        }
    }


    /**
     * Truyn vo state name tr v token id.
     * Truyn vo token id tr v state name ng k trn h thng.
     * @param id            // Token id hoc state name
     * @param instanceIndex     // 
     * @returns 
     */
    public findMapName(id:string, instanceIndex?:number):string{
        let _foundId:string = id;
        if(_foundId && !this._isTokenId(_foundId)){
            // Truong hop id truyen va la store name
            _foundId = _foundId + (instanceIndex ? '.' + instanceIndex : '' );
            _foundId = this.searchRegisterName(_foundId);
        }else{
            // Truong hop truyen vao la token id
            if(!this.hasRegister(_foundId)){
                return null;
            }
        }
        return this._tokenMap.get(_foundId);
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    public searchRegisterName(stateName:string):string{
        return this.searchAllRegisterNames(stateName)[0];
    }

    /**
     * 
     * @returns 
     */
    public getAllRegistedStates():string[]{
        return Array.from(this._stateNameMapKeys)
    }

    /**
     * 
     * @param stateName 
     * @returns [ [score, fullstatename], ... ]
     */
    // public searchMatchingNames(stateName:string):any[]{
    //     if(!this._stateNameSearching){
    //         this._stateNameSearching = new SearchingSet();
    //         this._stateNameMapKeys.forEach((mapKey:string)=>{
    //             this._stateNameSearching.add(mapKey);
    //         })
    //     }
    //     return this._stateNameSearching.get(stateName);
    // }

    /**
     * Tm kim u tin kim tra cng class trc.
     * @param stateName 
     * @returns 
     */
    public searchAllRegisterNames(stateName:string):string[]{
        if(!stateName) return [];
        return this._stateNameMapKeys.reduce((foundKeys:string[], fullStateName:string)=>{
            if(stateName && stateName.length < fullStateName.length){
                const parsingNames:string[] = fullStateName.split(/\<|\>/)
                if((parsingNames.indexOf(stateName) != -1) || (parsingNames[0].indexOf(stateName) != -1)){
                    foundKeys.push(fullStateName);            
                } else if(DEV && fullStateName.toLowerCase().indexOf(stateName.toLowerCase()) != -1 && (fullStateName.length == stateName.length)){
                    warn('The two state names ' + fullStateName + ' and ' + stateName + ' found that the class is not exactly the same. Please enter the correct name.');
                    warn('(Hai state name ' + fullStateName + ' va ' + stateName + ' tim duoc nhung co class ko hoan toan giong nhau. Luu y nhap ten chinh xac.)');
                }
            }else if(stateName && stateName == fullStateName){
                foundKeys.push(fullStateName);
            }else if(fullStateName.indexOf(stateName) != -1 && (fullStateName.length == stateName.length)){
                warn('The two state names ' + fullStateName + ' and ' + stateName + ' found but are not exactly the same. Please enter the correct names.')
                warn('(Hai state name ' + fullStateName + ' va ' + stateName + ' tim duoc nhung ko hoan toan giong nhau. Luu y nhap ten chinh xac.)')
                foundKeys.push(fullStateName);
            }
            return foundKeys;
        }, []);
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    // public searchAllRegisterNames(stateName:string):string[]{
    //     if(!this._stateNameSearching){
    //         this._stateNameSearching =
    //     }

    //     const results:any[] = this._stateNameMapKeys.get(stateName);
    //     return results.reduce( (exportStrs:string[], result:any[])=>{
    //         const fullStateName:string = result[1] as string;
    //         exportStrs.push(fullStateName);
    //         return exportStrs
    //     }, []);
    // }

    
    /**
     * Make sure the output is a token id, including converting the ids parameter from the store name to the corresponding token id if necessary.
     * (m bo u ra l token id, bao gm c vic convert tham s ids t dng store name thnh token id tng ng nu cn.)
     * @param ids 
     * @returns 
     */
    protected _validateIds(ids:Array<string>):Array<string>{
        return ids.reduce((mapList:Array<string>, id:string)=>{
            let validateId:string = this.findMapName(id);
            if(validateId && !this._isTokenId(validateId)){
                validateId = this.findMapName(id);
            }
            //
            return mapList.concat(validateId || [] )
        },[]);
        // return ids.reduce((mapList:Array<string>, id:string)=>mapList.concat(this.findMapName(id) || [] ),[]);
    }

    /**
     * Check if the passed id is a token id.
     * (Kim tra id truyn vo c phi l token id khng)
     * @param id 
     * @returns 
     */
    protected _isTokenId(id:string):boolean {
        return (id.indexOf(this._prefix) != -1);
    }

    /**
     * 
     * @param actionType 
     */
    protected getCallBackTask(actionType:string):Function[] {
        let dataTasks:Function[] = this._callBackMap.get(actionType);
        if(!dataTasks){           
            dataTasks = [];
            this._callBackMap.set(actionType, dataTasks);
        }
        return dataTasks;
    }

    /**
     * Automatically create a new name when the old name already exists in __tokenMap. Make sure the state name in the map is unique. The function called in mapToken(...)
     * (T to ra tn mi khi tn c  tn ti trong __tokenMap. m bo state name vo map l unique. Hm c gi trong mapToken(...))
     * @param stateName 
     * @param startNumber 
     * @returns 
     */
    private __getUniqueStateName(stateName:string, startNumber:number = 1):string {        
        if(this._tokenMap.has(stateName)) {                      
            return this.__getUniqueStateName(stateName + '.' + startNumber, ++startNumber)
        }else if( DEBUG && GAMIFY_WARNNING ){
            (startNumber > 1) ? warn(this.__ccClassName + '.mapToken(...) :: State name already exists in map list. The system automatically creates a new name ' + stateName) : undefined; 
            (startNumber > 1) ? warn(this.__ccClassName + '.mapToken(...) :: State name  tn ti trong map list. H thng t ng to tn mi l ' + stateName) : undefined;
        }
        return stateName;
    }

    

    //----------- Override -------------

    /**
     * 
     * @param ids String of full state name or token id or store/class name
     */
    public waitFor(ids:Array<string>) {
        super.waitFor(this._validateIds(ids));
    }
    

    protected _invokeCallback(id:string) {
        this._penddingId = id;
        super._invokeCallback(id)
    }
    
    /**
     * 
     * @param id 
     */
    protected _excuteCallback(id:string){
        if(this._pendingAction){
            const actionType:string = (this._pendingAction as Action).type;
            const tasks:Function[] = this.getCallBackTask(actionType);
            const selectedFuntion:Function = this._callbacks[id];
            tasks.push(selectedFuntion.bind(this, this._pendingAction))
            this._callBackMap.set(actionType, tasks);
        }else{
            throw new Error('Deleted Action !')
        }
        // this._callbacks[id](this._pendingAction);
    }

    /**
     * Clear bookkeeping used for dispatching.
     * 
     * @internal
     */
    protected _stopDispatching() {
        const actionType:string = (this._pendingAction as Action).type;
        const tasks:Function[] = this.getCallBackTask(actionType);
        while(tasks.length){
            tasks.shift().call(null);
        }
        super._stopDispatching();
        this._callBackMap.delete(actionType);
    }

    destroy(){
        this._tokenMap = null;
        this._callBackMap = null;
        super.destroy();
    }
    

}

