
import { _decorator, Component, warn, js, CCClass } from 'cc';
import { DEBUG, EDITOR } from 'cc/env';
import { IGamifyDispatcher } from '../interfaces/IDispatcher';
import { GAMIFY_WARNNING } from '../utils/Setting';
import { MechanicDispatcher } from './MechanicDispatcher';
import { Helper } from '../utils/Helper';
const { ccclass } = _decorator;


/**
 * 
 * Author: Hallopatidu@gmail.com
 */ 
@ccclass('GamifyDispatcher')
export class GamifyDispatcher extends MechanicDispatcher implements IGamifyDispatcher {

    private static _enum_:any    

    static get STORE_ENUM(){
        return this._enum_
    }

    static _instance: GamifyDispatcher;
        
    static get instance():GamifyDispatcher{
        if(!this._instance){
            this._instance = new GamifyDispatcher('global');
        }
        return this._instance;
    }


    
    /**
     *
     */
    constructor(id:string) {
        super(id);        
    }

    public static clearInstance(){        
        this._instance = null;
    }


    /**
     * 
     * @param stateName 
     * @param dispatchToken 
     * @returns 
     */
    public mapToken(stateName:string, dispatchToken:string):string{
        const fullStateName:string = super.mapToken(stateName, dispatchToken);
        if(EDITOR) GamifyDispatcher._enum_ = Helper.convertToEnum(this._stateNameMapKeys);
        return fullStateName;
    }

    public unmapToken(stateName:string, dispatchToken:string){
        super.unmapToken(stateName, dispatchToken)
        if(EDITOR) GamifyDispatcher._enum_ = Helper.convertToEnum(this._stateNameMapKeys);
    }

   
    destroy(){      
        super.destroy();     
        let dispatcherClass:any = js.getClassByName(this.__ccClassName);
        if(dispatcherClass.instance && dispatcherClass.instance == this){
            DEBUG && GAMIFY_WARNNING && warn('Xa hon ton dispatcher ' + this.id + ' k c trong ' + this.__ccClassName + '.instance .');
            dispatcherClass.clearInstance();
        }
    }
}

