
import { _decorator, Component, Node, error, warn, log, js } from 'cc';
import { Action, ActionObject, ActionType } from './core/Action';
import { Dispatcher } from './core/Dispatcher';
import { Reducer } from './core/Reducer';
import { BasicDispatcher } from './dispatchers/BasicDispatcher';
import { GamifyDispatcher } from './dispatchers/GamifyDispatcher';
import { IGamifyStore } from './interfaces/IStore';
import { Helper } from './utils/Helper';
import { GAMIFY_LOGS, GAMIFY_WARNNING } from './utils/Setting';
import { DEBUG, DEV, EDITOR } from 'cc/env';
import { ActionStoreDispatcher } from './dispatchers/ActionStoreDispatcher';
import { find } from 'cc';
// 
const { ccclass } = _decorator;
const ClassTagRegex:RegExp = /(?<=\<).+?(?=\>)/g;
const OrderTagRegex:RegExp = /(?<=\[).+?(?=\])/g;
// 
export const STORE_ACTION = {
    NEW_STATE_REGISTED:'store_action_new_state_registed'
}
/**
 * - This is a kind of Stateless Component. Basically, not directly affected by surrounding components.
 *   (L mt dng Stateless Component. i khi l Khng chu tc ng trc tip bi cc component xung quanh.)
 * - Create a new lifecycle. (To mt lifecycle mi.)
 * - Connect stores each other. (Ket noi cac store.) 
 * 
 * Author: Hallopatidu@gmail.com
 */
@ccclass('GamifyStore')
export class GamifyStore extends Reducer implements IGamifyStore{
        
    protected _currentAction:Action = null;
    protected _stateName:string;

    protected _rollCallCached:Map<string, string[]>;
    
    //---------- new Life Circle -----
        
    /**
     * Initialize default state. (Khi to state mc nh.)
     */
    protected get initialState():any {
        return {};
    }

    protected initialized(readyStates?:string[]){
        this.onInit(readyStates);
        this.dispatch({type:STORE_ACTION.NEW_STATE_REGISTED});
    }

    /**
     * 
     * @param stateName 
     * @param state 
     */
    onStoreRegisted(state:any, action:Action):any{
        
    }

    /**
     * 
     */
    onInit(readyStates?:string[]){ 
        
    }

    /**
     * Process the state as soon as an action is sent. (X l state ngay khi c mt action gi ti.)
     * The function only runs with actions allowed in allowActionTypes. (Hm ch chy vi cc action c cho php trong allowActionTypes.s)
     * 
     * @param state 
     * @param action 
     */
    onData(state:any, action:Action):any|void{
        return state;
    }
   

    /**
     * 
     */
    onRemove(){
        
    }

    /**
     * The function is called when an action is encountered that is not allowed to be processed. (Hm c gi vo khi gp mt action khng c php x l.)
     * @param action 
     */
    onRejectedAction(action:Action){

    }
    
    //------------ Feature ---------------

    /**
     * Limit the action types called to the component's reduce method. (Gii hn cc action type c gi vo reduce ca component.)
     * Store only receives actions contained in the array. If the array is empty, Store will receive all incoming actions. (Store ch nhn cc action c trong mng, nu mng rng, Store s nhn tt c cc action n.)
     * @returns string[]
     */
    public allowActionTypes():string[]{
        return []
    }

    /**
     * 
     * @param action 
     * @param receiver 
     */
    public dispatch(action:Action, ...receiver:string[]){
        // (DEBUG || DEV || EDITOR) && GAMIFY_LOGS && log('--------- Dispatch:: ' + action.type + ' ---------');
        action.sender = this.getStateName();
        action.receiver = receiver || action.receiver || [];
        if(!action.stores){
            action.stores = Object.create(null);
        }
        action.stores[this.getStateName()] = this.getState() || Object.create(null);
        this.getDispatcher().dispatch(action);
    }

    /**
     * Immediately blocks the data flow. (Chn ngay lp tc lung d liu.)
     * Note: (Lu :)
     * - The function only works in onData or onParentData (by GamifyMechanic). (Hm ch c tc dng trong onData hoc onParentData (ca GamifyMechanic).)
     * - Be careful when using because if calling references to state data causes other Stores to call before Gamify Mechainic, there may be a loop error. (Cn thn khi s dng v nu gi cc tham chiu n state data khin cc Store khc gi trc Gamify Mechainic th c th s c bo li vng lp.)
     */
    protected preventDispatching(){
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof BasicDispatcher){
            (DEBUG || DEV || EDITOR) && GAMIFY_WARNNING && warn(this.__ccClassName + '.preventDispatching():  chn lung action ');
            dispatcher.skipInvoking();
        }
    }

    /**
     * Check the number of senders that have dispatched the same type of action. (Kim s lng cc sender  dispatch cng loi action.)
     * Returns true when there are enough senders of the same action type. (Tr v true khi  s cc sender cng loi action.)
     * roll-call will reset every time the first state name is called a second time. (roll-call s reset mi khi state name u tin gi ln 2.)
     * @param stateNames  Mng string cha Node name hoc Class name ca GamifyStore hoc GamifyComponent.
     */
    protected rollCallSameAction(...stateNames:string[]):boolean{
        if(this._currentAction){
            let type:string = this._currentAction.type
            let sender:string = this.searchRegisterName(this._currentAction.sender);
            let validateCheckList:string[] = stateNames.reduce((validateStateNames:string[] ,stateName:string)=>{
                const valideStateName:string = this.searchRegisterName(stateName);
                if(valideStateName){
                    validateStateNames.push(valideStateName);
                }else{
                    (DEBUG || DEV || EDITOR) && GAMIFY_WARNNING && warn(this.__ccClassName +'.rollCall(' + stateNames + '): ' + stateName + ' is not in the system.')
                }
                return validateStateNames
            },[]);
           
            let rollCallList:string[] = this._rollCallCached.get(type) || [];
            let rollCallIndex:number = rollCallList.indexOf(sender);
            if(rollCallIndex == 0){
                rollCallList = [];
                rollCallIndex = -1;                
            }
            // Available in validateCheckList and not yet in _rollCallList. (C trong validateCheckList v cha c trong _rollCallList.)
            if((rollCallIndex == -1) && (validateCheckList.indexOf(sender) != -1)){
                rollCallList.push(sender);
                this._rollCallCached.set(type, rollCallList);
            }
            let condition:boolean = rollCallList.length == validateCheckList.length;
            if(condition){

            }
            return condition;
        }
        return false;        
    }

    /**
     * Check if The action is dispatched from the current component. (Kim tra xem Action nhn c c c gi i t thnh phn hin ti khng)
     * @returns 
     */
    protected senderIsMe():boolean{        
        if(DEBUG || DEV || EDITOR){
            const dispatcher:Dispatcher = this.getDispatcher();
            if(!dispatcher?.isDispatching()){
                throw new Error(this.getStateName() + '.senderIsMe():: senderIsMe method only used when processing in the Data flow');
            }
        }
        return this._currentAction.sender == this.getStateName();
    }

    
    /**
     * Get the state name of the current store. (Ly state name ca store hin ti.)
     * @returns 
     */
    public getStateName(){
        if(!this._stateName){            
            if(this.node){
                let nodePath:string = this.node.getPathInHierarchy() || this.node.name;
                nodePath = nodePath.replace(/\//g, '.');
                this._stateName = nodePath + '<' + this.__ccClassName + '>';
            }else{
                this._stateName = this.__ccClassName + '<' + this.__ccClassName + '>';
            }
            const existedSateNames:string[] = this.searchAllRegisterNames(this._stateName);
            if(existedSateNames.length){
                this._stateName = this._stateName + '[' + existedSateNames.length +']';
            }
        }
        return this._stateName;
    }

    /**
     * Tham chiu ti Component tng ng vi state name.
     * @param fullStateName The full name of the state name (not the abbreviation), usually the string taken from getStateName() hoc action.sender
     * (Tn y  ca state name (khng phi tn vit tt), thng l string ly ra t getStateName())
     * @returns 
     */
    protected getStoreComponent<GamifyStoreType = GamifyStore>(fullStateName:string):GamifyStoreType{
        const nodeName:string = fullStateName.slice(0, fullStateName.indexOf('<'))
        const compNode:Node = find(nodeName)
        if(compNode){            
            const compName:string = this.getStoreName(fullStateName);
            const compIndex:number = this.getIndexOfSameStores(fullStateName);
            if(compIndex !== -1){
                const compList:GamifyStoreType[] = compNode.getComponents(compName) as GamifyStoreType[] ;
                return compList.find((comp:GamifyStoreType)=>comp['getStateName']() == fullStateName)
            }
            return compNode.getComponent(compName) as GamifyStoreType;
        }
        return null;
    }

    /**
     * Get the class name of GamifyStore (called Store name) from the full name of the state. Usually a short name of the state for ease of use in code.
     * (Ly tn class ca GamifyStore (gi l Store name) t tn y  ca state. Thng l tn ngn gn ca state cho d s dng trong code.)
     * @param fullStateName 
     */
    public getStoreName(fullStateName?:string):string|null{
        let selectedSateName:string = fullStateName || (this._currentAction ? this._currentAction.sender : null);
        if(selectedSateName && selectedSateName.length){
            const regExpMatchArray:RegExpMatchArray = selectedSateName.match(ClassTagRegex)
            if(regExpMatchArray){
                const insideTags:string[] = regExpMatchArray[0]?.split(/\s/g);
                if(insideTags && insideTags.length) return insideTags[0];
            }
        }
        return null;
    }
    
    /**
     * Cc GamifyStore v GamifyComponent trong cng mt node s c nh index. Index ny c gn vo state name  d phn bit.
     * getIndexOfSameStores method ly ra index t tn y  ca state. Tn y  ca state c th c ly ra t getStateName() hoc action.sender
     * @param fullStateName 
     */
    protected getIndexOfSameStores(fullStateName?:string):number{
        let selectedSateName:string = fullStateName || (this._currentAction ? this._currentAction.sender : null);
        if(selectedSateName && selectedSateName.length){
            const regExpMatchArray:RegExpMatchArray = selectedSateName.match(OrderTagRegex)
            if(regExpMatchArray){
                const insideTags:string[] = regExpMatchArray[0]?.split(/\s/g);
                if(insideTags && insideTags.length){
                    return parseInt(insideTags[0]);
                }
            }
        }
        return -1;
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    protected searchRegisterName(stateName:string):string{
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof BasicDispatcher){
            return (dispatcher as BasicDispatcher).searchRegisterName(stateName);
        }
        // 
        return null
    }

    protected searchAllRegisterNames(stateName:string):string[]{
        let dispatcher:Dispatcher = this.getDispatcher();
        if(dispatcher && dispatcher instanceof ActionStoreDispatcher){
            return (dispatcher as ActionStoreDispatcher).searchAllRegisterNames(stateName);
        }
        // 
        return null
    }

    /**
     * 
     * @param one 
     * @param two 
     * @returns 
     */
    protected areEqual(one:any, two:any) {
        return Helper.areEqual(one, two);
    }

    /**
     * Merge all data export form all stores at the same action.
     * @param data 
     */
    // protected share(data:object){
    //     if(this._currentAction && data){
    //         this._currentAction.shares = data ? Object.assign(this._currentAction.shares || {}, data)  : this._currentAction.shares;
    //     }else{
    //         (DEBUG || DEV || EDITOR) ? error(this.getStateName() + '.export(data): Data export is unknown') : error(false);
    //     }
    // }

    /**
     * Ly state ca cc store khc.
     * @param stateName 
     * @returns 
     */
    protected getOtherState(stateName:string):any{
        !this._currentAction ? (DEBUG || DEV || EDITOR) ? error( this.getStateName() + '.getOtherState('+ stateName + '): Cannot get state, because the ' + this.getStateName() + ' store is not included in the data flow.') : error(false) : undefined;
        const inactiveAction:Action = this._currentAction;
        let dispatcher:Dispatcher = this.getDispatcher();
        if((DEBUG || DEV || EDITOR) && !dispatcher.isDispatching()){
            throw new Error(this.getStateName() + '.getOtherState('+stateName+'):: getOtherState only used in \"reduce\" function')
        }
        //
        if(!inactiveAction.stores){
            inactiveAction.stores = Object.create(null);
        }
        //        
        let orderStateNames:string[] = this.searchAllRegisterNames(stateName);
        try{
            this.getDispatcher().waitFor(orderStateNames);
            
        }catch(e:any){
            (DEBUG || DEV || EDITOR) && GAMIFY_WARNNING && warn('There is a loop when the  ' + this.getStateName() + ' store call to ' + stateName);
        }finally{
            (DEBUG || DEV || EDITOR) && orderStateNames.forEach((orderStateName:string)=>{
                if(!inactiveAction.stores[orderStateName]){
                    this.__detectedUndefinedState(orderStateName, inactiveAction);
                }
            })            
        }
        this._currentAction = inactiveAction;
        // 
        if(orderStateNames.length && orderStateNames.length > 1){
            warn('More than one store ' + orderStateNames + ' received from ' + stateName);
        }
        return this._currentAction.stores[orderStateNames[0]];
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    protected hasOtherState(stateName:string):boolean {
        !this._currentAction ? (DEBUG || DEV || EDITOR) ? error( this.getStateName() + '.getOtherState('+ stateName + '): Cannot get state, because the ' + this.getStateName() + ' store is not included in the data flow.') : error(false) : undefined;
        const inactiveAction:Action = this._currentAction;
        // let dispatcher:Dispatcher = this.getDispatcher();
        // if((DEBUG || DEV || EDITOR) && !dispatcher?.isDispatching()){
        //     throw new Error(this.getStateName() + '.hasStateInAction('+stateName+'):: hasStateInAction only used in \"reduce\" function')
        // }
        if(!inactiveAction.stores){
            return false;
        }
        let orderStateNames:string[] = this.searchAllRegisterNames(stateName);
        if(orderStateNames.length && orderStateNames.length > 1){
            warn('More than one store ' + orderStateNames + ' received from ' + stateName);
        }
        return !!this._currentAction?.stores[orderStateNames[0]];
    }

    /**
     * 
     * @param stateName 
     * @returns 
     */
    protected isStoreRegisted(stateName:string):boolean{
        let orderStateNames:string[] = this.searchAllRegisterNames(stateName);
        return orderStateNames && !!orderStateNames.length
    }

    // ---------- 
    /**
     * 
     * @param {*} action 
     */
    protected __prepareInvokeOnDispatch(action:Action){
        this._currentAction = action;
        this.__shareState();
    }

 
    protected __emitChange() {
        this.__shareState();
        super.__emitChange()
    }

    /**
     * 
     * @param action 
     */
    protected __endingInvokeOnDispatch(action:Action){
        //  
        this._currentAction = null;
    }

    // ---------- 

    /**
     * Check if the action is valid or not. (Kim tra action c hp l hay khng.)
     * - The action type is contained in the Action array that allows receiving, declared in the function allowActionTypes(). (Action type c trong mng Action cho php nhn, c khai bo trong function allowActionTypes().)
     * - If the action has a specific recipient list (action.receiver), it must exist in the system. (Nu action c danh sch ngi nhn (action.receiver) c th th phi tn ti trong h thng.)
     * @param action 
     * @returns boolean
     */
    protected _validateAction(action:Action):boolean {
        const currentStateName:string = this.getStateName();
        const allowTypes:Array<string> = this.allowActionTypes();
        const extendAllowType:string[] = [STORE_ACTION.NEW_STATE_REGISTED].concat(allowTypes);
        let receivers:string[] = action.receiver || [];
        let receiverCondition:boolean = !receivers.length;
        receivers.forEach((receiverName:string)=>{
            let validateReceiverNames:string[] = this.searchAllRegisterNames(receiverName);
            if(validateReceiverNames) {
                receiverCondition = receiverCondition || (validateReceiverNames.indexOf(currentStateName) != -1);             
            }
        });        
        let actionCondition:boolean = !(allowTypes.length && (extendAllowType.indexOf(action.type) == -1));        
        return actionCondition && receiverCondition;
    }

    /**
     * 
     * @param storeClassName 
     * @returns 
     */
    protected _checkRule(storeClassName:string):boolean{
        return js.isChildClassOf(js.getClassByName(storeClassName), GamifyStore);
    }

    /**
     * 
     * @param id 
     */
    protected __detectedUndefinedState(id:string, action:Action){
        if((DEBUG || DEV || EDITOR)){
            let problems:string[] = [
                '- There was a previous data loop error. (C li vng lp d liu trc .)',
                '- ' + id + ' may not have been initialized yet. (' + id + ' c th cha kp khi to.)'                
            ]
            error(this.getStateName() + '.getOtherState('+id +'): The store in action cannot be retrieved: ' + this._currentAction.type + '. Store '+id+' may encounter the following problems: ' + '\n ('+this.getStateName() + '.getOtherState('+id +'): Khng ly c state trong action: ' + this._currentAction.type + '. Store '+id+' c th gp cc vn  sau: \n' + problems.join('\n')) ;
            
        }
    }

    // ------------- PRIVATE -------------------

    /**
     * 
     */
    private __shareState(){
        if(this._currentAction){
            if(!this._currentAction.stores){
                this._currentAction.stores = Object.create(null);
            }
            this._currentAction.stores[this.getStateName()] = this.getState() || Object.create(null);
        }
    }

    /**
     * 
     * @param stateName 
     */
    private __setStateName(stateName:string){
        // let extension:string = btoa(this.getDispatchToken()).replace(/=/g,'')
        this._stateName = stateName //+ '.' + extension;
    }

    //------------ Override --------------   

    /**
     * 
     * @param action 
     */
    protected __invokeOnDispatch(action:Action) {
        if(this._validateAction(action)) {
            this.__prepareInvokeOnDispatch(action);
            super.__invokeOnDispatch(action);      
            this.__endingInvokeOnDispatch(action);
        }else{
            this.onRejectedAction(action);
        }
    }

    /**
     * 
     * @param state 
     * @param action 
     * @returns 
     */
    public reduce(state:any, action:Action):any {
        if(action.type == STORE_ACTION.NEW_STATE_REGISTED) return this.onStoreRegisted(state, action) || state ;            
        else return this.onData(state, action) || state ;
    }

    /**
     * 
     * @param dispatcher 
     * @param isForce 
     */
    public setDispatcher(dispatcher:Dispatcher, isForce:boolean = false){        
        super.setDispatcher(dispatcher, isForce);  
        let currentDispatcher:Dispatcher = this.getDispatcher();   
        if(currentDispatcher && currentDispatcher instanceof GamifyDispatcher && !isForce){  
            // !isForce condition to call only once when using setDispatcher (iu kin !isForce  ch gi mt ln khi dng setDispatcher)          
            // After mapping the state name with the token, change the state name according to system requirements.(Sau khi map state name vi token, thay i state name theo yu cu ca h thng.)
            this.__setStateName( (currentDispatcher as GamifyDispatcher).mapToken(this.getStateName(), this.getDispatchToken()) );
        }
        (DEBUG || DEV || EDITOR) && GAMIFY_LOGS && log('Register dispatcher '+ currentDispatcher.id +' to ' + this.getStateName());
    }

    /**
     * 
     */
    public removeDispatcher(){        
        let dispatcher:any = this.getDispatcher();
        if(dispatcher && dispatcher instanceof GamifyDispatcher){            
            (dispatcher as GamifyDispatcher).unmapToken(this.getStateName(), this.getDispatchToken());
        }
        (DEBUG || DEV || EDITOR) && GAMIFY_LOGS && log('G dispatcher c ' + dispatcher.id + ' ca ' + this.getStateName());
        super.removeDispatcher();
    }

    /**
     * Data Flow
     * Using for waitting another component to be implemented.
     * @param gamifyComponentName 
     */
    public waitFor(gamifyComponentName:string){
        let dispatcher:Dispatcher = this.getDispatcher();
        if((DEBUG || DEV || EDITOR) && !dispatcher.isDispatching()){
            throw new Error(this.getStateName() + '.waitFor('+gamifyComponentName+'):: waitFor method only used when processing in the Data flow')
        }
        super.waitFor(gamifyComponentName);
    }

    

    /**
     * 
     */
    onLoad(){
        super.onLoad();
        // Hin ti ch lm vic mc nh vi 01 dispatcher duy nht.
        this.setDispatcher(GamifyDispatcher.instance);
        this._rollCallCached = new Map();
        const readyStates:string[] = (this.getDispatcher() as GamifyDispatcher)?.getAllRegistedStates();
        this.initialized(readyStates);        
    }

    /**
     * 
     */
    onDestroy(){        
        this.onRemove();
        super.onDestroy();
    }

 
}



