import { _decorator, Component, Node } from 'cc';
import { GamePopup } from '../../../Scripts/ui/popup/GamePopup';
import { Prefab } from 'cc';
import { ServiceRequestConfig } from '../../../Scripts/ui/api/ServiceRequestConfig';
import { Action } from '../../../Scripts/gamify/core/Action';
import { API_ACTION, MainGameAction } from '../../../Scripts/ui/actions/StartActions';
import { ClaimInfo, InboxInfo, PlayerInboxAction, PlayerInboxBetSetting, PlayerInboxState, PlayerInboxStatus } from './types/PlayerInboxType';
import { error } from 'cc';
import { js } from 'cc';
import { Helper } from '../../../Scripts/gamify/utils/Helper';
import { WebSocketComponent } from '../../../Scripts/ui/network/WebSocketComponent';
import { instantiate } from 'cc';
import { PlayerInboxItem } from './ui/PlayerInboxItem';
import { log } from 'cc';
import { RemoteMultilingualTextPackage } from '../../../Scripts/ui/l10n/plugins/RemoteMultilingualTextPackage';
import { Event } from 'cc';
import { warn } from 'cc';
import { AsyncWaitify } from '../../../Scripts/gamify/master/AsyncWaitify';
import { PlayButtonState, PlayMode } from '../../../Scripts/ui/button/PlayButton';
import { Label } from 'cc';
import { resolve } from 'path';
const { ccclass, property } = _decorator;




@ccclass('PlayerInboxPopup')
export class PlayerInboxPopup extends AsyncWaitify(GamePopup) {


    @property({readonly:true})
    waitForStore:string = 'PlayButton';
    
    @property({
        type:Node,
        group:'PANEL'
    })
    curtain:Node = null;
    
    @property({
        type:Node,
        group:'PANEL'
    })
    closeButton:Node = null;

    @property({
        type:Node,
        group:'PANEL'
    })
    dragButton:Node = null;

    @property({
        type:Node,
        group:'PANEL'
    })
    continueNotication:Node = null;

    @property({
        type:Node,
        group:'PANEL'
    })
    playerInboxPanel:Node = null;
    
    
    @property({
        type:Node,
        group:'PANEL',
        displayName:'|__ Content'
    })
    inboxContainer:Node = null;

    @property({
        type:Prefab,
        group:'ASSETS',
        displayName:'Item Prefab'
    })
    itemPrefab:Prefab = null;
    
    @property({
        type:RemoteMultilingualTextPackage,
        group:'ASSETS',
        displayName:'Language Package'
    })
    languagePackage:RemoteMultilingualTextPackage = null;
    
    @property({
        type:Node,
        group:'PANEL',
        displayName:'|__ Prepaid Panel'
    })
    prepaidPanel:Node = null;
    
    @property({
        type:Node,
        group:'PANEL',
        displayName:'|__ CashDrop Panel'
    })
    cashDropPanel:Node = null;
    
    @property({
        type:Label,
        group:'PANEL',
        displayName:'|__ Empty Notication'
    })
    emptyNotication:Label = null;

    @property({
        type:ServiceRequestConfig,
        group:'NETWORK',
    })
    listingService:ServiceRequestConfig = null;
    
    @property({
        type:ServiceRequestConfig,
        group:'NETWORK',
    })
    claimService:ServiceRequestConfig = null;

    public allowActionTypes(): string[] {
        return [
            MainGameAction.INIT,
            MainGameAction.SETTING,
            PlayerInboxAction.UPDATE_INBOX,
            PlayerInboxAction.CLAIM,
            PlayerInboxAction.SHOW,
            PlayerInboxAction.HIDE
        ]
    }


    private _selectedItemInfo:InboxInfo;

    private servieName:string = 'APIService';

    private _continue:Function;

    onInit(readyStates?:string[]){
        // if(readyStates.findIndex((fullStateName:string)=>fullStateName.indexOf('<' +this.waitForStore+ '>') !== -1) !== -1){
        //     // Truong hop PlayButton khoi tao truoc PlayerInBoxPopup
        //     // log('[1]. Truong hop PlayButton khoi tao truoc PlayerInBoxPopup')
        //     this.ready();
        // }        
        this.waitForReady().then(()=>{
            this.dispatch({type:PlayerInboxAction.SHOW, payload:{status: PlayerInboxStatus.SHOW_ICON_VIEW}});
        })
    }

    /**
     * 
     * @param state 
     * @param action 
     */
    onStoreRegisted(state: any, action: Action): any {
        // Kiem tra khi PlayButton load xong.
        if( (this.senderIsMe() && this.hasOtherState(this.waitForStore)) || ((action.sender.indexOf(this.waitForStore) !== -1))){
            this.ready();
        }
    }

    /**
     * 
     * @param state 
     * @param action 
     */
    onData(state:PlayerInboxState, action:Action){
        const actionType:string = action.type;
        const payload:any = action.payload;
        switch(actionType){
            case MainGameAction.INIT:
                if(payload && payload.apiService) {
                    this.servieName = payload.apiService;
                }
                // 
                this.servieName = payload.apiService;
                this.checkAndUpdateClaimmingState(state);                
                break;
                
            case MainGameAction.SETTING:
                this.implementSettingState(state, action)
                break;

            case PlayerInboxAction.UPDATE_INBOX:
                state.status = PlayerInboxStatus.SHOW_INBOX_VIEW;
                this.render(true);
                state.status = PlayerInboxStatus.READY;
                break;            
           
            case PlayerInboxAction.SHOW:
                if(payload && (js.isNumber(payload.status) || !!payload.status)){
                    state.status = payload.status
                }
                this.render();
                break;

            case PlayerInboxAction.HIDE:
                state.status = PlayerInboxStatus.HIDE_VIEW;
                this.render();
                break;

            case PlayerInboxAction.CLAIM:
                if(payload){
                    state.status = PlayerInboxStatus.CLAIM;
                    if(!state.claimed) {state.claimed = Object.create(null);}
                    state.claimed.url = payload.url;
                    state.claimed.data = payload.data as ClaimInfo;
                }
                this.render();
                break;
        }
        
    }


    /**
     * 
     * @param done 
     * @param capturedState 
     * @param sharedData 
     */
    async onRender(done: Function, capturedState?: PlayerInboxState, sharedData?: any): Promise<void> {   
        //  Note: capturedState only exists during the rendering process. When finished it will disappear. 
        // (Chu : capturedState chi tn ti trong sut qu trnh render. Khi kt thc n s bin mt.)
        switch(capturedState.status){           

            case PlayerInboxStatus.SHOW_ICON_VIEW:
                await this.showIconViewRender(capturedState);
                break;

            // case PlayerInboxStatus.INIT:
            case PlayerInboxStatus.SHOW_INBOX_VIEW:
                await this.showInboxViewRender(capturedState);
                await this.requestInboxStateRender();
                break;

            case PlayerInboxStatus.HIDE_VIEW:
                // await this.showIconViewRender(capturedState);
                break;

            case PlayerInboxStatus.CLAIM:
                await this.requestClaimmingStateRender(capturedState);
                await this.showIconViewRender(capturedState);
                await this.updateAutoSpinSettingRender(capturedState);                
                break
                
            case PlayerInboxStatus.SHOW_CONTINUE_VIEW:
                await this.showContinuePopupRender(capturedState);                
                await this.updateAutoSpinSettingRender(capturedState);   
                // await this.executeSpinStateRender(capturedState);
                await this.showIconViewRender(capturedState);
                break;

            case PlayerInboxStatus.UPDATE:
                await this.updateAutoSpinSettingRender(capturedState);
                // await this.showIconViewRender(capturedState);
                break;

            case PlayerInboxStatus.AUTO_SPIN:
                await this.executeSpinStateRender(capturedState);
                break;
        }
        done && done();
    }

    // ------------------
    
    /**
     * 
     * @param state 
     */
    private checkAndUpdateClaimmingState(state:PlayerInboxState){        
        if(!this.servieName) return;
        const networkState:any = this.getOtherState(this.servieName);
        const playerReward:any = this.getClaimedRewards(networkState);
        const claimedInfo:ClaimInfo = this.getClaimInfoFromRewardData(playerReward);
        // 
        if(claimedInfo){
            if(!state.claimed) state.claimed = Object.create(null);
            state.claimed.data = claimedInfo || null;
            state.status = PlayerInboxStatus.SHOW_CONTINUE_VIEW;
            this.render(true);            
        }
        state.status = PlayerInboxStatus.READY;
    }

    /**
     * 
     * @param state 
     */
    private implementSettingState(state:PlayerInboxState, action:Action){
        if(this.senderIsMe()){     
            if(!this.isStoreRegisted(this.waitForStore)){   
                warn('implementSettingState----------- store ' + this.waitForStore + ' chua duoc khai tao')
            } 
            this.waitFor(this.waitForStore);
            const claimedInfo: {
                url: string;
                data: ClaimInfo;
            } = state.claimed;
            // const playButtonState:PlayButtonState = this.getOtherState(this.waitForStore);
            // // && playButtonState.mode == PlayMode.NORMAL
            if(claimedInfo && claimedInfo.data && !!claimedInfo.data?.count && claimedInfo.data?.count > 0){                
                const lastStatus:PlayerInboxStatus = state.status;
                state.status = PlayerInboxStatus.AUTO_SPIN;
                this.render(true);
                state.status = lastStatus;
                state.claimed = null;
            }
        }else{
            // log('settign from ' + action.sender)
        }
    }

    // ------------------

    

    // ------------------------------------------

    /**
     * 
     */
    private async requestInboxStateRender(){
        const networkState:any = await this.listingService.request();
        await this.renderInboxList(networkState);
    }

    /**
     * 
     * @param capturedState 
     */
    private async requestClaimmingStateRender(capturedState:PlayerInboxState){
        if(!this.isStoreRegisted(this.waitForStore)){
            await this.waitForReady();
        }
        const claimUrl:string = capturedState?.claimed?.url;
        const claimedInfo:ClaimInfo = capturedState?.claimed?.data as ClaimInfo;
        if(claimedInfo && claimUrl && claimUrl.length){            
            const networkState:any = await this.claimService.request([{url:claimUrl}]);
            if(!networkState) {
                error('[PlayerInboxPopup]::checkAndSetupPlayerInboxSetting(...): Do not found state data via \"servieName\" '); 
                return;
            }
            // 
            const playerReward:any = this.getClaimedRewards(networkState);
            const claimedInfo:ClaimInfo = this.getClaimInfoFromRewardData(playerReward);            
            if(!capturedState.claimed) capturedState.claimed = Object.create(null);
            capturedState.claimed.data = claimedInfo;
        }
    }

    /**
     * 
     * @param rewardCount 
     * @param betSetting 
     */
    protected async updateAutoSpinSettingRender(state:PlayerInboxState){        
        const claimedInfo:ClaimInfo = state?.claimed?.data;
        if(claimedInfo){            
            const rewardCount:number = claimedInfo.count;
            const betSetting:PlayerInboxBetSetting = claimedInfo.betSetting;
            const isUnstopable:boolean = (!!rewardCount && rewardCount > 0);

            if(!this.isStoreRegisted(this.waitForStore)){
                await this.waitForReady();
            }

            this.dispatch({type:MainGameAction.SETTING, payload: {     
                isUnstoppable:isUnstopable,         
                autoSpinNumber: rewardCount,
                betConfig: betSetting
            }});        
        }
    }


    /**
     * 
     * @param state 
     */
    private async executeSpinStateRender(state:PlayerInboxState){
        if(!this.isStoreRegisted(this.waitForStore)){  
            await this.waitForReady();
        }
        this.dispatch({type:API_ACTION.SPIN});
        // state.claimed = null;
        // this.setState(state);
    }
    

    // ------------------------------------------

    /**
     * 
     * @param state 
     */
    private async showContinuePopupRender(state:PlayerInboxState){
        this.curtain.active = true;
        this.playerInboxPanel.active = false;
        this.closeButton.active = false;
        this.continueNotication.active = true;
        this.dragButton.active = false;
        await new Promise((resolve:Function)=>{
            this._continue = resolve;
        })
    }


    /**
     * 
     * @param state 
     */
    private async showInboxViewRender(state?:PlayerInboxState){
        this.curtain.active = true;
        this.playerInboxPanel.active = true;
        this.closeButton.active = true;        
        this.continueNotication.active = false;
        this.dragButton.active = false;
    }

    /**
     * 
     * @param state 
     */
    private async showIconViewRender(state?:PlayerInboxState){
        this.curtain.active = false;        
        this.playerInboxPanel.active = false;
        this.closeButton.active = false;
        this.continueNotication.active = false;
        this.dragButton.active = true;
    }

        
    // --------------- PRIVATE ---------------------

    /**
     * 
     * @param networkState 
     * @returns 
     */
    private getClaimedRewards(networkState:any):any{
        if(!networkState) return;
        let claimedReward:any = Helper.searchValue(networkState, 'config.playerReward');
        if(Array.isArray(claimedReward)){
            claimedReward = (claimedReward as any[]).find((reward:any)=> reward.status == "CLAIMED");
        }
        return claimedReward;
    }

    /**
     * 
     * @param networkState 
     * @returns 
     */
    private getPlayMode(networkState:any):number{
        const spinData:any = networkState?.spin;
        if(spinData){
            const numOfFreeSpin:number = spinData.freeSpins || 0;
            return (numOfFreeSpin && numOfFreeSpin > 0) ? 1 : 0; // 1: FreeSpin , 0: Normal Spin
        }
        return 0
    }

    /**
     * 
     * @param claimRewardData 
     */
    private getClaimInfoFromRewardData(playerReward:any):ClaimInfo{
        if(!playerReward) return null;
        const id:string = Helper.searchValue(playerReward, 'id');       
        const rewardCount:number = playerReward.rewardCount || Helper.searchValue(playerReward, 'reward.count') || 0;
        let rewardData:any = Helper.searchValue(playerReward, 'reward.data');
        if(rewardData){
            rewardData = js.isString(rewardData) ? JSON.parse(rewardData) : rewardData;
        }
        // Update auto spin number
        const betSetting:PlayerInboxBetSetting = rewardData ? Helper.searchValue(rewardData, 'betSetting') : null;         
        // 
        if(betSetting && id){
            return {
                claimId: id,
                betSetting: betSetting,
                count: rewardCount
            } as ClaimInfo
        }
        return null
    }

    /**
     * 
     * @param state 
     */      
    protected async renderInboxList(networkState:any){
        if(networkState?.config?.playerInbox){
            const data:any[] = networkState?.config?.playerInbox.data;  // data of inbox_history
            const domain:string = Helper.searchValue(networkState?.config, 'domain');
            const playerToken:string = Helper.searchValue(networkState?.config, 'playerToken');
            // 
            if(this.emptyNotication) { 
                this.emptyNotication.node.active = !(data && data.length);
            }
            // 
            if(data && data.length){
                const now = new Date().getTime(); // Get the current date and time 
                const inboxList:InboxInfo[] = [];
                data.forEach((elementData:any, index:number)=>{
                    const inboxInfo:InboxInfo = js.createMap();
                    // Pick data field from network data.
                    inboxInfo.id = index;
                    inboxInfo.claimId = Helper.searchValue(elementData, 'id');
                    inboxInfo.module = Helper.searchValue(elementData, 'module');
                    inboxInfo.isRead = !!Helper.searchValue(elementData, 'isRead');
                    inboxInfo.claimBefore = new Date(Helper.searchValue(elementData, 'claimBefore'));
                    inboxInfo.claimUrl  = domain + Helper.searchValue(elementData, 'claimUrl') + playerToken;
                    inboxInfo.count  = Helper.searchValue(elementData, 'extraInfo.reward.count');
                    // inboxInfo.gameMode  = Helper.searchValue(elementData, 'extraInfo.reward.data.gameMode.type'); // Optional, do not need.
                    inboxInfo.betSetting = Helper.searchValue(elementData, 'extraInfo.reward.data.betSetting');
                    // 
                    inboxList.push(inboxInfo);
                })
                if(inboxList.length) {
                    inboxList.sort((a:InboxInfo, b:InboxInfo)=>{
                        const timeA = new Date(a.claimBefore).getTime();
                        const timeB = new Date(b.claimBefore).getTime();
                        // Compare the 'created' times
                        if (timeA < now && timeB < now) {
                            return 0; // Keep the order unchanged if both times are less than the current time
                        } else if (timeA < now) {
                            return 1; // 'a' should be placed at the end of the array
                        } else if (timeB < now) {
                            return -1; // 'b' should be placed at the end of the array
                        } else {
                            return (timeA - timeB); // Compare times in ascending order
                        }
                    })
                    // 
                    // await this.activeViewRender();
                    await this.updateInboxHistoryRender(inboxList);
                }
                // 
            }else{
                warn('Empty Inbox !!!')
            }
            //

            // 
        }else{
            warn('There is no data for networkState.config.playerInbox');
        }
    }

    /**
     * 
     * @param inboxList 
     */
    private async updateInboxHistoryRender(inboxList:InboxInfo[]){
        if(this.inboxContainer && this.itemPrefab){
            if(this.languagePackage){
                await this.languagePackage.waitForReady();
            }
            this.inboxContainer.removeAllChildren();
            inboxList.forEach((data:InboxInfo)=>{
                const itemName:string = data.claimId;
                let itemNode:Node = this.inboxContainer.getChildByName(itemName);
                if(!itemNode){
                    itemNode= instantiate(this.itemPrefab);
                    itemNode.parent = this.inboxContainer;
                    itemNode.name = itemName;
                }
                // 
                const item:PlayerInboxItem = itemNode.getComponent(PlayerInboxItem);
                item.on(PlayerInboxItem.EVENT.SELECTED, this.onClaimItem.bind(this));
                item.render(data);
                // 
            })
        }
    }

    // -----------------
    
    /**
     * 
     * @param itemInfo 
     */
    protected async onClaimItem(itemInfo:InboxInfo){        
        // if(this._selectedItemInfo == itemInfo) return;
        // 
        if(this.claimService){
            const claimedInfo:ClaimInfo = {
                claimId: itemInfo.claimId,
                count: itemInfo.count,
                betSetting: itemInfo.betSetting
            }
            // 
            this.dispatch({type:PlayerInboxAction.CLAIM, payload:{
                url: itemInfo.claimUrl,
                data: claimedInfo                
            }});
            //           
        }else{
            error('No service config \"claimService\"');
        }
    }

    /**
     * 
     * @param event 
     */
    protected onPressIconButton(event:Event){
        this.dispatch({type:PlayerInboxAction.SHOW, payload:{status: PlayerInboxStatus.SHOW_INBOX_VIEW} });
    }
    
    /**
     * 
     * @param event 
     */
    protected onPressCloseButton(event:Event){
        this.dispatch({type:PlayerInboxAction.SHOW, payload:{status: PlayerInboxStatus.SHOW_ICON_VIEW}});
    }

    protected onPressOkButton(event:Event){
        if(this._continue){
            this._continue();
        }
        // this.dispatch({type:PlayerInboxAction.SHOW, payload:{status: PlayerInboxStatus.SHOW_INBOX_VIEW} });
    }

    // -----------------
    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns the string which passed to {paramKey} of request.
     */
    getStartDateParam(state:any, paramKey:string):string{
        const date:Date = new Date();
        return new Date(date.getFullYear(), date.getMonth(), date.getDate()).toISOString();        
    }
    
    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns the string which passed to {paramKey} of request.
     */
    getEndDateParam(state:any, paramKey:string):string{
        return new Date().toISOString();
    }

    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns 
     */
    getCurrentRewardId(state:any, paramKey:string):string{        
        return (this._selectedItemInfo) ? this._selectedItemInfo.claimId : undefined
    }

    /**
     * This funtion using for handler the params tranfer to service config.
     * @param state 
     * @param paramKey 
     * @returns 
     */
    getCurrentModule(state:any, paramKey:string):string{
        return (this._selectedItemInfo) ? this._selectedItemInfo.module : undefined
    }

    getIsFunParam(state:any, paramKey:string):string{
        return 'false'
    }
    // -----------------
    /**
     * 
     * @returns 
     */
    protected getAPIData():any{
        const serviceData:any = this.getOtherState(this.servieName);
        if(!serviceData) {
            error('Khng c hoc setup nhm service');
            return null;
        }
        return serviceData;
    }
    // -----------------

}


