import { _decorator, Component, Enum, error, js, log, Node, Skeleton, sp } from 'cc';
import { TimelineClip } from '../clip/TimelineClip';
import { GameUtils } from '../../utils/GameUtils';
import { Vec2 } from 'cc';
import { Vec3 } from 'cc';

const { ccclass, property, executeInEditMode, menu } = _decorator;
const COMMAND:string = '{ADJUST_ON_RUNTIME}'

let DefaultSkinsEnum = Enum({ '*': -1 });
let DefaultAnimsEnum = Enum({ '*': -1 });


@ccclass('SkeletonClip')
@menu('Gamify/Animation/SkeletonClip')
@executeInEditMode(true)
export class SkeletonClip extends TimelineClip<sp.Skeleton> {

    @property({
        type: DefaultAnimsEnum,
        displayName: "Animation",
    })
    public get animIndex(): number {
        return this._animIndex;
    }
    public set animIndex(value: number) {
        this._animIndex = value;
        this.updateAnimation();
    }

    @property({
        type: DefaultSkinsEnum,        
        displayName: "Skin"
    })
    public get skinIndex(): number {
        return this._skinIndex;
    }
    public set skinIndex(value: number) {
        this._skinIndex = value;
        this.updateSkin();
    }  
    
    @property({
        serializable:true
    })
    protected _animation:string = '';

    get animation():string{
        return this._animation
    }

    @property({
        serializable:true
    })
    protected _skin:string = '';

    get skin():string{
        return this._skin;
    }

    @property({
        serializable:true,
        visible:false
    })
    private _skinIndex: number = 0;

    @property({
        serializable:true,
        animatable:false
    })
    protected _animIndex: number = 0;
    
    /**
     * 
     */
    onLoad(){        
        super.onLoad();
        this.generateEnums();
    }

    /**
     * 
     * @param config 
     * @returns 
     */
    public adjust(config:number|string|Vec2|Vec3){
        if(js.isNumber(config)){
            this.combineNumber(config as number);
        }else if(js.isString(config)){
            this.combineString(config as string);
        }else if(js.isChildClassOf(config, Vec2)){
            this.combineVec2(config as Vec2);
        }else if(js.isChildClassOf(config, Vec3)){
            this.combineVec3(config as Vec3);
        }
        return this;
    }

    /**
     * Check animation index first.
     * @param id 
     */
    private combineNumber(id:number){
        if(this.isAdjustOnRuntime(this.animIndex)){
            this.animIndex = id;
        }else if(this.isAdjustOnRuntime(this.skinIndex)){
            this.skinIndex = id;
        }else {
            // this.isLoop = Boolean(id) ? true : false;
        }
    }
    
    /**
     * Check animation index first.
     * @param animationName 
     */
    private combineString(animationName:string){
        if(this.super){
            const skeletonData:sp.SkeletonData = this.super.skeletonData;
            const skinEnum:any = skeletonData['getSkinsEnum']();        
            this.combineNumber(skinEnum[animationName]);
        }
    }

    /**
     * vec2.x : skin index
     * vec2.y : animation index
     * @param vec 
     */
    private combineVec2(vec:Vec2){        
        this.skinIndex = this.isAdjustOnRuntime(this.skinIndex) ? vec.x : this.skinIndex;
        this.animIndex = this.isAdjustOnRuntime(this.animIndex) ? vec.y : this.animIndex;        
    }

    /**
     * vec3.x : skin index
     * vec3.y : animation index
     * vec3.z : loop
     * @param vec 
     */
    private combineVec3(vec:Vec3){
        this.skinIndex = this.isAdjustOnRuntime(this.skinIndex) ? vec.x : this.skinIndex;
        this.animIndex = this.isAdjustOnRuntime(this.animIndex) ? vec.y : this.animIndex;
        this.isLoop = !!vec.z ? true : false;
    }
    
    // -------- OVERRIDE -------

    /**
     * 
     * @param isLoop 
     */
    async excute(isLoop:boolean = this.isLoop){        
        await new Promise((resolve:Function, reject:Function)=>{
            this.super.setSkin(this.skin);
            if(isLoop){
                this.excute(false).then(()=>{
                    this.super.loop = true;
                    // this.super.setToSetupPose();       
                    this.super.setAnimation(0, this.animation, true);
                    resolve();
                })                 
            }else{                
                // this.super.loop = false;
                // Fixed quicker
                // this.done && this.done();
                this.done = ()=>{ 
                    this.done = null;
                    this.super.setCompleteListener(null);
                    resolve();
                };
                this.super.setCompleteListener((trackEntry:sp.spine.TrackEntry) => {                 
                    if(trackEntry.animation && trackEntry.animation.name == this.animation){
                        // this.super.clearTrack(trackEntry.trackIndex);
                        // this.super.clearAnimation();
                        if(this.done) this.done();
                    }
                });
                this.super.setAnimation(0, this.animation, false);
            }
        })
        //         
    }

    // ---------------------------------------------------------------------
    
    /**
     * 
     * @param id 
     * @returns 
     */
    protected isAdjustOnRuntime(id:number):boolean{
        const skeletonData:sp.SkeletonData = this.super.skeletonData;
        const skinEnum:any = skeletonData['getSkinsEnum']();
        return COMMAND == skinEnum[id];
    }
    
    /**
     * 
     */
    protected generateEnums(){
        if(this.super){
            const skeletonData:sp.SkeletonData = this.super.skeletonData;   
            if(skeletonData){
                // const skeletonEnum:any ={ ...skeletonData['getSkinsEnum'](), OPTION:2000};
                const skeletonSkinEnum:any =  skeletonData['getSkinsEnum']();
                const animationSkinEnum:any =  skeletonData['getAnimsEnum']();
                // log('>>>>>>>>>>>>> ' + Enum.getList(skeletonEnum).length)
                // GameUtils.updateEnum(this, 'skinIndex', skeletonData['getSkinsEnum']());
                // GameUtils.updateEnum(this, 'animIndex', skeletonData['getAnimsEnum']());
                GameUtils.updateEnum(this, 'skinIndex', Object.assign({ [COMMAND]: Enum.getList(skeletonSkinEnum).length }, skeletonData['getSkinsEnum']())  );
                GameUtils.updateEnum(this, 'animIndex', Object.assign({ [COMMAND]: Enum.getList(animationSkinEnum).length }, skeletonData['getAnimsEnum']()) );
                this.updateAnimation();
                this.updateSkin();                
            }
        }
    }

    /**
     * 
     */
    protected updateAnimation(){
        if(this.super){
            const skeletonData:sp.SkeletonData = this.super.skeletonData;
            const animEnum:any = skeletonData['getAnimsEnum']();
            this._animation = animEnum[this._animIndex];            
            if(!this.clipName.length) this.clipName = this._animation;
        }
    }
    
    /**
     * 
     */
    protected updateSkin(){
        if(this.super){
            const skeletonData:sp.SkeletonData = this.super.skeletonData;
            const skinEnum:any = skeletonData['getSkinsEnum']();
            this._skin = skinEnum[this._skinIndex];
            if(this.clipName == this._animation) this.clipName += '.' + this._skin;
        }
    }
    
    // ---------------

    /**
     * 
     */
    protected onDestroy(): void {
        super.onDestroy();  
        this.done && this.done();
        this.done = null;
    }

}


