import { _decorator, Component, Node } from 'cc';
// import { LocalizationController } from './LocalizationController';
import { Label } from 'cc';
import { MultilingualConvertor } from './MultilingualConvertor';
import { Sprite } from 'cc';
import { importRemoteCCClass, remoteCCClass } from '../../gamify/utils/RemoteScriptImporter';
import { LocalizationController } from './LocalizationController';
import { AssetManager } from 'cc';
import { CCString } from 'cc';
import { DEV } from 'cc/env';
import ParasiteComponent from '../../gamify/master/ParasiteComponent';
// import { RemoteBundle } from '../../gamify/master/RemoteBundle';
const { property, executeInEditMode } = _decorator;
const Helper:any = importRemoteCCClass('Helper')
// const LocalizationController:any = importRemoteCCClass('LocalizationController');



@remoteCCClass('MultilingualController')
@executeInEditMode(true)
export class MultilingualController extends LocalizationController {
    
    @property({
        type:[Node],
        serializable:true,
        group: 'Setup'
    })
    localizingNodes:Node[] = [];

    get languageBundle():AssetManager.Bundle{
        return super.languageBundle
    }

    protected getLanguageURL(lang: string, defaultPath: string): string {
        return super.getLanguageURL(lang, defaultPath);
    }

    
    @property({
        displayName: 'Run On Load',
        tooltip:'Note: If true, localization will run before the plugin is initialized. \n This will create annoying warnings when the default language package does not have words to translate.'
    })
    runOnLoad: boolean = true;

    /**
     * 
     */
    async onReady() {        
        // Neu ko co plugin thi chay luon        
        await this.startLocalizing();
        await super.onReady();        
    }

    async startLocalizing(isForce?:boolean){
        // if(this.runOnLoad || isForce){
        this.localizingNodes && this.localizingNodes.forEach((node:Node)=>{
            if(node){
                let localComp:MultilingualConvertor = node.getComponent(MultilingualConvertor) || node.addComponent(MultilingualConvertor);
                if(this.runOnLoad || isForce){
                    localComp.enabled = true;
                    localComp.translate();
                }else{
                    localComp.enabled = false;
                }
            }
        })
        
    }

}


