import { _decorator } from 'cc';
import ParasiteComponent, { override } from '../../../gamify/master/ParasiteComponent';
import { MultilingualController } from '../MultilingualController';
import { importRemoteCCClass, remoteCCClass } from '../../../gamify/utils/RemoteScriptImporter';
import { Enum } from 'cc';
import { AssetManager } from 'cc';
import { assetManager } from 'cc';
import { JsonAsset } from 'cc';
import { js } from 'cc';
import { error } from 'cc';
import { DEV, EDITOR } from 'cc/env';
import { warn } from 'cc';
import { log } from 'cc';
import { Component } from 'cc';
import { AsyncWaitify } from '../../../gamify/master/AsyncWaitify';
import { Asset } from 'cc';
import { CCString } from 'cc';
import { resources } from 'cc';
const { property, executeInEditMode } = _decorator;
const Helper:any = importRemoteCCClass('Helper');
const LocalizationController:any = importRemoteCCClass('LocalizationController');
// const ParasiteComponent:any = importRemoteCCClass('ParasiteComponent');
// when release the game-template. I will add more links to resource servers.
const RESOURCE_SERVER = {
    STAGE_LOADING: '',
    GAMIFY_WORKSPACE :'http://222.252.29.181:2567',
    JSDELIVR_NET:'https://cdn.jsdelivr.net/gh/hallo-gamify/gamify-resource@master',
}

const LANG_BLOCK:string = '{language}';

type BundleJSON = {
    [key:string] : string[]
}

let DefaultResourceServer = Helper.convertToEnum(RESOURCE_SERVER);
let DefaultBundleName = Enum({ERROR_LOADING_BUNDLE:0});
let DefaultBundleVersion = Enum({ERROR_LOADING_BUNDLE_VERSION:0});

@remoteCCClass('RemoteMultilingualTextPackage')
@executeInEditMode(true)
export class RemoteMultilingualTextPackage extends AsyncWaitify(Component) {
    
    static BundleList:BundleJSON;
    
    // @override
    // async startLocalizing() {
    //     await this.startLoadingLanguagePack();
    //     console.log('goi vao super startLocalizing')
    // }

    // -----------------------
    

    @property({
        type: DefaultResourceServer,
        displayName:'Host'
    })    
    public get hostIndex(): number {
        return this._hostIndex;
    }
    public set hostIndex(value: number) {
        if(this._hostIndex !== value){
            this._hostIndex = value;
            this.hostName = RESOURCE_SERVER[DefaultResourceServer[value]];
            this.__loadAndParseBundleList();
        }
    }



    @property({
        type: DefaultBundleName,
        displayName:'Bundle',
        visible() {
            return this.hostIndex !== DefaultResourceServer.STAGE_LOADING;
        }
    })    
    public get bundleNameIndex(): number {
        return this._bundleNameIndex;
    }
    public set bundleNameIndex(value: number) {      
        this.updateBundleEnum();        
        if(RemoteMultilingualTextPackage.BundleList){
            this._bundleNameIndex = value;
            this.bundleName = DefaultBundleName[this._bundleNameIndex];
        }
        this.updateBundleVersionEnum();
    }
    
    @property({
        type: DefaultBundleVersion,
        displayName:'Version',
        visible() {
            return this.hostIndex !== DefaultResourceServer.STAGE_LOADING;
        }
    })    
    public get bundleVersionIndex(): number {
        return this._bundleVersionIndex;
    }
    public set bundleVersionIndex(value: number) {                           
        this.updateBundleVersionEnum();        
        if(RemoteMultilingualTextPackage.BundleList){
            this._bundleVersionIndex = value;   
            this.bunderVersion = DefaultBundleVersion[this._bundleVersionIndex];
        }        
    }
        
    //
    // @property({
    //     type: [DefaultBundleName],
    //     displayName: 'Wait For Loading',
    //     visible() {
    //         return this.hostIndex !== DefaultResourceServer.STAGE_LOADING ;
    //     },        
    // }) 
    // waitBundleLoading:number[] = [];

    @property({serializable:true , visible:false})
    hostName:string = RESOURCE_SERVER.GAMIFY_WORKSPACE;

    @property({
        type:CCString,
        serializable:true ,
        visible(){
            return this.hostIndex == DefaultResourceServer.STAGE_LOADING
        }
        // visible:false
    })
    bundleName:string = '';

    @property({serializable:true , visible:false})
    bunderVersion:string = null;

    @property({serializable:true})
    private _hostIndex: number = 0;

    @property({serializable:true})
    private _bundleVersionIndex: number = 0;

    @property({serializable:true})
    private _bundleNameIndex: number = 0;

    // @property({
    //     // visible:false,
    //     tooltip:'Ex: '+ LANG_BLOCK + '.json - Path to language json file. In which, the '+ LANG_BLOCK + ' key word would be replaced by current language'
    // })
    // patternPath:string = LANG_BLOCK + '.json';
    
    // protected controller:MultilingualController = null;
    private controller:MultilingualController = null;
    
    /**
     * 
     */
    async onLoad() {
        if(!this.controller) this.controller = this.getComponent(MultilingualController)
        super.onLoad && super.onLoad();
        await this.startLoadingLanguagePack();        
        this.controller && await this.controller.startLocalizing(true);
    }

    /**
     * 
     */
    private async startLoadingLanguagePack(){
        const lang:string = this.controller?.currentLanguage || LocalizationController.language;
        if(this.hostIndex !== DefaultResourceServer.STAGE_LOADING){
            await this.__loadAndParseBundleList.call(this);
        }
        const bundle:AssetManager.Bundle = await this.__loadBundle();
        const languageJSONData:{[n:string]:string} = await this._loadAndParseLanguagePackage(lang, bundle);
        this.ready(languageJSONData);
    }
    
    /**
     * 
     * @returns Promise<BundleJSON>
     */
    private async __loadAndParseBundleList():Promise<BundleJSON>{
        if(this.hostIndex == DefaultResourceServer.STAGE_LOADING) return null;
        RemoteMultilingualTextPackage.BundleList = RemoteMultilingualTextPackage.BundleList || await new Promise((resolve:Function)=>assetManager.loadAny({url: this.hostName + '/remote'}, (err:Error, json:BundleJSON&JsonAsset|string)=>{
            const jsonObj:any = js.isString(json) ? JSON.parse(json as string) : json
            return !err ? resolve(jsonObj) : resolve(null);
        }))
        // Parsing bundles
        if(RemoteMultilingualTextPackage.BundleList){                
            this.updateBundleEnum();
            this.updateBundleVersionEnum();
            return RemoteMultilingualTextPackage.BundleList as BundleJSON;
        }else{
            error('[RemoteMultilingualTextPackage] Khong load duoc server !')
        }
        return null;
    }

    /**
     * 
     * @returns Promise<AssetManager.Bundle>
     */
    private async __loadBundle():Promise<AssetManager.Bundle>{
        if(!EDITOR){
            if(!this.bundleName || !this.bundleName.length) {
                warn('Do not exsit bundle name ' + this.bundleName + ' use cc.resources bundle instead')
                return resources
            }
            let bundle:AssetManager.Bundle = assetManager.getBundle(this.bundleName);
            if(!bundle){
                const bundleUrl:string = ((this.hostIndex == DefaultResourceServer.STAGE_LOADING) ? '' : this.hostName) + '/remote/' + this.bundleName;
                bundle = await new Promise<AssetManager.Bundle>((resolve:Function)=>{
                    if(this.hostIndex == DefaultResourceServer.STAGE_LOADING){
                        assetManager.loadBundle(bundleUrl, (err:Error, downloadBundle:AssetManager.Bundle)=>{                   
                            if(!err){                               
                                resolve(downloadBundle);                            
                            }else{
                                DEV && error('Bundle Loading Error ' + err + ' bundle name: ' + this.bundleName)
                                resolve(null)
                            }
                            
                        }) 
                    }else{
                        // 
                        assetManager.loadBundle(bundleUrl, {version: this.bunderVersion}, (err:Error, downloadBundle:AssetManager.Bundle)=>{                   
                            if(!err){                               
                                resolve(downloadBundle);                            
                            }else{
                                DEV && error('Bundle Loading Error ' + err + ' bundle name: ' + this.bundleName)
                                resolve(null)
                            }
                            
                        }) 
                    }
                })
            }
            return bundle;
        }else{
            // 
        }
    }

    /**
     * 
     * @param bundle 
     */
    private async _loadAndParseLanguagePackage(lang:string, bundle:AssetManager.Bundle):Promise<{[n:string]:string}>{
        const textAssetPath:string = (this.controller && this.controller.localizedPath ? this.controller.localizedPath + '/' : '') + lang; //this.parseLangUrl(lang, this.controller.localizedPath);        
        if(textAssetPath && bundle){
            const languageJSONData:{[n:string]:string} = await new Promise((resolve:Function)=>{                
                bundle.load(textAssetPath,JsonAsset, (err, assets) => {                
                    if (err) {
                        (EDITOR || DEV) && warn('Error loading common text for language ' + lang + " (This message only show on EDITOR or DEV mode)");
                        resolve(null);
                    }else{
                        if (assets && assets instanceof JsonAsset) {
                            resolve(assets.json)
                        }else{
                            resolve(JSON.parse(assets.toString()))
                        }
                    }
                });
            })
            const packageName:string = bundle.name;
            languageJSONData && LocalizationController.addJSONPackage(packageName, languageJSONData, false);
            // DEV && console.log('languageJSONData: ' + JSON.stringify(languageJSONData))
            return languageJSONData
        }
        return null
    }


    // private parseLangUrl(lang:string, patternPath:string):string{
    //     return patternPath.replace(LANG_BLOCK, lang)
    // }

    /**
     * 
     */
    private updateBundleEnum(){        
        if(RemoteMultilingualTextPackage.BundleList){
            DefaultBundleName = Helper.convertToEnum(RemoteMultilingualTextPackage.BundleList);
            EDITOR && Helper.updateEnum(this,'bundleNameIndex', DefaultBundleName);   
            if(this.bundleName && this.bundleName.length && DefaultBundleName[this.bundleName] !== undefined && DefaultBundleName[this.bundleName] !== null){
                this._bundleNameIndex = DefaultBundleName[this.bundleName];
            }else{
                this._bundleNameIndex = 0;
            }
            this.bundleName = DefaultBundleName[this._bundleNameIndex];
        }
        if(!RemoteMultilingualTextPackage.BundleList) console.log('Unable update bundle Enum !')
    }

    /**
     * 
     */
    private updateBundleVersionEnum(){
        if(RemoteMultilingualTextPackage.BundleList){
            const bundleName:string = DefaultBundleName[this.bundleNameIndex];
            const versionList:string[] = RemoteMultilingualTextPackage.BundleList[bundleName]
            DefaultBundleVersion = Helper.convertToEnum(versionList);
            EDITOR && Helper.updateEnum(this,'bundleVersionIndex', DefaultBundleVersion);            
            if(this.bunderVersion && this.bunderVersion.length && DefaultBundleVersion[this.bunderVersion] !== undefined && DefaultBundleVersion[this.bunderVersion] !== null){
                this._bundleVersionIndex = DefaultBundleVersion[this.bunderVersion];
            }else{
                this._bundleVersionIndex = 0;
            }
            this.bunderVersion = DefaultBundleVersion[this._bundleVersionIndex];
        }
        if(!RemoteMultilingualTextPackage.BundleList) console.log('Unable update version Enum !')
    }

    

}


