
import { _decorator, Component, warn, log } from 'cc';
import { DEBUG, DEV } from 'cc/env';
import { Action } from '../core/Action';
import { IMechanicDispatcher } from '../interfaces/IDispatcher';
import { RelativeMap, RenderTask } from '../types/GamifyType';
import { Helper } from '../utils/Helper';
import { GAMIFY_LOGS, GAMIFY_WARNNING } from '../utils/Setting';
import { RenderQueueDispatcher } from './RenderQueueDispatcher';

const { ccclass } = _decorator;


/**
 * iu khin vic vit kch bn tp trung cho hot ng ca game.
 * 
 * Author: Hallopatidu@gmail.com
 */ 
@ccclass('MechanicDispatcher')
export class MechanicDispatcher extends RenderQueueDispatcher implements IMechanicDispatcher {

    private _relativeScenarioMap:Map<string, Set<string[]>> = null;
    private _configActionMap:Map<string, Action> = null;

    // private _usingMechanicRelativeMap:boolean = false;

    /**
    *
    */
    constructor(id:string) {
        super(id);       
        this._relativeScenarioMap = new Map();
        this._configActionMap = new Map();
    }

    

    /**
    * ng k kch bn chuyn ng.
    * - Nu ng k lc khng xy action (isDispatching == false) s lu vo kch bn mc nh. V d: s dng trong onInit
    * - Nu ng k lc xy ra action s iu chnh ngay vo relativeMap v action hin ti.
    * @param configAction 
    * @param ids stateName hoc tokenId
    */
    public registerActionScenario(configAction:Action, ids:string[]){
        let actionType:string = configAction.type;
        if(actionType){         
            if(this.isDispatching() && this._pendingAction){
                const renderTask:RenderTask = this.getRenderTask(actionType);
                // this._relativeMap = this.applyActionScenario(ids, this._relativeMap || Object.create(null));
                renderTask.map = this.applyActionScenario(ids, renderTask.map || Object.create(null));
                // this._usingMechanicRelativeMap = true;
                
                // 
            }else{
                // Default mechanic relative map
                let scenarioSet:Set<string[]> = this._relativeScenarioMap.get(actionType);
                if(!scenarioSet){
                    scenarioSet = new Set();
                }
                scenarioSet.add(ids);
                this._relativeScenarioMap.set(actionType, scenarioSet);
            }
            // 
            
            this.registerConfigAction(configAction)
            //
        }        
    }

    /**
     * 
     * @param ids 
     * @param relativeMap 
     * @returns 
     */
    private applyActionScenario(ids:string[], relativeMap:RelativeMap):RelativeMap{
        const parsingIds:string[] = ids.slice();
        while(parsingIds.length){
            const nextLeftId:string = parsingIds.shift();
            const nextRightId:string = parsingIds[0];
            if(nextRightId){
                const leftIds:string[] = this.searchAllRegisterNames(nextLeftId);
                const rightIds:string[] = this.searchAllRegisterNames(nextRightId);
                let leftId:string;
                while(leftIds.length){
                    leftId = leftIds.shift();
                    const leftToken:string = this.findMapName(leftId);
                    relativeMap[leftToken] = relativeMap[leftToken] || [];
                }
                // for (let i:number = 0; i < leftIds.length; i++) {
                //     const leftId:string = leftIds[i];
                if(leftId){
                    for (let j:number = 0; j < rightIds.length; j++) {
                        const rightId = rightIds[j];
                        if(leftId && rightId){
                            this._applyRenderBefore([leftId, rightId], relativeMap);
                        }
                    }         
                }           
                // }
            }
        }
        return relativeMap
    }

    /**
     * 
     * @param configAction 
     * @param stateName 
     */
    public registerConfigAction(configAction:Action, stateName?:string[]){
        let actionType:string = configAction.type;
        if(actionType){         
            if(configAction.payload){
                let recordAction:Action = this._configActionMap.get(actionType) || Object.create(null);
                configAction.payload = recordAction.payload ? Object.assign(configAction.payload, recordAction.payload) : configAction.payload;
            }
            if(this.isDispatching() && this._pendingAction){
                this._pendingAction = Object.assign(this._pendingAction, configAction);
            }else{
                this._configActionMap.set(actionType, configAction);
            }
        }else{
            DEV && GAMIFY_WARNNING && warn(this.__ccClassName +'::registerConfigAction : action phi c type.')
        }
    }

    /**
     * 
     * @param action 
     */
     public removeConfigAction(action:Action){
        let actionType:string = action.type;
        if(actionType){
            this._configActionMap.delete(actionType);
        }
    }

    /**
     * 
     * @param action 
     */
    public removeDefaultActionScenerio(action:Action){
        let actionType:string = action.type;
        if(actionType){
            this._relativeScenarioMap.delete(actionType);
            this._configActionMap.delete(actionType);
        }
    }

    /**
     * 
     * @param action 
     * @returns 
     */
    public hasScenario(action?:Action):boolean {
        let targetAction:Action = action || (this._pendingAction as Action)
        if(targetAction){
            let actionType:string = targetAction.type;
            // return (this._relativeScenarioMap.get(actionType) || this._usingMechanicRelativeMap) ? true : false;
            return this._relativeScenarioMap.get(actionType) ? true : false;
        }
    }

   
    /**
     * 
     * @returns 
     */
    protected _generateMechanicRelativeMap(action:Action):RelativeMap {
        let relativeMap:RelativeMap = Object.create(null);
        let actionType:string = action.type;
        if(actionType){
            let scenarioSet:Set<string[]> = this._relativeScenarioMap.get(actionType);
            scenarioSet && scenarioSet.forEach((ids:string[])=>{
                this.applyActionScenario(ids, relativeMap);
            })
        }        
        return relativeMap;
    }


    //------------------ Override -------------------

    /**
     * 
     * @param beforeIds 
     * @param currentId 
     */
    // public renderBefore(beforeIds:string[], currentId:string){
    //     if(!this._usingMechanicRelativeMap){
    //         super.renderBefore(beforeIds, currentId);
    //     }
    // }


    /**
     * B sung: Ghi  configAction vo ton b action cng loi khi bt u dispatch.
     * @param action 
     */
    protected _startDispatching(action:Action) {
        let configAction:Action = this._configActionMap.get(action.type);
        if(configAction){
            configAction.receiver = action.receiver.length ? (configAction.receiver || []).concat(action.receiver) : configAction.receiver;
            action = Object.assign(action, configAction);
        }
        //
        super._startDispatching(action);
        //
        // let mechanicRelativeMap:RenderMap =  this._generateMechanicRelativeMap(action);
        // if(!Helper.areEqual(this._nullObject, mechanicRelativeMap)) {
        //     this._relativeMap = Object.assign(this._relativeMap, mechanicRelativeMap);
        //     this._usingMechanicRelativeMap = true;
        // }
    }

    /**
     * B sung: ghi  this.__relativeMap nu tn ti relativeMap c config trong Gamify Mechanic.
     */
    protected _stopDispatching() {
        // To relative map bng scenario.
        // const action:Action = this._pendingAction;
        let mechanicRelativeMap:RelativeMap =  this._generateMechanicRelativeMap(this._pendingAction as Action);
        if(!Helper.areEqual(this._nullObject, mechanicRelativeMap)) {
            const actionType:string = this._pendingAction ? (this._pendingAction as Action).type : null;
            const renderTask:RenderTask = this.getRenderTask(actionType);
            renderTask.map = Object.assign(renderTask.map, mechanicRelativeMap);
            // this._usingMechanicRelativeMap = true;
        }
        // 
        super._stopDispatching();
        // this._usingMechanicRelativeMap = false;
    }

    /**
     * 
     */
    destroy(){
        this._relativeScenarioMap = null;
        this._configActionMap = null;
        super.destroy();
    }
   
}

