import { _decorator, Component, Node } from 'cc';
import { InboxInfo } from '../types/PlayerInboxType';
import { Event } from 'cc';
import { Label } from 'cc';
import { Eventify } from 'cc';
import { Sprite } from 'cc';
import { Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PlayerInboxItem')
export class PlayerInboxItem extends Eventify(Component) {

    static EVENT = {
      SELECTED:'selected_item_event'
    }

    @property({type:Label})
    rewardTitle:Label = null;

    @property({type:Label})
    expireTimeTitle:Label = null;

    private data:InboxInfo;
    private _isActive: boolean = true;
    public get isActive(): boolean {
      return this._isActive;
    }
    public set isActive(value: boolean) {
      if(this._isActive !== value){
        const allSprites:Sprite[] = this.getComponentsInChildren(Sprite);
        allSprites.forEach((sp:Sprite)=>sp.grayscale = !this._isActive);
        (this.getComponentInChildren(Button) as Button).interactable = this._isActive;      
      }
      this._isActive = value;
    }

    /**
     * 
     * @param deltaTime 
     */
    update(deltaTime: number) {
        if(this.data && this.data.claimBefore && this.isActive){
          const timeRemaining:number = this.data.claimBefore.getTime() - Date.now()
          this.expireTimeTitle.string = this.convertMsToTime(timeRemaining);
          this.isActive = (!this.data.isRead && timeRemaining > 1000);
        }
    }

    /**
     * 
     * @param data 
     */
    render(data:InboxInfo){
        this.data = data        
        this.rewardTitle.string = data.count + this.rewardTitle.string;        
        

        // this.isActive = false
    }

    onClaimReward(event:Event){
        this.emit(PlayerInboxItem.EVENT.SELECTED, this.data)
    }



    // ---------------------

    private updateExpireTime(){

    }

    /**
     * 
     * @param milliseconds 
     * @returns 
     */
    private convertMsToTime(milliseconds: number) {
        if(!this.isActive)
          return `00d:00h:00m:00s`;
          
        let seconds:number = Math.floor(milliseconds / 1000);
        let minutes:number = Math.floor(seconds / 60);
        let hours:number = Math.floor(minutes / 60);
        let days:number = Math.floor(hours / 24);
    
        seconds = seconds % 60;
        minutes = minutes % 60;
    
        //  If you don't want to roll hours over, e.g. 24 to 00
        //  comment (or remove) the line below
        // commenting next line gets you `24:00:00` instead of `00:00:00`
        // or `36:15:31` instead of `12:15:31`, etc.
        hours = hours % 24;
    
        if (seconds < 0)
          return `00d:00h:00m:00s`;
    
        return `${this.padTo2Digits(days)}d:${this.padTo2Digits(hours)}h:${this.padTo2Digits(minutes)}m:${this.padTo2Digits(seconds)}s`;
      }
    
    private padTo2Digits(num: number) {
      return num.toString().padStart(2, '0');
    }

    // ---------------------
    protected onDestroy(): void {
      this.targetOff(this)
    }

}


