export type PlayerInboxResponse = {
    items:InboxInfo[]
    count?:number,
    page?:number,
    pageCount?:number,
    size?: number,
    total?: number
}

export type ClaimInfo = {
    claimId:string,
    count:number,
    betSetting:PlayerInboxBetSetting,
}

export type InboxInfo = {
    id:number,
    claimId:string,
    gameMode:string,
    module:string,
    isRead:boolean,
    // count:number,
    // betSetting:PlayerInboxBetSetting,
    claimUrl:string,
    claimBefore:Date
} & ClaimInfo



export type PlayerInboxBetSetting = {
    betSize: number, 
    betLevel: number, 
    baseBet: number, 
    betAmount: number
}

export type PlayerInboxState = {
    status:number,
    // gameMode?:number,
    claimed:{
        url:string
        data: ClaimInfo
    },
    // rewardCount?:number
}

export enum PlayerInboxStatus {
    INIT,
    READY,
    UPDATE,    
    SHOW_CONTINUE_VIEW,
    SHOW_ICON_VIEW,
    SHOW_INBOX_VIEW,    
    HIDE_VIEW,
    CLAIM,
    AUTO_SPIN
}

export const PlayerInboxAction = {
    UPDATE_INBOX:'player_inbox_update_inbox_action',
    UPDATE_SETTING:'player_inbox_update_setting_action',
    SHOW:'player_inbox_show_action',
    HIDE:'player_inbox_hide_action',
    CLAIM:'player_inbox_claim_action',
}