
import { _decorator, js, error, log, warn } from 'cc';
import { DEBUG, DEV, EDITOR } from 'cc/env';
import { Action } from './Action';

const { ccclass } = _decorator;

/**
 * 
 * Author: Hallopatidu@gmail.com
 */
@ccclass('Dispatcher')
export class Dispatcher {

    static _instance: Dispatcher;
    
    static get instance():Dispatcher{
        if(!this._instance){
            this._instance = new Dispatcher('global');
        }
        return this._instance
    }

    protected __ccClassName:string = "";
    protected _prefix:string = "gamify_";
    protected _isHandled:Object = Object.create(null);
    protected _isPending:Object = Object.create(null);
    protected _pendingAction: object;
    protected _callbacks:Object = Object.create(null);
    protected _dataTaskPromise:Promise<any>;

    private _id:string = '';    
    
    private _isDispatching:boolean;    
    
    private _lastID:number = 1;    
    private _count:number = 0;              // run on Debug mode


    public get id():string{
        return this._id;
    }

    /**
     *
     */
    constructor(id:string) {
        // super();
        this._id = id ? id : this._id;
        this.__ccClassName = js.getClassName(this);
    }

    destroy(){
        this._callbacks = null;
        this._isHandled = null;
        this._isPending = null;
    }

    /**
     * Registers a callback to be invoked with every dispatched action. Returns
     * a token that can be used with `waitFor()`.
     */
    public register(callback:Function):string {
        let id = this._prefix + this._lastID++;
        // const originMethod:Function = callback;
        this._callbacks[id] = callback;
        // this._callbacks[id] = function():Promise<any>{
        //     const returnValue:any = originMethod.apply(this, Array.from(arguments));
        //     return (typeof returnValue === 'object' && typeof returnValue.then === 'function') ? returnValue : Promise.resolve(returnValue);
        // };
        (DEBUG||DEV||EDITOR) && this._count ++;
        return id;
    }

    /**
     * 
     * @param {*} id 
     */
    public hasRegister(id:string){
        return this._callbacks[id] ? true : false;
    }


    /**
    * Removes a callback based on its token.
    * @param id 
    */
    public unregister(id:string) {
       !this._callbacks[id] ? (DEBUG||DEV||EDITOR) ? error( this.__ccClassName + ".unregister(" + id + "): B ng k tht bi. Khng tm c callback no tng ng `%s`.", id) : error(false) : undefined;
       delete this._callbacks[id];
       if(DEBUG||DEV||EDITOR){
            if(this._count < 0) throw new Error(" xa ht store trc  !");
            this._count --;
            // DEBUG && GAMIFY_LOGS && log("-------- Unregister token : " + id + " - con lai " + this._count + " store.");
            if(this._count <= 0) {this.destroy();}           
       }else{  
            if(!Object.keys(this._callbacks).length){
                this.destroy();
            }
        }
    }

    /**
     * Waits for the callbacks specified to be invoked before continuing execution
     * of the current callback. This method should only be used by a callback in
     * response to a dispatched action.
     */
    public waitFor(ids:Array<string>) {        
        !this._isDispatching ? (DEBUG||DEV||EDITOR) ? error( this.__ccClassName + ".waitFor(...): Hm ch s dng trong lc dispatch. V d: trong hm callback tr v action") : error(false) : undefined;
        for (let ii:number = 0; ii < ids.length; ii++) {
            let id = ids[ii];
            if (this._isPending[id]) {                      
                // if(!this._isHandled) 
                this.__detectCircleLoop(id);
                continue;
            }
            !this._callbacks[id] ? (DEBUG||DEV||EDITOR) ? error( this.__ccClassName + ".waitFor(...): Khng tm c callback no tng ng `" + id + "`.") : error(false) : undefined;
            this._invokeCallback(id);
        }               
    }

    /**
     * Dispatches a action to all registered callbacks.
     */
    public dispatch(action:Action) {
        !!this._isDispatching ? (DEBUG||DEV||EDITOR) ? error( this.__ccClassName + ".dispatch(...): Khng th xen gia khi s kin dispatch trc  cha kt thc.") : error(false) : undefined;        
        this._startDispatching(action);
        try {
            for (let id in this._callbacks) {
                if (this._isPending[id]) {
                    continue;
                }
                this._invokeCallback(id);
            }
        } finally {
            this._stopDispatching();
        }
    }


    /**
     * Is this Dispatcher currently dispatching.
     */
    public isDispatching():boolean {
        return this._isDispatching;
    }

    /**
     * Call the callback stored with the given id. Also do some internal
     * bookkeeping.
     *
     * @internal
     */
    protected _invokeCallback(id:string) {
        this._isPending[id] = true;        
        this._excuteCallback(id);
        this._isHandled[id] = true;
    }
    
    /**
     * 
     * @param id 
     */
    protected _excuteCallback(id:string){
        try{
            if(!this._callbacks[id]) {
                throw new Error('GamifyStore cha c ng k : ' + id)
            }
            this._callbacks[id](this._pendingAction);
        }catch(err){
            error('Error when execute callback at ' + id + ' -action ' + JSON.stringify(this._pendingAction))
        }
    }


    /**
     * Set up bookkeeping needed when dispatching.
     *
     * @internal
     */
    protected _startDispatching(action:Action) {
        for (let id in this._callbacks) {
            this._isPending[id] = false;
            this._isHandled[id] = false;
        }
        this._pendingAction = action;        
        this._isDispatching = true;
    }

    /**
     * Clear bookkeeping used for dispatching.
     * 
     * @internal
     */
    protected _stopDispatching() {
        delete this._pendingAction;
        this._isDispatching = false;
    }

    /**
     * 
     * @param id 
     */
    protected __detectCircleLoop(id:string){
        !this._isHandled[id] ? (DEBUG||DEV||EDITOR) ? error( this.__ccClassName + ".waitFor(...): Pht hin li lp vng trn (A i B, B i A).") : error(false) : undefined;
    }

}

