import { _decorator, Component, Node } from 'cc';
import { ChainClip } from './ChainClip';
import { Enum } from 'cc';
import { js } from 'cc';
import { override } from '../../gamify/master/ParasiteComponent';
import { error } from 'cc';
import { log } from 'cc';
const { ccclass, property, executeInEditMode } = _decorator;

enum CLIP_MODE {
    SEQUENCING,
    IN_ORDER
}
Enum(CLIP_MODE)

@ccclass('TimelineClip')
export class TimelineClip<SuperComponent> extends ChainClip<SuperComponent> {
    @property({
        type:CLIP_MODE,
        visible(){
            return this.controller == this;
        }
    })
    get mode():CLIP_MODE{
        return this._mode
    }

    set mode(value:CLIP_MODE){
        this._mode = value;
        this.setMode(value)
    }

    @property({
        serializable:true,
        visible:false
    })
    protected _mode:CLIP_MODE = CLIP_MODE.SEQUENCING;

    @property({
        displayName: 'Id'
    })
    get frameId():number{
        return this._frameId
    }



    protected _frameId:number = 0;

    protected _currentFrameId:number = 0
    get currentFrame():number{
        return this._currentFrameId
    }

    // protected _time:number = 0;
    protected _currentClipName:string
    get currentClipName():string{
        return this.controller['_currentClipName'];
    }

    protected _isLoop:boolean = false;
    protected get isLoop():boolean{
        return this._isLoop;
    }

    protected set isLoop(value:boolean){
        this._isLoop = value;
        if(this.sub !== undefined && this.sub !== null) {
            this.sub['isLoop'] = value;
        }
    }
    
    protected _isPause:boolean = false;
    protected get isPause():boolean{
        return this._isPause;
    }

    protected set isPause(value:boolean){
        this._isPause = value;
        if(this.sub !== undefined && this.sub !== null) {
            this.sub['isPause'] = value;
        }
    }

    protected _isPlaying:boolean = false;
    get isPlaying():boolean{
        return (this.controller as TimelineClip<SuperComponent>)._isPlaying;
    }

    protected set isPlaying(value:boolean){
        this._isPlaying = value;
        if(this.sub !== undefined && this.sub !== null) {
            this.sub['isPlaying'] = value;
        }
    }
    
    protected _playList:string[] = null;
    get playList():string[]{
        return this._playList;
    }

    protected _currentToken:string;

    protected done:Function = null;

    // --------------------------------------------------------------------

    @override
    protected _updateRealtime(dt:number){
        if(!this.isPause){
            this.super['_updateRealtime'](dt);
            // const skeleton = this.super['_skeleton'];
            // if(skeleton){
            //     // if(this._time > 2){
            //     //     skeleton.time = 0
            //     // }
            //     this._time = skeleton.time;
            //     // log('time: ' + this._time);
            // }
        }
    }


    // ---------------------------------------------------------------------

    /**
     * 
     * @param clip 
     */
    registerClip(clip:TimelineClip<SuperComponent>){
        if(clip == this){
            const clipNames:string[] = this.controller['_clipNames'];
            this._frameId = clipNames.length;
        }
        super.registerClip(clip);
    }

    /**
     * 
     */
    async play(clipOrder?:string|number|Array<string|number>, loop:boolean = false){
        const controller:TimelineClip<SuperComponent> = this.controller as TimelineClip<SuperComponent>;
        if(controller.isPlaying){
            if(controller.isPause){
                controller.resume()
                return;
            }else{
                controller.stop();
            }
        }
        //          
        if(!controller.isPlaying){
            if(this.makePlaylist(clipOrder)){
                // this.stop();
                controller.isLoop = loop;
                controller.isPlaying = true;
                controller.isPause = false;
                // log('\n [Start] ' + this.playList);
                await controller.progress();
                controller.setPlaylist(null);
            }else{
                error('Can\'t find out clips with order ' + clipOrder)
            }
        }
    }

    /**
     * 
     */
    stop(){        
        // (this.super as sp.Skeleton).setToSetupPose();
        this._isPlaying = false;
        if(this.done) this.done();
        if(this.sub !== undefined && this.sub !== null && this.sub['stop']) {
            this.sub['stop']();
        }
        
    }

    resume(){
        this.isPause = false; 
    }

    /**
     * 
     */
    pause(){
        this.isPause = this.isPlaying && true;
    }
    
    /**
     * 
     * @param clipName 
     */
    async playFrom(clipName:string|number){
        if(this.mode == CLIP_MODE.SEQUENCING){
            if(!clipName) error("Don't know where to start ! clipName is undefined")
            if(!this.isPlaying){
                if(this.makePlaylist([clipName, undefined])){
                    this.controller['isPlaying'] = true;      
                    this.controller['isPause'] = false;      
                    await this.progress();
                }
            }else{
                this.resume()
            }
        }else{
            error('This funtion is not active in ' + this.mode + ' mode');
        }
    }

    /**
     * 
     * @param clipName 
     */
    async playTo(clipName:string|number){        
        if(this.mode == CLIP_MODE.SEQUENCING){
            if(!clipName) error("Don't know where to stop ! clipName is undefined")
            if(!this.isPlaying){
                if(this.makePlaylist([this.controller.clipName, clipName])){
                    this.controller['isPlaying'] = true;
                    this.controller['isPause'] = false;
                    await this.progress();
                }
            }else{
                this.resume();
            }
        }else{
            error('This funtion is not active in ' + this.mode + ' mode');
        }
    }
    
    // ----------------------------- OVERRIDE ChainClip ----------------------------------------

    /**
     * 
     */
    async progress(clip:ChainClip<SuperComponent> = this.controller){        
        if(clip && (clip as TimelineClip<SuperComponent>).isPlaying){            
            await super.progress(clip);
        }
    }


    /**
     * 
     */
    async completed(){        
        // this.super.setToSetupPose();        
        this.controller['isPlaying'] = false;
        this.controller['isLoop'] = false;//this.isLoop
        // if(this.done) this.done();
        // log('[END] _playList:: ' + this._playList + '\n')      
    }

    /**
     * 
     * @returns 
     */
    protected isExecutable():boolean {        
        const canPlay:boolean = this.isPlaying && this.isClipInPlayList()//(clip.playList.indexOf(clip.clipName) !== -1) && (clip.mode == CLIP_MODE.IN_ORDER ? clip.playList[0] == clip.clipName : true);
        if(canPlay){
            const controller:TimelineClip<SuperComponent> = this.controller as TimelineClip<SuperComponent>;
            controller._currentClipName = this.clipName;
            controller._currentFrameId = this.frameId;
            const lastId:number = controller.playList.length - 1;
            if(this.clipName == controller.playList[lastId]){
                this._isLoop = controller.isLoop;
            }else{
                this._isLoop = false
            }
        }
        return canPlay;
    }
    
    
    /**
     * 
     * @Override
     */
    protected nextClip():TimelineClip<SuperComponent>{  
        let nextClip:TimelineClip<SuperComponent> = null;
        switch(this._mode)  {
            case CLIP_MODE.IN_ORDER:
                if(this.playList && this.playList.length > 1 && this.clipName == this.playList[0]){
                    const nextClipName:string = this.playList[1];
                    if(nextClipName){
                        nextClip = this.getClipByName(nextClipName);
                        this.reducePlaylistInOrderMode();
                    }                    
                }                
                break;                
        }
        return nextClip || (this.sub as TimelineClip<SuperComponent>);        
    }

    // ---------------------------------------------------------------------

    /**
     * 
     * @returns 
     */
    protected isClipInPlayList():boolean {
        return this.playList && this.playList.length && (this.playList.indexOf(this.clipName) !== -1) && (this.mode == CLIP_MODE.IN_ORDER ? this._playList[0] == this.clipName : true)
    }

    /**
     * 
     * @param mode 
     */
    protected setMode(mode:CLIP_MODE){
        this._mode = mode;
        if(this.sub && this.sub['setMode']){
            this.sub['setMode'](mode);
        }
    }

    /**
     * 
     * @param playList 
     */
    protected setPlaylist(playList:string[], isOnlyThis:boolean = false){
        this._playList = playList;
        if(!isOnlyThis && this.sub !== undefined && this.sub !== null && this.sub['setPlaylist']) {
            this.sub['setPlaylist'](playList);
        }
    }

    /**
     * 
     */
    protected reducePlaylistInOrderMode(){        
        if(this.mode == CLIP_MODE.IN_ORDER && this.isClipInPlayList()){
            this.playList.shift();
            (this.controller as TimelineClip<SuperComponent>).setPlaylist(this.playList);
        }
    }

    /**
     * 
     * @param clipName 
     * @returns 
     */
    protected getClipByName(clipName:string):TimelineClip<SuperComponent>{
        return this.node.components.find((comp:Component)=>{
            return js.isChildClassOf(comp.constructor, TimelineClip) && ((comp as ChainClip).clipName == clipName);
        }) as TimelineClip<SuperComponent>;        
    }

    /**
     * Make a playlist for clips playing in a sequencing
     * @param clipOrder 
     * clipOrder param can be a string, a number or a Array of string or Array of number
     * When it's a number or a string:
     *  clipOrder param is clipName or frame id of clip.
     * When it's a Array :
     *  if clipOrder param has length greater than 1, clip just play with these element of Array which is a frame id or a clip name.
     *      In the case, clip mode is in the order mode, the playing sequencing is made from clipOrder array, Clip would be played with from first element to end element.
     *  if length of clipOrder array equal to 1, clip will be played from first element to end of sequencing (setted on node).
     * @returns 
     */
    protected makePlaylist(clipOrder:string|number|Array<string|number>):boolean{
        if(clipOrder || js.isNumber(clipOrder)){
            if(Array.isArray(clipOrder) && clipOrder.length){
                let clipOrderNames:string[] = this.verifyPlaylist(clipOrder);
                if(!clipOrderNames) return false;
                if(clipOrderNames.length > 1){                    
                    if(!clipOrderNames.length) return false;
                    // this._currentToken = this.getToken();
                    if(this.mode == CLIP_MODE.SEQUENCING){
                        let isExecutable:boolean = false;
                        const allClipNames:string[] = this.controller['_clipNames'];
                        clipOrderNames = allClipNames.filter((clipName:string)=>{
                            let excutabled:boolean;
                            if(clipName == clipOrderNames[0]){
                                isExecutable = !isExecutable;
                                excutabled = true;
                                clipOrderNames.shift();
                            }else{
                                excutabled = isExecutable;
                            }
                            return excutabled;
                        })
                    }else{

                    }
                    this.controller['setPlaylist'](clipOrderNames);
                    return true;

                }else if(clipOrderNames.length == 1){                    
                    // if(!clipOrderNames.length) return false;
                    const clipOrderName:string = clipOrderNames[0];
                    const allClipNames:string[] = this.controller['_clipNames'];
                    let isExecutable:boolean = false
                    clipOrderNames = allClipNames.filter((clipName:string)=>{
                        if(clipName == clipOrderName){
                            isExecutable = true;
                        }
                        return isExecutable;
                    })
                    // this._currentToken = this.getToken();
                    this.controller['setPlaylist'](clipOrderNames);
                    return true;

                }else{
                    return false;
                }                
            }else{
                const clipOrderNames:string[] = this.verifyPlaylist([clipOrder as string|number])
                if(!clipOrderNames.length) return false;
                // this._currentToken = this.getToken();
                this.controller['setPlaylist'](clipOrderNames);                
                return true;
            }
        }else{
            // Play through from start clip to end clip            
            // this._currentToken = this.getToken();
            this.controller['setPlaylist'](Array.from(this.controller['_clipNames']))
            return true
        }
        
    }

    /**
     * 
     * @param clipOrders 
     * @returns 
     */
    protected verifyPlaylist(clipOrders:Array<string|number>):string[]{
        const allClipNames:string[] = this.controller['_clipNames'];
        const playList:string[] = [];
        clipOrders.forEach((value:string|number, indexOrder:number)=>{
            if(value || js.isNumber(value)){
                const clipName:string = allClipNames.find((clipName:string, index:number)=>(clipName == value || index == value))
                if(clipName){
                    playList[indexOrder] = clipName;
                }
            }
        })
        return playList;
    }

    getIdByName(clipName:string):number{
        const allClipNames:string[] = this.controller['_clipNames'];
        return allClipNames.findIndex((clipName:string, index:number)=>clipName == clipName)
    }
    // ---------------

    /**
     * 
     */
    protected onDestroy(): void {
        super.onDestroy();  
        this.done && this.done();
        this.done = null;
    }

}


