import { _decorator, Component, Node } from 'cc';
import { LangEnum, LanguageConvertorPackage } from './plugins/LanguageConvertorPackage';
import { log } from 'cc';
import { Enum } from 'cc';
import { Label } from 'cc';
import { Sprite } from 'cc';
import { EDITOR } from 'cc/env';
import { warn } from 'cc';
import ParasiteComponent, { override } from '../../gamify/master/ParasiteComponent';
import { SpriteFrame } from 'cc';
import { importRemoteCCClass, remoteCCClass } from '../../gamify/utils/RemoteScriptImporter';
const { ccclass, property , executeInEditMode} = _decorator;

const GameUtils:any = importRemoteCCClass('GameUtils');
const LocalizationController:any = importRemoteCCClass('LocalizationController');

export enum MultilingualKeyMode {
    STATIC_KEY,
    NODE_NAME_IS_KEY,
    LABEL_STRING_IS_KEY,
    SPRITE_FRAME_IS_KEY    
};

/**
 * This is a parasite component which use LanguageConvertorPackage to control the rendering of multilingual Label or Sprite
 */
@remoteCCClass('MultilingualConvertor (Label or Sprite Plugin)')
@executeInEditMode(true)
export class MultilingualConvertor extends ParasiteComponent<Label|Sprite> {

    @property({
        type:Enum(MultilingualKeyMode),
        // visible():boolean{
        //     return this.autoKey
        // },
        displayName:'Translation Mode',
    })
    get keyMode():number{
        return this._keyMode
    }

    set keyMode(value:number){
        this._keyMode = value;
        this.autoUpdateKey();
    }
    
    @property({
        displayName:'Key',
        visible():boolean{
            return this.keyMode == MultilingualKeyMode.STATIC_KEY;
        }
    })
    get key():string{
        return this._key;
    }

    set key(value:string){
        this._key = value;   
        this.syncKeyToPackages();     
    }
    
    // -----------------------
    @property({serializable:true, visible:false})
    private _translateInEditor: boolean = false;

    @property({serializable:true, visible:false})
    private _isAutoKey: boolean = false;

    @property({serializable:true, visible:false})
    private _key: string = '';

    @property({serializable:true, visible:false})
    private _keyMode:number = MultilingualKeyMode.NODE_NAME_IS_KEY;
        
    private _langPackage:LanguageConvertorPackage;

    @override
    set spriteFrame(value:SpriteFrame){
        const lastSpriteFrame:SpriteFrame = (this.super as Sprite).spriteFrame;
        (this.super as Sprite).spriteFrame = value;
        if(value !== lastSpriteFrame){            
            this.autoUpdateKey();
            LocalizationController.isReady && this.node.active && this.translate();
        }
    }

    @override
    set string(value:string){
        if (value === null || value === undefined) {
            value = '';
        } else {
            value = value.toString();
        }

        // if ((this.super as Label).string === value) {
        //     return;
        // }
        (this.super as Label).string = value;
        if(this._langPackage){
            this._langPackage.string = value;
        }else{
            this._pendingString = value;
        }   
    }
    
    get string():string{
        if(this._langPackage && this._langPackage.string.length){
            return this._langPackage.string
        }
        return (this.super as Label).string
    }

    private _pendingString:string = null

    /**
     * 
     */
    onLoad(){
        super.onLoad && super.onLoad();
        if(EDITOR){
            let enumObj:any = {STATIC_KEY:0, NODE_NAME_IS_KEY:1}; // Object.assign({}, MultilingualKeyMode)
            let comp:Label|Sprite;
            if(comp ??= this.node.getComponent(Label)){
                enumObj[MultilingualKeyMode[MultilingualKeyMode.LABEL_STRING_IS_KEY]] = MultilingualKeyMode.LABEL_STRING_IS_KEY;                
            }else if(comp ??= this.node.getComponent(Sprite)){
                enumObj[MultilingualKeyMode[MultilingualKeyMode.SPRITE_FRAME_IS_KEY]] = MultilingualKeyMode.SPRITE_FRAME_IS_KEY;                
            }
            GameUtils.updateEnum(this, 'keyMode', Enum(enumObj));            
        }
        // Mc nh lun c mt gi language default
        let defaultLang:LanguageConvertorPackage = this.node.getComponent(LanguageConvertorPackage);
        if(!defaultLang){            
            defaultLang = this.node.addComponent(LanguageConvertorPackage);
            defaultLang.setKey(this.key);
            defaultLang.language = LangEnum.default;
        }        
        // 
    }

    start() {        
        const styles:LanguageConvertorPackage[] = this.node.getComponents(LanguageConvertorPackage);
        styles.forEach((style:LanguageConvertorPackage)=>{
            if(style) {
                style.setKey(this.key);
            }            
        })
        if(this._pendingString){
            if(this._langPackage){
                this._langPackage.string = this._pendingString;
            }
            this._pendingString = null;
        }
    }
    
    /**
     * 
     */
    protected onEnable(): void {
        this.autoUpdateKey();
        LocalizationController.isReady && this.node.active && this.translate();
    }

    // protected onDisable(): void {
    //     const styles:LanguageConvertorPackage[] = this.node.getComponents(LanguageConvertorPackage);
    //     styles.forEach((style:LanguageConvertorPackage)=>{
    //         if(style){
    //             style.clear();
    //         }
    //     })
    // }

    
    /**
     * Start localizing by MultilingualController.
     * @returns success or not
     */
    translate(){
        if(!this._langPackage){
            const styles:LanguageConvertorPackage[] = this.node.getComponents(LanguageConvertorPackage);
            let defaultStyle:LanguageConvertorPackage;
            const selectedStyle:LanguageConvertorPackage = styles.find((style:LanguageConvertorPackage)=>{
                if(style.language == LangEnum.default){
                    defaultStyle = style;
                }
                style.clear();
                return style.languageInUse;
            })
            //     
            this._langPackage = selectedStyle ? selectedStyle : defaultStyle;
            this._langPackage.setKey(this.key);
        }
        this._langPackage.translate(true);
    }

    // -------------

    /**
     * 
     */
    protected autoUpdateKey(){
        // if(this.autoKey){
        switch(this.keyMode){
            case MultilingualKeyMode.NODE_NAME_IS_KEY:
                this._key = this.node ? this.node.name : this._key;
                break;

            case MultilingualKeyMode.LABEL_STRING_IS_KEY:
                let label:Label;
                if(label ??= this.node.getComponent(Label)){
                    this._key =  label.string && label.string.length ? label.string : this._key;
                }
                break;
                
            case MultilingualKeyMode.SPRITE_FRAME_IS_KEY:
                let sprite:Sprite;
                if(sprite ??= this.node.getComponent(Sprite)){
                    this._key =  sprite.spriteFrame ? sprite.spriteFrame.name : this._key;
                }
                break;
        }
        // 
        this.syncKeyToPackages();
        // }
    }

    protected syncKeyToPackages(){
        const styles:LanguageConvertorPackage[] = this.node.getComponents(LanguageConvertorPackage);
        styles.forEach((style:LanguageConvertorPackage)=>{
            if(style) {
                style.setKey(this.key);
            }            
        })
    }

    /**
     * 
     */
    // protected onDestroy(): void {
    //     if(this.target instanceof Sprite){
    //         this.node.off(Sprite.EventType.SPRITE_FRAME_CHANGED, this.onEnable.bind(this),this);
    //     }
    // }
}


